import { t } from '../../i18n'
import { Logger } from '../../tools/logger'
import './subtitleBar.css'

/**
 * SubtitleBar -  UI 
 * 
 */
export class SubtitleBar {
  private static instance: SubtitleBar | null = null

  private logger = new Logger('[SubtitleBar]')
  private element: HTMLElement | null = null
  private translatedDiv: HTMLElement | null = null
  private originalDiv: HTMLElement | null = null

  private static readonly ELEMENT_ID = 'moki-youtube-subtitle'
  private static readonly TRANSLATED_ID = 'moki-translated'
  private static readonly ORIGINAL_ID = 'moki-original'

  private constructor() {
    this.initDOM()
  }

  /**
   * 
   */
  static getInstance(): SubtitleBar {
    if (!SubtitleBar.instance) {
      SubtitleBar.instance = new SubtitleBar()
    }
    return SubtitleBar.instance
  }

  /**
   *  DOM 
   */
  private initDOM(): void {
    this.element = document.createElement('div')
    this.element.id = SubtitleBar.ELEMENT_ID
    this.element.className = 'moki-subtitle-bar'
    this.element.innerHTML = `
      <div id="${SubtitleBar.TRANSLATED_ID}" class="moki-subtitle-line"></div>
      <div id="${SubtitleBar.ORIGINAL_ID}" class="moki-subtitle-line"></div>
    `
    ;(document.head || document.documentElement).appendChild(this.element)

    this.translatedDiv = document.getElementById(SubtitleBar.TRANSLATED_ID)
    this.originalDiv = document.getElementById(SubtitleBar.ORIGINAL_ID)
    this.logger.debug('SubtitleBar DOM initialized')
  }

  /**
   * 
   */
  activate(): void {
    this.clearContent()
    this.show()
    this.logger.debug('SubtitleBar activated')
  }

  /**
   * 
   */
  getElement(): HTMLElement | null {
    if (!this.element) {
      this.element = document.getElementById(SubtitleBar.ELEMENT_ID)
    }
    return this.element
  }

  /**
   * 
   * @param original - 
   * @param translated - 
   * @param isAuthenticated - 
   */
  updateContent(original: string, translated: string | null, isAuthenticated: boolean): void {
    if (!this.translatedDiv || !this.originalDiv) {
      this.translatedDiv = document.getElementById(SubtitleBar.TRANSLATED_ID)
      this.originalDiv = document.getElementById(SubtitleBar.ORIGINAL_ID)
    }

    if (!this.translatedDiv || !this.originalDiv) {
      return
    }

    if (isAuthenticated) {
      // + 
      this.translatedDiv.innerHTML = translated || t('contentScript_translating')
      this.translatedDiv.style.display = ''
      this.originalDiv.innerHTML = original || t('contentScript_noNativeContent')
    } else {
      // 
      this.translatedDiv.innerHTML = ''
      this.translatedDiv.style.display = 'none'
      this.originalDiv.innerHTML = original || t('contentScript_noNativeContent')
    }
  }

  /**
   * 
   */
  clearContent(): void {
    if (this.translatedDiv) {
      this.translatedDiv.innerHTML = ''
    }
    if (this.originalDiv) {
      this.originalDiv.innerHTML = ''
    }
  }

  /**
   * 
   */
  hasContent(): boolean {
    const translated = this.translatedDiv?.innerHTML || ''
    const original = this.originalDiv?.innerHTML || ''
    return translated !== '' || original !== ''
  }

  /**
   * 
   * @param visible - 
   */
  setVisible(visible: boolean): void {
    const element = this.getElement()
    if (element) {
      element.style.opacity = visible ? '1' : '0'
    }
  }

  /**
   * 
   */
  show(): void {
    const element = this.getElement()
    if (element) {
      element.style.display = '' //  CSS  (flex)
      this.logger.debug('SubtitleBar shown')
    }
  }

  /**
   * 
   */
  hide(): void {
    const element = this.getElement()
    if (element) {
      element.style.display = 'none'
      this.logger.debug('SubtitleBar hidden')
    }
  }

  /**
   * 
   */
  isShown(): boolean {
    const element = this.getElement()
    return element ? element.style.display !== 'none' : false
  }

  /**
   * 
   * @param player - YouTube 
   */
  adjustPosition(player: Element | null): void {
    const subtitleBar = this.getElement()
    if (!subtitleBar) {
      return
    }

    if (player) {
      // 
      if (subtitleBar.parentElement !== player) {
        player.appendChild(subtitleBar)
      }

      // 
      subtitleBar.style.position = 'absolute'
      subtitleBar.style.left = '50%'
      subtitleBar.style.transform = 'translateX(-50%)'

      // 
      const playerHeight =
        (player as HTMLElement).clientHeight || player.getBoundingClientRect().height
      const subBaseBottom = Math.max(Math.round(playerHeight * 0.06), 20)
      const controller = player.querySelector('.ytp-chrome-bottom') as HTMLElement
      const controllerShow = !player.classList.contains('ytp-autohide')
      const controllerHeight = controller?.offsetHeight || 0

      // 12px
      const subBottom =
        controllerShow && controllerHeight > 0
          ? Math.max(subBaseBottom, controllerHeight + 12)
          : Math.max(12, subBaseBottom - 40)

      if (subtitleBar.style.bottom !== subBottom + 'px') {
        subtitleBar.style.bottom = subBottom + 'px'
      }
    } else {
      // 
      subtitleBar.style.position = 'fixed'
      subtitleBar.style.left = '50%'
      subtitleBar.style.transform = 'translateX(-50%)'
      subtitleBar.style.bottom = '12%'
    }
  }

  /**
   * 
   */
  destroy(): void {
    if (this.element) {
      this.element.remove()
      this.element = null
      this.translatedDiv = null
      this.originalDiv = null
      this.logger.debug('SubtitleBar destroyed')
    }
    SubtitleBar.instance = null
  }
}
