import { Logger } from '../../tools/logger'
import { CollectionBridge } from '../../tools/collectionBridge'
import { Subtitle, SubtitleSegment } from '../subtitle'
import { TooltipController } from '../tooltip/controller'
import { SidebarView } from './view'
import {
  getYouTubePlayerMode,
  getFullBleedContainer,
  getWatchFlexy,
  type YouTubePlayerMode,
} from '../youtube'

/**
 * SidebarController - 
 *  View  TooltipController, 
 */
export class SidebarController {
  private static instance: SidebarController | null = null

  private logger = new Logger('[SidebarController]')
  private view: SidebarView
  private tooltipController: TooltipController
  private segments: SubtitleSegment[] = []
  private subtitle: Subtitle | null = null
  private nativeLng: string
  private currentPlayingIndex: number = -1
  private hoverTimer: ReturnType<typeof setTimeout> | null = null
  private pendingHoverWord: string | null = null
  private eventListenersSetup: boolean = false
  private currentMode: YouTubePlayerMode = 'default'
  private modeObserver: MutationObserver | null = null

  private constructor(nativeLng: string) {
    this.nativeLng = nativeLng
    this.view = SidebarView.getInstance()
    this.tooltipController = new TooltipController(nativeLng)
    //  TooltipController
    this.tooltipController.setOverlayCallbacks(
      () => this.view.showOverlay(),
      () => this.view.hideOverlay(),
    )
    //  TooltipController
    this.tooltipController.setRefreshHighlightsCallback(() => this.refreshCollectedHighlights())
  }

  /**
   * 
   * @param nativeLng - 
   *  updateNativeLanguage() 
   */
  static getInstance(nativeLng: string = 'zh-CN'): SidebarController {
    if (!SidebarController.instance) {
      SidebarController.instance = new SidebarController(nativeLng)
    }
    return SidebarController.instance
  }

  /**
   * 
   *  DOM 
   * 
   * / full-bleed-container 
   */
  async init(subtitle: Subtitle | null): Promise<void> {
    // 
    this.currentMode = getYouTubePlayerMode()
    this.logger.debug(`Init with player mode: ${this.currentMode}`)

    const insertionPoint = await this.waitForInsertionPoint(5000)
    if (!insertionPoint) {
      this.logger.warn('Insertion point not found, sidebar init aborted')
      return
    }

    this.view.activate(insertionPoint.container, insertionPoint.referenceNode, this.currentMode)
    this.tooltipController.create(subtitle)

    if (subtitle) {
      this.subtitle = subtitle
      this.segments = subtitle.getSegments()
      //  Background  lemmas
      const videoViewId = subtitle.getVideoViewId()
      const lemmas = videoViewId
        ? await CollectionBridge.getCollectedLemmas(videoViewId)
        : new Set<string>()
      this.view.setCollectedLemmas(lemmas)
      this.view.renderSubtitles(this.segments)
    } else {
      this.view.renderSubtitles([])
    }

    this.setupEventListeners()
    this.setupModeObserver()
  }

  /**
   * 
   *  DOM 
   */
  destroy(): void {
    //  hover timer
    if (this.hoverTimer) {
      clearTimeout(this.hoverTimer)
      this.hoverTimer = null
    }
    this.pendingHoverWord = null
    this.eventListenersSetup = false
    // 
    if (this.modeObserver) {
      this.modeObserver.disconnect()
      this.modeObserver = null
    }
    this.view.destroy()
    this.tooltipController.destroy()
    SidebarController.instance = null
  }

  /**
   * 
   * 
   */
  async updateSubtitles(subtitle: Subtitle): Promise<void> {
    this.subtitle = subtitle
    this.segments = subtitle.getSegments()
    this.tooltipController.setSubtitle(subtitle)
    this.tooltipController.setNativeLng(this.nativeLng)
    //  Background  lemmas
    const videoViewId = subtitle.getVideoViewId()
    const lemmas = videoViewId
      ? await CollectionBridge.getCollectedLemmas(videoViewId)
      : new Set<string>()
    this.view.setCollectedLemmas(lemmas)
    this.view.renderSubtitles(this.segments)
    // 
    this.restorePlayingIndicator()
  }

  /**
   * 
   * /
   */
  async refreshCollectedHighlights(): Promise<void> {
    if (this.subtitle) {
      const videoViewId = this.subtitle.getVideoViewId()
      const lemmas = videoViewId
        ? await CollectionBridge.getCollectedLemmas(videoViewId)
        : new Set<string>()
      this.logger.debug(
        `[refreshCollectedHighlights] lemmas: ${JSON.stringify([...lemmas])}, segments: ${this.segments.length}`,
      )
      this.view.setCollectedLemmas(lemmas)
      this.view.renderSubtitles(this.segments)
      // 
      this.restorePlayingIndicator()
    } else {
      this.logger.warn('[refreshCollectedHighlights] subtitle is null')
    }
  }

  /**
   * 
   */
  scrollToSegment(segmentIndex: number): void {
    this.view.scrollToSegment(segmentIndex, this.currentPlayingIndex)
    this.currentPlayingIndex = segmentIndex
  }

  /**
   * 
   * 
   */
  private restorePlayingIndicator(): void {
    if (this.currentPlayingIndex >= 0) {
      //  -1  .playing class
      this.view.scrollToSegment(this.currentPlayingIndex, -1)
    }
  }

  /**
   * 
   *  Tooltip 
   */
  updateNativeLanguage(nativeLng: string): void {
    this.nativeLng = nativeLng
    this.tooltipController.setNativeLng(nativeLng)
  }

  /**
   * 
   */
  show(): void {
    this.view.show()
    this.logger.debug('Sidebar shown')
  }

  /**
   * 
   */
  hide(): void {
    this.view.hide()
    this.tooltipController.clearLockedWord()
    this.logger.debug('Sidebar hidden')
  }

  /**
   * 
   */
  isVisible(): boolean {
    return this.view.isVisible()
  }

  /**
   * 
   */
  private setupEventListeners(): void {
    // 
    if (this.eventListenersSetup) return

    const container = this.view.getContainer()
    if (!container) return

    container.addEventListener('mouseover', this.handleMouseOver.bind(this))
    container.addEventListener('mouseout', this.handleMouseOut.bind(this))
    container.addEventListener('click', this.handleIconClick.bind(this))

    this.eventListenersSetup = true
  }

  /**
   *  API 
   *  1 
   */
  private handleMouseOver(event: Event): void {
    //  timer
    if (this.hoverTimer) {
      clearTimeout(this.hoverTimer)
      this.hoverTimer = null
    }

    const target = event.target as HTMLElement
    const subtitleItem = target.closest('.moki-subtitle-item') as HTMLElement
    if (!subtitleItem) return

    const segmentIndex = subtitleItem.dataset.index
    const segment = this.segments.find((s) => s.index === parseInt(segmentIndex || '-1'))
    if (!segment) return

    const tokenElement = target.closest('.moki-token') as HTMLElement
    const word = tokenElement?.dataset.word

    if (tokenElement && word) {
      //  tooltip
      if (this.pendingHoverWord && this.pendingHoverWord !== word) {
        this.tooltipController.clearLockedWord()
      }
      this.pendingHoverWord = word

      //  0.5  hover 
      this.hoverTimer = setTimeout(() => {
        this.hoverTimer = null
        this.tooltipController.handleWordHover(
          subtitleItem,
          segment,
          tokenElement,
          word,
          segmentIndex || '',
        )
      }, 500)
    } else {
      //  API 
      this.pendingHoverWord = null
      this.tooltipController.handleNonWordHover(subtitleItem, segment, segmentIndex || '')
    }
  }

  /**
   * 
   */
  private handleMouseOut(event: Event): void {
    const target = event.target as HTMLElement
    const relatedTarget = (event as MouseEvent).relatedTarget as HTMLElement
    const subtitleItem = target.closest('.moki-subtitle-item')

    if (subtitleItem && !subtitleItem.contains(relatedTarget)) {
      //  timer
      if (this.hoverTimer) {
        clearTimeout(this.hoverTimer)
        this.hoverTimer = null
      }
      this.pendingHoverWord = null
      this.tooltipController.clearLockedWord()
    }
  }

  /**
   *  token
   */
  private handleIconClick(event: Event): void {
    const target = event.target as HTMLElement
    const tokenElement = target.closest('.moki-token') as HTMLElement
    if (tokenElement) {
      const word = tokenElement.dataset.word
      if (word) {
        event.stopPropagation()
        this.tooltipController.handleTokenClick(tokenElement, word, this.segments)
      }
      return
    }

    const icon = target.closest('.moki-playing-icon')
    if (icon) {
      const subtitleItem = icon.closest('.moki-subtitle-item') as HTMLElement
      if (subtitleItem && !subtitleItem.classList.contains('playing')) {
        const segmentIndex = parseInt(subtitleItem.dataset.index || '-1')
        const segment = this.segments.find((s) => s.index === segmentIndex)
        if (segment) {
          this.seekToTimestamp(segment.begin)
        }
      }
    }
  }

  /**
   * 
   */
  private seekToTimestamp(timestampMs: number): void {
    const video = document.querySelector('video')
    if (video) {
      video.currentTime = timestampMs / 1000
    }
  }

  /**
   * 
   * YouTube  SPA
   * 
   */
  private async waitForInsertionPoint(
    timeout: number,
  ): Promise<{ container: HTMLElement; referenceNode: Element | null } | null> {
    return new Promise((resolve) => {
      const getPoint = () => {
        const mode = getYouTubePlayerMode()

        //  full-bleed-container 
        if (mode === 'theater' || mode === 'fullscreen') {
          const fullBleed = getFullBleedContainer()
          if (fullBleed) {
            // 
            const firstChild = fullBleed.firstElementChild
            this.logger.debug(
              `Found full-bleed-container for ${mode} mode, firstChild: ${firstChild?.id}`,
            )
            return { container: fullBleed, referenceNode: firstChild }
          }
        }

        //  #secondary
        const secondary = document.querySelector<HTMLElement>('#secondary')
        if (secondary) {
          const watchNext = secondary.querySelector('#secondary-inner')
          return { container: secondary, referenceNode: watchNext }
        }
        return null
      }

      if (getPoint()) {
        return resolve(getPoint())
      }

      const observer = new MutationObserver(() => {
        if (getPoint()) {
          observer.disconnect()
          resolve(getPoint())
        }
      })
      observer.observe(document.body, { childList: true, subtree: true })
      setTimeout(() => {
        observer.disconnect()
        resolve(getPoint())
      }, timeout)
    })
  }

  /**
   * 
   *  ytd-watch-flexy  theater/fullscreen 
   */
  private setupModeObserver(): void {
    const watchFlexy = getWatchFlexy()
    if (!watchFlexy) {
      this.logger.warn('ytd-watch-flexy not found, cannot observe mode changes')
      return
    }

    this.modeObserver = new MutationObserver((mutations) => {
      for (const mutation of mutations) {
        if (
          mutation.type === 'attributes' &&
          (mutation.attributeName === 'theater' || mutation.attributeName === 'fullscreen')
        ) {
          this.handlePlayerModeChange()
          break
        }
      }
    })

    this.modeObserver.observe(watchFlexy, { attributes: true })
    this.logger.debug('Mode observer set up')
  }

  /**
   * 
   */
  private async handlePlayerModeChange(): Promise<void> {
    const newMode = getYouTubePlayerMode()
    if (newMode === this.currentMode) return

    this.logger.info(`Player mode changed: ${this.currentMode} -> ${newMode}`)
    this.currentMode = newMode

    // 
    const insertionPoint = await this.getInsertionPointForMode(newMode)
    if (insertionPoint) {
      this.view.relocate(insertionPoint.container, insertionPoint.referenceNode, newMode)
    }
  }

  /**
   * 
   */
  private getInsertionPointForMode(
    mode: YouTubePlayerMode,
  ): { container: HTMLElement; referenceNode: Element | null } | null {
    if (mode === 'theater' || mode === 'fullscreen') {
      const fullBleed = getFullBleedContainer()
      if (fullBleed) {
        // 
        const firstChild = fullBleed.firstElementChild
        return { container: fullBleed, referenceNode: firstChild }
      }
    }

    // 
    const secondary = document.querySelector<HTMLElement>('#secondary')
    if (secondary) {
      const watchNext = secondary.querySelector('#secondary-inner')
      return { container: secondary, referenceNode: watchNext }
    }
    return null
  }
}
