/**
 * YouTube Subtitle Trigger
 *
 * Handles fetching subtitle data by directly requesting from YouTube's API.
 *
 * Flow:
 * 1. Parse playerCaptionsTracklistRenderer from ytInitialPlayerResponse
 * 2. Select best caption track (ASR > non-ASR > first)
 * 3. Fetch subtitle data using track's baseUrl
 */

import { Logger } from '../../tools/logger'

// ============================================================================
// Type Definitions
// ============================================================================

export interface CaptionTrack {
  baseUrl: string
  languageCode: string
  name: { simpleText: string }
  isTranslatable: boolean
  vssId: string
  kind?: string // 'asr' for auto-generated
  trackName?: string
}

export interface TranslationLanguage {
  languageCode: string
  languageName: { simpleText: string }
}

interface CaptionsRenderer {
  captionTracks?: CaptionTrack[]
  translationLanguages?: TranslationLanguage[]
  audioTracks?: Array<{
    captionTrackIndices?: number[]
    defaultCaptionTrackIndex?: number
    hasDefaultTrack?: boolean
  }>
  defaultAudioTrackIndex?: number
}

interface CaptionsData {
  playerCaptionsTracklistRenderer?: CaptionsRenderer
}

interface PlayerResponse {
  captions?: CaptionsData
  videoDetails?: {
    videoId?: string
  }
}

// ============================================================================
// Module State
// ============================================================================

const logger = new Logger('[SubtitleTrigger]')

// Cached captions data from ytInitialPlayerResponse
let cachedCaptions: CaptionsData | null = null

// Callback to notify when captions data is received
type CaptionsReadyCallback = () => void
let captionsReadyCallback: CaptionsReadyCallback | null = null

// ============================================================================
// Language Code Utilities
// ============================================================================

/**
 * YouTube language code to standard BCP 47 code mapping.
 * YouTube uses non-standard codes like 'zh-Hans', 'zh-Hant', 'zh'.
 */
const YOUTUBE_LANG_CODE_MAP: Record<string, string> = {
  'zh-Hans': 'zh-CN',
  'zh-Hant': 'zh-TW',
  zh: 'zh-CN',
}

/**
 * Normalize YouTube language code to standard BCP 47 format.
 * @param youtubeCode - YouTube's language code (e.g., 'zh-Hans', 'zh-Hant', 'zh', 'en')
 * @returns Normalized language code (e.g., 'zh-CN', 'zh-TW', 'en')
 */
export function normalizeYoutubeLangCode(youtubeCode: string): string {
  return YOUTUBE_LANG_CODE_MAP[youtubeCode] || youtubeCode
}

// ============================================================================
// Captions Data Listener
// ============================================================================

/**
 * Initialize the captions data listener.
 * Receives captions data from Injected Script via VIDEO_METADATA message.
 * Should be called once when Content Script initializes.
 */
export function initCaptionsListener(): void {
  window.addEventListener('message', (event) => {
    if (event.source !== window) return
    if (event.data?.source === 'INJECTED_SCRIPT' && event.data?.type === 'VIDEO_METADATA') {
      const playerResponse = event.data.data?.playerResponse
      if (playerResponse) {
        cachedCaptions = playerResponse.captions || null
        logger.debug('Received captions data from Injected Script', {
          hasCaptions: !!cachedCaptions,
          trackCount: cachedCaptions?.playerCaptionsTracklistRenderer?.captionTracks?.length || 0,
        })

        // Notify callback that captions are ready
        if (captionsReadyCallback) {
          try {
            captionsReadyCallback()
          } catch (error) {
            logger.error('Error in captions ready callback:', error)
          }
        }
      }
    }
  })
}

/**
 * Set callback to be called when captions data is received.
 * Only one callback is supported - if multiple components need this data,
 * use Background Service broadcast pattern instead.
 * @param callback - Function to call when captions are ready
 */
export function onCaptionsReady(callback: CaptionsReadyCallback): void {
  captionsReadyCallback = callback
}

/**
 * Clear cached captions data and registered callback.
 * Call this when navigating to a new video.
 */
export function clearCaptionsCache(): void {
  cachedCaptions = null
  // Clear callback to prevent memory leaks and stale references
  captionsReadyCallback = null
  logger.debug('Captions cache and callback cleared')
}

// ============================================================================
// Subtitle Availability Check
// ============================================================================

/**
 * Check if the current video has subtitles available.
 * Uses cached playerResponse.captions data.
 * @returns true if subtitles are available
 */
export function hasSubtitles(): boolean {
  const tracks = cachedCaptions?.playerCaptionsTracklistRenderer?.captionTracks
  return Array.isArray(tracks) && tracks.length > 0
}

/**
 * Get all available caption tracks.
 * @returns Array of caption tracks
 */
export function getAvailableCaptionTracks(): CaptionTrack[] {
  return cachedCaptions?.playerCaptionsTracklistRenderer?.captionTracks || []
}

/**
 * Get all available translation languages.
 * These can be used with tlang parameter to request translated subtitles.
 * @returns Array of translation languages
 */
export function getTranslationLanguages(): TranslationLanguage[] {
  return cachedCaptions?.playerCaptionsTracklistRenderer?.translationLanguages || []
}

/**
 * Check if a specific translation language is available.
 * @param languageCode - The language code to check (e.g., 'zh-Hans', 'ja')
 * @returns true if the translation language is available
 */
export function isTranslationAvailable(languageCode: string): boolean {
  const languages = getTranslationLanguages()
  return languages.some((lang) => lang.languageCode === languageCode)
}

/**
 * Select the best caption track based on priority:
 * 1. ASR (auto-generated) subtitles - better word-level timestamps
 * 2. Video's native language subtitles
 * 3. First available track
 *
 * @returns The best caption track, or null if none available
 */
export function selectBestCaptionTrack(): CaptionTrack | null {
  const tracks = getAvailableCaptionTracks()
  if (tracks.length === 0) return null

  // Priority 1: ASR (auto-generated) subtitles
  const asrTrack = tracks.find((track) => track.kind === 'asr' || track.vssId?.startsWith('a.'))
  if (asrTrack) {
    logger.debug('Selected ASR track:', asrTrack.languageCode)
    return asrTrack
  }

  // Priority 2: Video's native language (from videoDetails or microformat)
  // For now, we don't have reliable way to get video language, so skip this

  // Priority 3: First available track
  logger.debug('Selected first available track:', tracks[0].languageCode)
  return tracks[0]
}

// ============================================================================
// CC Button Operations
// ============================================================================

/**
 * Get YouTube's CC (subtitles) button element.
 * @returns The CC button element, or null if not found
 */
export function getSubtitleButton(): HTMLButtonElement | null {
  return document.querySelector('.ytp-subtitles-button') as HTMLButtonElement
}

/**
 * Check if the CC button is currently enabled (subtitles are showing).
 * @returns true if CC is enabled
 */
export function isSubtitleButtonEnabled(): boolean {
  const button = getSubtitleButton()
  return button?.getAttribute('aria-pressed') === 'true'
}

/**
 * Simulate clicking the CC button.
 * @returns true if click was successful
 */
export function clickSubtitleButton(): boolean {
  const button = getSubtitleButton()
  if (!button) {
    logger.warn('CC button not found')
    return false
  }

  button.click()
  logger.debug('CC button clicked')
  return true
}

// ============================================================================
// Subtitle Fetch Helpers
// ============================================================================

/**
 * Modify the captured subtitle URL with best track params.
 *
 * @param originalUrl - The original timedtext URL captured from YouTube
 * @param tlang - Optional translation language code
 * @returns Modified URL with kind and tlang params
 */
function modifySubtitleUrl(originalUrl: string, tlang?: string): string {
  try {
    const url = new URL(originalUrl)
    const track = selectBestCaptionTrack()

    // Add kind=asr if best track is ASR
    if (track && (track.kind === 'asr' || track.vssId?.startsWith('a.'))) {
      url.searchParams.set('kind', 'asr')
    }

    // Add or remove tlang based on request
    // If the language has native caption (in captionTracks), don't add tlang
    // Only add tlang when requesting a translation (language not in captionTracks)
    const captionTracks = getAvailableCaptionTracks()
    const hasNativeCaption = captionTracks.some((track) => track.languageCode === tlang)

    if (tlang && !hasNativeCaption && isTranslationAvailable(tlang)) {
      // Only add tlang when no native caption but translation is available
      url.searchParams.set('tlang', tlang)
    } else {
      // Has native caption or no translation needed
      url.searchParams.delete('tlang')
    }

    // Ensure JSON format
    url.searchParams.set('fmt', 'json3')

    const modifiedUrl = url.toString()
    logger.debug('Modified subtitle URL:', { original: originalUrl, modified: modifiedUrl })
    return modifiedUrl
  } catch (e) {
    logger.error('Failed to modify URL:', e)
    return originalUrl
  }
}

/**
 * Fetch subtitle data using the modified URL.
 * Uses fetch() in Content Script context which shares cookies with the page.
 *
 * @param url - The modified subtitle URL
 * @returns Promise with subtitle events and URL
 */
async function fetchSubtitleData(url: string): Promise<{ events: any[]; url: string }> {
  logger.debug('Fetching subtitle with Content Script fetch:', url)

  const response = await fetch(url, {
    credentials: 'include', // Include cookies
  })

  if (!response.ok) {
    throw new Error(`Failed to fetch subtitle: ${response.status} ${response.statusText}`)
  }

  const data = await response.json()

  if (!data.events) {
    throw new Error('Invalid subtitle response: missing events')
  }

  logger.info(`Fetched subtitle: ${data.events.length} events`)
  return { events: data.events, url }
}

// ============================================================================
// Trigger and Wait for Subtitle Data
// ============================================================================

/**
 * Trigger subtitle request by clicking CC button and fetch subtitle data.
 *
 * Flow:
 * 1. Click CC button to trigger YouTube's subtitle request
 * 2. Injected Script detects timedtext request and notifies Content Script (stateless)
 * 3. Content Script receives the URL, modifies it (add kind/tlang), and fetches with fetch()
 * 4. Return the subtitle data
 *
 * Note: The original YouTube request proceeds normally, but we hide native subtitles
 * with hideYoutubeNativeSubtitle() so users don't see them.
 *
 * @param tlang - Optional translation language code (e.g., 'zh-Hans')
 * @param timeout - Maximum time to wait in milliseconds (default: 5000)
 * @returns Promise that resolves with subtitle events data
 */
export async function triggerAndWaitForSubtitle(
  tlang?: string,
  timeout: number = 5000,
): Promise<{ events: any[]; url: string }> {
  logger.info('Triggering subtitle fetch', { tlang })

  return new Promise((resolve, reject) => {
    let timeoutId: NodeJS.Timeout
    let resolved = false

    // Set up message listener for detected subtitle URL
    const handler = async (event: MessageEvent) => {
      if (event.source !== window) return
      if (
        event.data?.source === 'XHR_INTERCEPTED' &&
        event.data?.type === 'SUBTITLE_URL_DETECTED' &&
        !resolved
      ) {
        resolved = true
        window.removeEventListener('message', handler)
        clearTimeout(timeoutId)

        const detectedUrl = event.data.data.url
        logger.info('Received detected subtitle URL:', detectedUrl)

        try {
          // Modify URL with best track params
          const modifiedUrl = modifySubtitleUrl(detectedUrl, tlang)

          // Fetch subtitle data using Content Script's fetch
          const result = await fetchSubtitleData(modifiedUrl)
          resolve(result)
        } catch (err) {
          reject(new Error(`Failed to fetch subtitle: ${err}`))
        }
      }
    }

    window.addEventListener('message', handler)

    // Set up timeout
    timeoutId = setTimeout(() => {
      if (!resolved) {
        resolved = true
        window.removeEventListener('message', handler)
        reject(new Error('Timeout waiting for subtitle URL detection'))
      }
    }, timeout)

    // Trigger the CC button
    const wasEnabled = isSubtitleButtonEnabled()

    if (!wasEnabled) {
      // CC is off, click to turn on (triggers subtitle request)
      const clicked = clickSubtitleButton()
      if (!clicked) {
        resolved = true
        window.removeEventListener('message', handler)
        clearTimeout(timeoutId)
        reject(new Error('Failed to click CC button'))
      }
    } else {
      // CC is already on, need to toggle off then on to trigger new request
      logger.debug('CC already enabled, toggling to trigger new request')
      clickSubtitleButton() // Turn off

      setTimeout(() => {
        if (!resolved) {
          clickSubtitleButton() // Turn back on
        }
      }, 100)
    }
  })
}

/**
 * Hide YouTube's native subtitle display using CSS injection.
 * This allows us to show our custom subtitle UI instead.
 */
export function hideYoutubeNativeSubtitle(): void {
  const styleId = 'moki-hide-youtube-subtitle'
  if (document.getElementById(styleId)) {
    return
  }

  const style = document.createElement('style')
  style.id = styleId
  style.textContent = `
    #ytp-caption-window-container,
    .html5-video-player #ytp-caption-window-container {
      display: none !important;
    }
  `
  ;(document.head || document.documentElement).appendChild(style)
  logger.debug('YouTube native subtitle hidden')
}

/**
 * Show YouTube's native subtitle display by removing the hide CSS.
 */
export function showYoutubeNativeSubtitle(): void {
  const style = document.getElementById('moki-hide-youtube-subtitle')
  if (style) {
    style.remove()
    logger.debug('YouTube native subtitle shown')
  }
}
