/**
 * YouTube Moki Control Button
 *
 * A custom control button placed in YouTube's player control bar (ytp-right-controls).
 * Allows users to toggle Moki subtitle functionality on/off.
 *
 * Features:
 * - Visual states: disabled (white), enabled (green), unavailable (gray)
 * - Keyboard shortcut: 'V' key
 * - Tooltip on hover
 */

import feather from 'feather-icons'
import { SUPPORTED_LANGUAGES, getLanguageName } from '../../context'
import { t } from '../../i18n'
import { Logger } from '../../tools/logger'
import {
  hasSubtitles,
  getAvailableCaptionTracks,
  getTranslationLanguages,
  normalizeYoutubeLangCode,
  onCaptionsReady,
  CaptionTrack,
} from './youtubeSubtitleTrigger'
import './youtubeControl.css'

// ============================================================================
// Type Definitions
// ============================================================================

export interface MokiControlCallbacks {
  onEnable: () => Promise<void>
  onDisable: () => void
  onLanguageChange?: (langCode: string) => void
}

export interface MokiControlOptions {
  shortcutKey?: string
  initialEnabled?: boolean
}

// Available language item for submenu
interface AvailableLanguage {
  code: string
  name: string
  isVideoLanguage: boolean // 
  isAsr: boolean // 
}

// ============================================================================
// Constants
// ============================================================================

const BUTTON_ID = 'moki-control-button'
const TOOLTIP_ID = 'moki-control-tooltip'
const MENU_ID = 'moki-control-menu'
const SUBMENU_ID = 'moki-control-submenu'

// SVG icon for Moki button (M letter in a circle)
const MOKI_ICON_SVG = `
<svg viewBox="0 0 36 36" class="moki-control-icon">
  <circle cx="18" cy="18" r="14" fill="none" stroke="currentColor" stroke-width="2"/>
  <text x="18" y="19" fill="currentColor" dominant-baseline="central" text-anchor="middle"
        font-size="16" font-weight="bold" font-family="Arial, sans-serif">M</text>
</svg>
`

// Icons from feather-icons
const CHEVRON_RIGHT_SVG = feather.icons['chevron-right'].toSvg({ width: 16, height: 16 })
const CHEVRON_LEFT_SVG = feather.icons['chevron-left'].toSvg({ width: 16, height: 16 })
const CHECK_SVG = feather.icons['check'].toSvg({ width: 16, height: 16 })

// ============================================================================
// MokiControl Class
// ============================================================================

export class MokiControl {
  private logger = new Logger('[MokiControl]')
  private callbacks: MokiControlCallbacks
  private options: MokiControlOptions

  private button: HTMLButtonElement | null = null
  private tooltip: HTMLDivElement | null = null
  private menu: HTMLDivElement | null = null
  private submenu: HTMLDivElement | null = null
  private menuVisible: boolean = false
  private submenuVisible: boolean = false
  private enabled: boolean = false
  private available: boolean = true
  private loading: boolean = false
  private selectedLanguage: string = 'en'

  private boundKeyHandler: ((e: KeyboardEvent) => void) | null = null
  private boundOutsideClickHandler: ((e: MouseEvent) => void) | null = null

  constructor(callbacks: MokiControlCallbacks, options?: MokiControlOptions) {
    this.callbacks = callbacks
    this.options = {
      shortcutKey: 'v',
      initialEnabled: false,
      ...options,
    }
    this.enabled = this.options.initialEnabled || false
  }

  /**
   * Initialize the control button.
   * Waits for ytp-right-controls to appear, then creates the button.
   */
  async init(): Promise<void> {
    const rightControls = await this.waitForRightControls()
    if (!rightControls) {
      this.logger.warn('Right controls not found, cannot create Moki button')
      return
    }

    this.createButton(rightControls)
    this.createMenu()
    this.createSubmenu()
    this.setupKeyboardShortcut()
    this.setupOutsideClickHandler()

    // Check if subtitles are available
    this.setAvailable(hasSubtitles())

    // Register callback to update when captions data becomes available
    onCaptionsReady(() => this.handleCaptionsUpdate())

    // If captions data already available, update immediately
    if (hasSubtitles()) {
      this.handleCaptionsUpdate()
    }

    this.logger.info('Moki control button initialized')
  }

  /**
   * Handle captions data update.
   * Called when VIDEO_METADATA is received (either via callback or on init if already available).
   */
  private handleCaptionsUpdate(): void {
    this.initDefaultLanguage()
    this.updateMenuLanguageDisplay()
    this.refreshSubmenuContent()
    this.setAvailable(hasSubtitles())
  }

  /**
   * Initialize default language selection.
   * Prefers the video's native language, falls back to first available or 'en'.
   */
  private initDefaultLanguage(): void {
    const availableLanguages = this.getAvailableLanguages()

    // Find video's native language
    const videoLanguage = availableLanguages.find((lang) => lang.isVideoLanguage)
    if (videoLanguage) {
      this.selectedLanguage = videoLanguage.code
      return
    }

    // Fall back to first available language
    if (availableLanguages.length > 0) {
      this.selectedLanguage = availableLanguages[0].code
    }
  }

  /**
   * Wait for YouTube's right controls container to appear.
   */
  private async waitForRightControls(timeout: number = 10000): Promise<Element | null> {
    return new Promise((resolve) => {
      const startTime = Date.now()

      const check = () => {
        const rightControls = document.querySelector('.ytp-right-controls')
        if (rightControls) {
          resolve(rightControls)
          return
        }

        if (Date.now() - startTime > timeout) {
          resolve(null)
          return
        }

        requestAnimationFrame(check)
      }

      check()
    })
  }

  /**
   * Create the Moki control button and insert it into the player controls.
   */
  private createButton(rightControls: Element): void {
    // Remove existing button if any
    const existing = document.getElementById(BUTTON_ID)
    if (existing) {
      existing.remove()
    }

    // Create button element
    this.button = document.createElement('button')
    this.button.id = BUTTON_ID
    this.button.className = 'ytp-button moki-control-button'
    this.button.setAttribute('aria-label', 'Moki Subtitles')
    this.button.setAttribute('title', '')
    this.button.innerHTML = MOKI_ICON_SVG

    // Create tooltip
    this.tooltip = document.createElement('div')
    this.tooltip.id = TOOLTIP_ID
    this.tooltip.className = 'moki-control-tooltip'
    this.tooltip.textContent = `Moki Subtitles (${this.options.shortcutKey?.toUpperCase()})`

    // Add event listeners
    this.button.addEventListener('click', () => this.handleClick())
    this.button.addEventListener('mouseenter', () => this.showTooltip())
    this.button.addEventListener('mouseleave', () => this.hideTooltip())

    // Insert at the beginning of right controls (leftmost position)
    rightControls.insertBefore(this.button, rightControls.firstChild)

    // Append tooltip to body
    document.body.appendChild(this.tooltip)

    // Update visual state
    this.updateButtonState()

    this.logger.debug('Button created and inserted')
  }

  /**
   * Set up keyboard shortcut listener.
   */
  private setupKeyboardShortcut(): void {
    this.boundKeyHandler = (e: KeyboardEvent) => {
      // Ignore if typing in input/textarea
      if (
        e.target instanceof HTMLInputElement ||
        e.target instanceof HTMLTextAreaElement ||
        (e.target as HTMLElement)?.isContentEditable
      ) {
        return
      }

      if (e.key.toLowerCase() === this.options.shortcutKey) {
        e.preventDefault()
        this.handleClick()
      }
    }

    document.addEventListener('keydown', this.boundKeyHandler)
  }

  /**
   * Handle button click - toggle menu visibility.
   */
  private handleClick(): void {
    if (!this.available) {
      this.logger.debug('Button not available, ignoring click')
      return
    }

    if (this.menuVisible) {
      this.hideMenu()
    } else {
      this.showMenu()
    }
  }

  /**
   * Enable Moki subtitles.
   */
  async enable(): Promise<void> {
    if (this.loading) return

    this.loading = true
    this.updateButtonState()

    try {
      await this.callbacks.onEnable()
      this.enabled = true
      this.logger.info('Moki enabled')
    } catch (error) {
      this.logger.error('Failed to enable Moki:', error)
      // Reset state on error
      this.enabled = false
    } finally {
      this.loading = false
      this.updateButtonState()
    }
  }

  /**
   * Disable Moki subtitles.
   */
  disable(): void {
    if (this.enabled) {
      this.callbacks.onDisable()
    }
    this.enabled = false
    this.loading = false
    this.updateButtonState()
    this.logger.info('Moki disabled')
  }

  /**
   * Toggle enabled state.
   */
  async toggle(): Promise<void> {
    await this.handleClick()
  }

  /**
   * Check if Moki is currently enabled.
   */
  isEnabled(): boolean {
    return this.enabled
  }

  /**
   * Set button availability (when video has no subtitles).
   */
  setAvailable(available: boolean): void {
    this.available = available
    this.updateButtonState()

    if (!available) {
      this.logger.debug('Button set to unavailable (no subtitles)')
    }
  }

  /**
   * Update the visual state of the button.
   */
  private updateButtonState(): void {
    if (!this.button) return

    // Remove all state classes
    this.button.classList.remove(
      'moki-control-enabled',
      'moki-control-disabled',
      'moki-control-unavailable',
      'moki-control-loading',
    )

    // Add appropriate class
    if (this.loading) {
      this.button.classList.add('moki-control-loading')
    } else if (!this.available) {
      this.button.classList.add('moki-control-unavailable')
    } else if (this.enabled) {
      this.button.classList.add('moki-control-enabled')
    } else {
      this.button.classList.add('moki-control-disabled')
    }

    // Update aria-pressed
    this.button.setAttribute('aria-pressed', this.enabled ? 'true' : 'false')
  }

  /**
   * Create the settings menu.
   */
  private createMenu(): void {
    // Remove existing menu if any
    const existing = document.getElementById(MENU_ID)
    if (existing) {
      existing.remove()
    }

    this.menu = document.createElement('div')
    this.menu.id = MENU_ID
    this.menu.className = 'moki-control-menu'
    this.menu.innerHTML = `
      <div class="moki-menu-row moki-menu-toggle-row">
        <span class="moki-menu-label" data-i18n="enableMoki">${t('mokiControl_enableMoki')}</span>
        <div class="moki-menu-toggle ${this.enabled ? 'active' : ''}" data-action="toggle">
          <div class="moki-menu-toggle-knob"></div>
        </div>
      </div>
      <div class="moki-menu-row moki-menu-subtitle-row" data-action="subtitle">
        <span class="moki-menu-label" data-i18n="subtitle">${t('mokiControl_subtitle')}</span>
        <div class="moki-menu-value">
          <span>${getLanguageName(this.selectedLanguage)}</span>
          ${CHEVRON_RIGHT_SVG}
        </div>
      </div>
    `

    // Add click handler for toggle
    this.menu.addEventListener('click', (e) => this.handleMenuClick(e))

    // Append to player (positioned via CSS)
    const player = document.querySelector('.html5-video-player')
    if (player) {
      player.appendChild(this.menu)
    }

    this.logger.debug('Menu created')
  }

  /**
   * Handle menu item clicks.
   */
  private handleMenuClick(e: MouseEvent): void {
    const target = e.target as HTMLElement

    // Handle toggle click
    const toggle = target.closest('[data-action="toggle"]')
    if (toggle) {
      e.stopPropagation()
      if (this.enabled) {
        this.disable()
      } else {
        this.enable()
      }
      // Update toggle visual state
      toggle.classList.toggle('active', this.enabled)
      return
    }

    // Handle subtitle row click - show submenu
    const subtitleRow = target.closest('[data-action="subtitle"]')
    if (subtitleRow) {
      e.stopPropagation()
      this.showSubmenu()
    }
  }

  /**
   * Show the settings menu.
   */
  private showMenu(): void {
    if (!this.menu) return

    // Update toggle state before showing
    const toggle = this.menu.querySelector('.moki-menu-toggle')
    if (toggle) {
      toggle.classList.toggle('active', this.enabled)
    }

    this.menu.classList.add('visible')
    this.menuVisible = true
    this.hideTooltip()
    this.logger.debug('Menu shown')
  }

  /**
   * Hide the settings menu.
   */
  private hideMenu(): void {
    if (!this.menu) return
    this.menu.classList.remove('visible')
    this.menuVisible = false
    this.hideSubmenu()
    this.logger.debug('Menu hidden')
  }

  /**
   * Get available languages by intersecting caption tracks with supported languages.
   * Priority: ASR tracks > non-ASR tracks
   * Also marks the video's native language.
   */
  private getAvailableLanguages(): AvailableLanguage[] {
    const captionTracks = getAvailableCaptionTracks()
    const translationLanguages = getTranslationLanguages()

    // Create a set of supported languages for quick lookup
    const supportedLangSet = new Set<string>(SUPPORTED_LANGUAGES)

    const result: AvailableLanguage[] = []
    const addedCodes = new Set<string>()

    // Detect video's native language (first non-translated track, preferably ASR)
    let videoLanguageCode: string | null = null
    const asrTrack = captionTracks.find((t) => t.kind === 'asr' || t.vssId?.startsWith('a.'))
    if (asrTrack) {
      videoLanguageCode = normalizeYoutubeLangCode(asrTrack.languageCode)
    } else if (captionTracks.length > 0) {
      videoLanguageCode = normalizeYoutubeLangCode(captionTracks[0].languageCode)
    }

    // Process caption tracks (these are direct subtitles, not translations)
    // Group by language code, prefer ASR
    const tracksByLang = new Map<string, CaptionTrack>()
    for (const track of captionTracks) {
      const normalizedCode = normalizeYoutubeLangCode(track.languageCode)
      const existing = tracksByLang.get(normalizedCode)

      // Prefer ASR track over non-ASR
      const isAsr = track.kind === 'asr' || track.vssId?.startsWith('a.')
      const existingIsAsr = existing?.kind === 'asr' || existing?.vssId?.startsWith('a.')

      if (!existing || (isAsr && !existingIsAsr)) {
        tracksByLang.set(normalizedCode, track)
      }
    }

    // Add caption track languages that we support
    for (const [code, track] of tracksByLang) {
      if (supportedLangSet.has(code) && !addedCodes.has(code)) {
        const isAsr = track.kind === 'asr' || track.vssId?.startsWith('a.')
        result.push({
          code,
          name: getLanguageName(code),
          isVideoLanguage: code === videoLanguageCode,
          isAsr,
        })
        addedCodes.add(code)
      }
    }

    // Add translation languages that we support (these require tlang parameter)
    for (const transLang of translationLanguages) {
      const normalizedCode = normalizeYoutubeLangCode(transLang.languageCode)
      if (supportedLangSet.has(normalizedCode) && !addedCodes.has(normalizedCode)) {
        result.push({
          code: normalizedCode,
          name: getLanguageName(normalizedCode),
          isVideoLanguage: false,
          isAsr: false,
        })
        addedCodes.add(normalizedCode)
      }
    }

    // Sort: video language first, then by SUPPORTED_LANGUAGES order
    result.sort((a, b) => {
      if (a.isVideoLanguage && !b.isVideoLanguage) return -1
      if (!a.isVideoLanguage && b.isVideoLanguage) return 1
      // Sort by SUPPORTED_LANGUAGES order
      const indexA = SUPPORTED_LANGUAGES.indexOf(a.code as (typeof SUPPORTED_LANGUAGES)[number])
      const indexB = SUPPORTED_LANGUAGES.indexOf(b.code as (typeof SUPPORTED_LANGUAGES)[number])
      return indexA - indexB
    })

    this.logger.debug('Available languages:', result)
    return result
  }

  /**
   * Create the subtitle language submenu.
   */
  private createSubmenu(): void {
    // Remove existing submenu if any
    const existing = document.getElementById(SUBMENU_ID)
    if (existing) {
      existing.remove()
    }

    this.submenu = document.createElement('div')
    this.submenu.id = SUBMENU_ID
    this.submenu.className = 'moki-control-submenu'

    // Get available languages dynamically
    const availableLanguages = this.getAvailableLanguages()

    // Build language options
    const languageItems = availableLanguages
      .map((lang) => {
        const isSelected = lang.code === this.selectedLanguage
        const label = lang.isVideoLanguage
          ? `${lang.name} (${t('mokiControl_videoLanguage')})`
          : lang.name
        return `
        <div class="moki-submenu-row ${isSelected ? 'selected' : ''}" data-action="select-lang" data-lang="${lang.code}">
          <span class="moki-submenu-check">${isSelected ? CHECK_SVG : ''}</span>
          <span class="moki-submenu-label">${label}</span>
        </div>
      `
      })
      .join('')

    // Show empty state if no languages available
    const content =
      availableLanguages.length > 0
        ? languageItems
        : `<div class="moki-submenu-empty">${t('mokiControl_noSubtitle')}</div>`

    this.submenu.innerHTML = `
      <div class="moki-submenu-header" data-action="back">
        ${CHEVRON_LEFT_SVG}
        <span>${t('mokiControl_subtitle')}</span>
      </div>
      <div class="moki-submenu-content">
        ${content}
      </div>
    `

    // Add click handler
    this.submenu.addEventListener('click', (e) => this.handleSubmenuClick(e))

    // Append to player
    const player = document.querySelector('.html5-video-player')
    if (player) {
      player.appendChild(this.submenu)
    }

    this.logger.debug('Submenu created')
  }

  /**
   * Handle submenu item clicks.
   */
  private handleSubmenuClick(e: MouseEvent): void {
    const target = e.target as HTMLElement

    // Handle back button
    const backBtn = target.closest('[data-action="back"]')
    if (backBtn) {
      e.stopPropagation()
      this.hideSubmenu()
      return
    }

    // Handle language selection
    const langRow = target.closest('[data-action="select-lang"]') as HTMLElement
    if (langRow) {
      e.stopPropagation()
      const langCode = langRow.dataset.lang
      if (langCode) {
        this.selectLanguage(langCode)
      }
    }
  }

  /**
   * Select a subtitle language.
   */
  private selectLanguage(langCode: string): void {
    const previousLanguage = this.selectedLanguage
    this.selectedLanguage = langCode

    // Get language name from available languages or fall back to i18n lookup
    const availableLanguages = this.getAvailableLanguages()
    const lang = availableLanguages.find((l) => l.code === langCode)
    const langName = lang?.name || getLanguageName(langCode)

    // Update menu display
    if (this.menu) {
      const valueSpan = this.menu.querySelector('.moki-menu-value span')
      if (valueSpan) {
        valueSpan.textContent = langName
      }
    }

    // Update submenu check marks
    if (this.submenu) {
      const rows = this.submenu.querySelectorAll('.moki-submenu-row')
      rows.forEach((row) => {
        const rowLang = (row as HTMLElement).dataset.lang
        const checkSpan = row.querySelector('.moki-submenu-check')
        if (rowLang === langCode) {
          row.classList.add('selected')
          if (checkSpan) checkSpan.innerHTML = CHECK_SVG
        } else {
          row.classList.remove('selected')
          if (checkSpan) checkSpan.innerHTML = ''
        }
      })
    }

    this.logger.info(`Language selected: ${langCode}`)
    this.hideSubmenu()

    // If enabled and language changed, notify callback
    if (this.enabled && langCode !== previousLanguage && this.callbacks.onLanguageChange) {
      this.logger.info(`Language changed while enabled, triggering callback`)
      this.callbacks.onLanguageChange(langCode)
    }
  }

  /**
   * Get the currently selected subtitle language.
   */
  getSelectedLanguage(): string {
    return this.selectedLanguage
  }

  /**
   * Show the subtitle language submenu.
   * Refreshes the language list each time to ensure latest data.
   */
  private showSubmenu(): void {
    if (!this.submenu) return

    // Refresh language list each time submenu is shown
    // (captions data may not be available when submenu was first created)
    this.refreshSubmenuContent()

    this.submenu.classList.add('visible')
    this.submenuVisible = true
    this.logger.debug('Submenu shown')
  }

  /**
   * Refresh submenu content with latest available languages.
   */
  private refreshSubmenuContent(): void {
    if (!this.submenu) return

    const availableLanguages = this.getAvailableLanguages()
    const contentEl = this.submenu.querySelector('.moki-submenu-content')
    if (!contentEl) return

    // Build language options
    const languageItems = availableLanguages
      .map((lang) => {
        const isSelected = lang.code === this.selectedLanguage
        const label = lang.isVideoLanguage
          ? `${lang.name} (${t('mokiControl_videoLanguage')})`
          : lang.name
        return `
        <div class="moki-submenu-row ${isSelected ? 'selected' : ''}" data-action="select-lang" data-lang="${lang.code}">
          <span class="moki-submenu-check">${isSelected ? CHECK_SVG : ''}</span>
          <span class="moki-submenu-label">${label}</span>
        </div>
      `
      })
      .join('')

    // Show empty state if no languages available
    contentEl.innerHTML =
      availableLanguages.length > 0
        ? languageItems
        : `<div class="moki-submenu-empty">${t('mokiControl_noSubtitle')}</div>`

    this.logger.debug('Submenu content refreshed', { languageCount: availableLanguages.length })
  }

  /**
   * Update the main menu's language display to match selectedLanguage.
   */
  private updateMenuLanguageDisplay(): void {
    if (!this.menu) return

    const valueSpan = this.menu.querySelector('.moki-menu-value span')
    if (valueSpan) {
      valueSpan.textContent = getLanguageName(this.selectedLanguage)
    }
  }

  /**
   * Hide the subtitle language submenu.
   */
  private hideSubmenu(): void {
    if (!this.submenu) return
    this.submenu.classList.remove('visible')
    this.submenuVisible = false
    this.logger.debug('Submenu hidden')
  }

  /**
   * Setup handler to close menu when clicking outside.
   */
  private setupOutsideClickHandler(): void {
    this.boundOutsideClickHandler = (e: MouseEvent) => {
      if (!this.menuVisible && !this.submenuVisible) return

      const target = e.target as HTMLElement
      const isClickInMenu = this.menu?.contains(target)
      const isClickInSubmenu = this.submenu?.contains(target)
      const isClickOnButton = this.button?.contains(target)

      if (!isClickInMenu && !isClickInSubmenu && !isClickOnButton) {
        this.hideMenu()
      }
    }

    document.addEventListener('click', this.boundOutsideClickHandler)
  }

  /**
   * Show tooltip near button.
   */
  private showTooltip(): void {
    if (!this.tooltip || !this.button) return

    const rect = this.button.getBoundingClientRect()
    const shortcut = this.options.shortcutKey?.toUpperCase() || 'V'

    // Update tooltip text based on state
    if (this.loading) {
      this.tooltip.textContent = t('mokiControl_tooltip_loading')
    } else if (!this.available) {
      this.tooltip.textContent = t('mokiControl_tooltip_unavailable')
    } else if (this.enabled) {
      this.tooltip.textContent = `${t('mokiControl_tooltip_disable')} (${shortcut})`
    } else {
      this.tooltip.textContent = `${t('mokiControl_tooltip_enable')} (${shortcut})`
    }

    // Position above button
    this.tooltip.style.left = `${rect.left + rect.width / 2}px`
    this.tooltip.style.top = `${rect.top - 8}px`
    this.tooltip.classList.add('visible')
  }

  /**
   * Hide tooltip.
   */
  private hideTooltip(): void {
    if (!this.tooltip) return
    this.tooltip.classList.remove('visible')
  }

  /**
   * Refresh all i18n text in the menu.
   * Call this when interface language changes.
   */
  refreshI18n(): void {
    // Refresh main menu labels
    if (this.menu) {
      const enableLabel = this.menu.querySelector('[data-i18n="enableMoki"]')
      if (enableLabel) {
        enableLabel.textContent = t('mokiControl_enableMoki')
      }

      const subtitleLabel = this.menu.querySelector('[data-i18n="subtitle"]')
      if (subtitleLabel) {
        subtitleLabel.textContent = t('mokiControl_subtitle')
      }

      // Update language name in menu
      const valueSpan = this.menu.querySelector('.moki-menu-value span')
      if (valueSpan) {
        valueSpan.textContent = getLanguageName(this.selectedLanguage)
      }
    }

    // Refresh submenu header
    if (this.submenu) {
      const headerText = this.submenu.querySelector('.moki-submenu-header span')
      if (headerText) {
        headerText.textContent = t('mokiControl_subtitle')
      }

      // Refresh submenu content (language names)
      this.refreshSubmenuContent()
    }

    this.logger.debug('MokiControl i18n refreshed')
  }

  /**
   * Clean up resources.
   */
  cleanup(): void {
    // Remove keyboard listener
    if (this.boundKeyHandler) {
      document.removeEventListener('keydown', this.boundKeyHandler)
      this.boundKeyHandler = null
    }

    // Remove outside click handler
    if (this.boundOutsideClickHandler) {
      document.removeEventListener('click', this.boundOutsideClickHandler)
      this.boundOutsideClickHandler = null
    }

    // Remove button
    if (this.button) {
      this.button.remove()
      this.button = null
    }

    // Remove tooltip
    if (this.tooltip) {
      this.tooltip.remove()
      this.tooltip = null
    }

    // Remove menu
    if (this.menu) {
      this.menu.remove()
      this.menu = null
    }

    // Remove submenu
    if (this.submenu) {
      this.submenu.remove()
      this.submenu = null
    }

    this.logger.debug('Moki control cleaned up')
  }
}
