import { LinguisticToken, SubtitleSegment } from '../subtitle'
import './sidebar.css'
import { t } from '../../i18n'
import type { YouTubePlayerMode } from '../youtube'

/**
 * SidebarView  DOM 
 * 
 */
export class SidebarView {
  private static instance: SidebarView | null = null
  private static readonly CONTAINER_ID = 'moki-sidebar-container'

  private container: HTMLDivElement | null = null
  private overlay: HTMLDivElement | null = null
  private currentParentContainer: HTMLElement | null = null
  private currentMode: YouTubePlayerMode = 'default'

  private constructor() {
    this.initDOM()
  }

  /**
   * 
   */
  static getInstance(): SidebarView {
    if (!SidebarView.instance) {
      SidebarView.instance = new SidebarView()
    }
    return SidebarView.instance
  }

  /**
   *  DOM 
   */
  private initDOM(): void {
    this.container = document.createElement('div')
    this.container.id = SidebarView.CONTAINER_ID
    this.container.className = 'moki-sidebar'
    this.container.innerHTML = `
      <div class="moki-sidebar-content">
        <div class="moki-sidebar-list" id="moki-sidebar-list">
          <!--  -->
        </div>
      </div>
      <div class="moki-sidebar-overlay"></div>
    `
    this.overlay = this.container.querySelector('.moki-sidebar-overlay')
  }

  /**
   * 
   * @param insertionPoint - 
   * @param referenceNode - 
   * @param mode - 
   */
  activate(
    insertionPoint: HTMLElement,
    referenceNode: Element | null,
    mode: YouTubePlayerMode = 'default',
  ): void {
    // 
    this.cleanupParentContainerStyles()

    //  container 
    if (this.container!.parentElement !== insertionPoint) {
      insertionPoint.insertBefore(this.container!, referenceNode)
    }

    // 
    this.currentParentContainer = insertionPoint
    this.currentMode = mode

    this.updateModeClass(mode)
    this.applyParentContainerStyles(mode)
    this.show()
  }

  /**
   * 
   * 
   * @param newContainer - 
   * @param referenceNode - 
   * @param mode - 
   */
  relocate(
    newContainer: HTMLElement,
    referenceNode: Element | null,
    mode: YouTubePlayerMode,
  ): void {
    if (!this.container) return

    // 
    this.cleanupParentContainerStyles()

    // 
    if (this.container.parentElement === newContainer) {
      this.currentParentContainer = newContainer
      this.currentMode = mode
      this.updateModeClass(mode)
      this.applyParentContainerStyles(mode)
      return
    }

    // 
    newContainer.insertBefore(this.container, referenceNode)
    this.currentParentContainer = newContainer
    this.currentMode = mode
    this.updateModeClass(mode)
    this.applyParentContainerStyles(mode)
  }

  /**
   *  CSS class 
   * @param mode - 
   */
  private updateModeClass(mode: YouTubePlayerMode): void {
    if (!this.container) return

    //  class
    this.container.classList.remove('mode-default', 'mode-theater', 'mode-fullscreen')
    //  class
    this.container.classList.add(`mode-${mode}`)

    // /
    if (mode === 'theater' || mode === 'fullscreen') {
      this.container.style.width = '350px'
      this.container.style.minWidth = '350px'
      this.container.style.minHeight = 'unset'
      this.container.style.alignSelf = 'stretch'
      this.container.style.borderRadius = '0'
      this.container.style.marginBottom = '0'
    } else {
      //  CSS 
      this.container.style.width = ''
      this.container.style.minWidth = ''
      this.container.style.minHeight = ''
      this.container.style.alignSelf = ''
      this.container.style.borderRadius = ''
      this.container.style.marginBottom = ''
    }
  }

  /**
   *  flexbox /
   *  CSS :has() 
   * @param mode - 
   */
  private applyParentContainerStyles(mode: YouTubePlayerMode): void {
    if (!this.currentParentContainer) return

    if (mode === 'theater' || mode === 'fullscreen') {
      //  flexbox 
      this.currentParentContainer.style.display = 'flex'
      this.currentParentContainer.style.flexDirection = 'row-reverse'

      //  flex 
      const playerContainer = this.currentParentContainer.querySelector(
        '#player-full-bleed-container',
      ) as HTMLElement
      if (playerContainer) {
        playerContainer.style.flex = '1'
        playerContainer.style.minWidth = '0'
      }

      //  resize  YouTube 
      // YouTube  .ytp-chrome-bottom 
      //  resize 
      //  requestAnimationFrame  DOM 
      requestAnimationFrame(() => {
        window.dispatchEvent(new Event('resize'))
      })
    }
  }

  /**
   *  flexbox 
   */
  private cleanupParentContainerStyles(): void {
    if (!this.currentParentContainer) return

    //  resize/
    const needsResize = this.currentMode === 'theater' || this.currentMode === 'fullscreen'

    // 
    this.currentParentContainer.style.display = ''
    this.currentParentContainer.style.flexDirection = ''

    // 
    const playerContainer = this.currentParentContainer.querySelector(
      '#player-full-bleed-container',
    ) as HTMLElement
    if (playerContainer) {
      playerContainer.style.flex = ''
      playerContainer.style.minWidth = ''
    }

    //  resize  YouTube 
    if (needsResize) {
      requestAnimationFrame(() => {
        window.dispatchEvent(new Event('resize'))
      })
    }
  }

  /**
   * 
   */
  showOverlay(): void {
    if (this.overlay) {
      this.overlay.classList.add('visible')
    }
  }

  /**
   * 
   */
  hideOverlay(): void {
    if (this.overlay) {
      this.overlay.classList.remove('visible')
    }
  }

  // Collected lemmas cache for highlighting
  private collectedLemmas: Set<string> = new Set()

  /**
   *  lemmas
   * @param lemmas -  lemma 
   */
  setCollectedLemmas(lemmas: Set<string>): void {
    this.collectedLemmas = lemmas
  }

  /**
   * 
   * @param segments - 
   */
  renderSubtitles(segments: SubtitleSegment[]): void {
    const listElement = this.container?.querySelector('#moki-sidebar-list')
    if (!listElement) {
      console.error('List element not found, cannot render subtitles')
      return
    }

    listElement.innerHTML = ''

    if (segments.length === 0) {
      this.renderEmptyState()
      return
    }

    segments.forEach((segment, index) => {
      const itemElement = document.createElement('div')
      itemElement.className = `moki-subtitle-item ${index % 2 === 0 ? 'even' : 'odd'}`
      itemElement.dataset.index = segment.index.toString()

      const iconContainer = document.createElement('span')
      iconContainer.className = 'moki-playing-icon'
      itemElement.appendChild(iconContainer)

      const contentContainer = document.createElement('span')
      contentContainer.className = 'moki-subtitle-content'
      this.renderTokens(contentContainer, segment)
      itemElement.appendChild(contentContainer)

      listElement.appendChild(itemElement)
    })
  }

  /**
   * 
   */
  private renderEmptyState(): void {
    const listElement = this.container?.querySelector('#moki-sidebar-list')
    if (!listElement) return

    listElement.innerHTML = `
      <div class="moki-subtitle-empty">
        <p>${t('sidebar_empty_noData')}</p>
        <p>${t('sidebar_empty_enableSubtitle')}</p>
      </div>
    `
  }

  /**
   *  segment  linguistic tokens  span 
   *  linguisticTokens NLP 
   */
  private renderTokens(container: HTMLElement, segment: SubtitleSegment): void {
    if (!segment.nativeContent) return

    const hasLinguisticTokens = segment.linguisticTokens && segment.linguisticTokens.length > 0
    if (!hasLinguisticTokens) {
      container.textContent = segment.nativeContent
      return
    }

    // 
    const languageSupportsRomanization = ['zh-CN', 'zh-TW', 'ja', 'ko'].includes(
      segment.targetLanguage || '',
    )

    try {
      //  linguisticTokens  token spans
      segment.linguisticTokens!.forEach((token, tokenIndex) => {
        if (!token || !token.text) return

        // 
        // Unicode  +  ASCII 
        const isPunctuation =
          /^[\p{P}\p{S}]+$/u.test(token.text) || /^[.,;:!?'"()\[\]{}<>\-\/\\]+$/.test(token.text)
        // 
        const isNumber = /^\d+$/u.test(token.text)

        //  token 
        if (!isPunctuation && tokenIndex > 0) {
          container.appendChild(document.createTextNode(' '))
        }

        const tokenSpan = this.createTokenSpan(
          token,
          tokenIndex,
          isPunctuation,
          isNumber,
          languageSupportsRomanization,
        )
        container.appendChild(tokenSpan)
      })

      // 
      const reconstructed = segment.linguisticTokens!.map((t) => t.text || '').join(' ')
      const nativeContent = segment.nativeContent.trim()
      if (nativeContent.length > reconstructed.length) {
        const suffix = nativeContent.substring(reconstructed.length)
        if (suffix) {
          container.appendChild(document.createTextNode(suffix))
        }
      }
    } catch (error) {
      // 
      console.error('Error rendering tokens, falling back to simple render', error)
      container.textContent = segment.nativeContent
    }
  }

  /**
   *  token span 
   */
  private createTokenSpan(
    token: LinguisticToken,
    tokenIndex: number,
    isPunctuation: boolean,
    isNumber: boolean,
    supportsRomanization: boolean,
  ): HTMLSpanElement {
    const tokenSpan = document.createElement('span')
    //  moki-token-punctuation class
    tokenSpan.className = isPunctuation || isNumber ? 'moki-token-punctuation' : 'moki-token'

    //  lemma
    if (!isPunctuation && !isNumber && token.lemma) {
      const isCollected = this.collectedLemmas.has(token.lemma.toLowerCase())
      if (isCollected) {
        tokenSpan.classList.add('collected')
      }
    }

    //  dataset
    if (!isPunctuation && !isNumber) {
      tokenSpan.dataset.word = token.text
      tokenSpan.dataset.tokenIndex = tokenIndex.toString()
      tokenSpan.dataset.start = token.start.toString()
      tokenSpan.dataset.end = token.end.toString()
    }

    // 
    if (supportsRomanization) {
      // CJK 
      tokenSpan.appendChild(this.createRomanizationWrapper(token, isPunctuation, isNumber))
    } else {
      //  CJK 
      tokenSpan.textContent = token.text
    }
    return tokenSpan
  }

  /**
   *  token 
   */
  private createRomanizationWrapper(
    token: LinguisticToken,
    isPunctuation: boolean,
    isNumber: boolean,
  ): HTMLSpanElement {
    const wrapper = document.createElement('span')
    wrapper.className = 'moki-token-with-roman'

    if (token.romanization && token.romanization.length > 0) {
      // 
      token.romanization.forEach(({ char, roman }) => {
        // 
        if (isPunctuation || isNumber) {
          // 
          wrapper.appendChild(this.createCharGroup(char, null))
        } else {
          // 
          wrapper.appendChild(this.createCharGroup(char, roman))
        }
      })
    } else {
      // 
      //  placeholder class 
      wrapper.classList.add('moki-roman-placeholder')
      for (const char of token.text) {
        wrapper.appendChild(this.createCharGroup(char, null))
      }
    }
    return wrapper
  }

  /**
   * 
   */
  private createCharGroup(char: string, roman: string | null): HTMLSpanElement {
    const charGroup = document.createElement('span')
    charGroup.className = 'moki-char-group'

    // - 
    const romanSpan = document.createElement('span')
    romanSpan.className = 'moki-roman'
    romanSpan.innerHTML = roman || '&nbsp;'

    // 
    const charSpan = document.createElement('span')
    charSpan.className = 'moki-char'
    charSpan.textContent = char

    charGroup.appendChild(romanSpan)
    charGroup.appendChild(charSpan)

    return charGroup
  }

  /**
   *  DOM 
   */
  getContainer(): HTMLDivElement | null {
    return this.container
  }

  /**
   * 
   */
  show(): void {
    if (this.container) {
      this.container.style.display = '' //  CSS 
      this.container.classList.remove('hidden')
      // 
      this.applyParentContainerStyles(this.currentMode)
    }
  }

  /**
   * 
   */
  hide(): void {
    if (this.container) {
      this.container.style.display = 'none'
      this.container.classList.add('hidden')
      // 
      this.cleanupParentContainerStyles()
    }
  }

  /**
   * 
   */
  isVisible(): boolean {
    return this.container ? this.container.style.display !== 'none' : false
  }

  /**
   * 
   *  sidebar 
   * @param segmentIndex - 
   */
  scrollToSegment(segmentIndex: number, currentPlayingIndex: number): void {
    if (!this.container) return

    this.updatePlayingIndicator(segmentIndex, currentPlayingIndex)

    // 
    // index 0  1
    const targetIndex = Math.max(0, segmentIndex - 2)
    const scrollContainer = this.container.querySelector('.moki-sidebar-content') as HTMLElement
    const targetElement = this.container.querySelector(
      `[data-index="${targetIndex}"]`,
    ) as HTMLElement

    if (!scrollContainer || !targetElement) return

    const containerRect = scrollContainer.getBoundingClientRect()
    const targetRect = targetElement.getBoundingClientRect()
    const scrollOffset = targetRect.top - containerRect.top + scrollContainer.scrollTop
    scrollContainer.scrollTop = scrollOffset
  }

  /**
   * 
   */
  private updatePlayingIndicator(segmentIndex: number, currentPlayingIndex: number): void {
    if (!this.container || currentPlayingIndex === segmentIndex) return

    if (currentPlayingIndex >= 0) {
      const prevElement = this.container.querySelector(
        `[data-index="${currentPlayingIndex}"]`,
      ) as HTMLElement
      if (prevElement) {
        prevElement.classList.remove('playing')
      }
    }

    const currentElement = this.container.querySelector(
      `[data-index="${segmentIndex}"]`,
    ) as HTMLElement
    if (currentElement) {
      currentElement.classList.add('playing')
    }
  }

  /**
   *  DOM 
   */
  destroy(): void {
    // 
    this.cleanupParentContainerStyles()

    if (this.container) {
      this.container.remove()
      this.container = null
      this.overlay = null
    }
    this.currentParentContainer = null
    this.currentMode = 'default'
    SidebarView.instance = null
  }
}
