import { Logger } from '../../tools/logger'
import { Phrase, Subtitle, SubtitleSegment, LinguisticToken, EntryDetails } from '../subtitle'
import { TooltipService } from './service'
import { TooltipStateMachine } from './state'
import { TooltipView } from './view'
import { ExpandedContentData } from './types'
import { t } from '../../i18n'
import { MediaCaptureService, CaptureResult } from '../services'
import { CollectionBridge } from '../../tools/collectionBridge'

/**
 * TooltipController - ""
 *  Service  View
 */
export class TooltipController {
  private logger = new Logger('[TooltipController]')
  private view: TooltipView
  private stateMachine: TooltipStateMachine
  private service: TooltipService
  private subtitle: Subtitle | null = null
  private nativeLng: string
  private escKeyHandler: ((e: KeyboardEvent) => void) | null = null
  private activeTokenElement: HTMLElement | null = null
  private currentTarget: { segmentIndex: string; word: string } = { segmentIndex: '', word: '' }
  private expandedContentData: ExpandedContentData | null = null
  private loadingWords: Set<string> = new Set() // 
  private loadingEntries: Set<string> = new Set() // 
  private mediaCaptureService = new MediaCaptureService() // 
  private showSidebarOverlay: (() => void) | null = null
  private hideSidebarOverlay: (() => void) | null = null
  private refreshHighlightsCallback: (() => void) | null = null

  constructor(nativeLng: string = 'zh-CN') {
    this.nativeLng = nativeLng
    this.view = TooltipView.getInstance()
    this.stateMachine = new TooltipStateMachine(new Logger('[TooltipState]'))
    this.service = new TooltipService()

    this.stateMachine.setOnHideCallback(() => {
      this.view.hide()
      this.clearActiveToken()
    })
  }

  create(subtitle: Subtitle | null): void {
    this.logger.debug(
      `TooltipController.create() called with subtitle: ${subtitle ? 'present' : 'null'}`,
    )
    this.subtitle = subtitle

    //  ESC 
    this.escKeyHandler = (e: KeyboardEvent) => {
      if (e.key === 'Escape' && this.stateMachine.getState() === 'expanded') {
        this.logger.debug('ESC key pressed, closing expanded tooltip')
        this.stateMachine.transition({ type: 'CLOSE_TOOLTIP' })
        e.preventDefault()
        e.stopPropagation()
      }
    }
    document.addEventListener('keydown', this.escKeyHandler)

    // 
    this.view.getElement()?.addEventListener('click', (e) => {
      const target = e.target as HTMLElement

      // 
      if (target.closest('.moki-tooltip-close')) {
        e.stopPropagation()
        this.logger.debug('Close button clicked')
        this.stateMachine.transition({ type: 'CLOSE_TOOLTIP' })
        return
      }

      // 
      const favoriteBtn = target.closest('[data-action="favorite"]')
      if (favoriteBtn) {
        e.stopPropagation()
        this.handleFavoriteClick()
        return
      }

      // 
      const pronounceBtn = target.closest('[data-action="pronounce"]')
      if (pronounceBtn) {
        e.stopPropagation()
        this.handlePronunciationClick()
        return
      }

      // 
      const posBtn = target.closest('.moki-tooltip-pos-switcher-btn') as HTMLButtonElement
      if (posBtn && !posBtn.disabled) {
        const pos = posBtn.dataset.pos
        if (pos) {
          this.handlePosSwitcherClick(pos)
        }
        return
      }
    })
  }

  /**
   *  tooltip 
   */
  destroy(): void {
    this.view.destroy()
    if (this.escKeyHandler) {
      document.removeEventListener('keydown', this.escKeyHandler)
    }
  }

  /**
   * 
   */
  setNativeLng(lng: string): void {
    this.nativeLng = lng
  }

  /**
   * 
   */
  setOverlayCallbacks(showOverlay: () => void, hideOverlay: () => void): void {
    this.showSidebarOverlay = showOverlay
    this.hideSidebarOverlay = hideOverlay
  }

  /**
   * 
   */
  setRefreshHighlightsCallback(callback: () => void): void {
    this.refreshHighlightsCallback = callback
  }

  /**
   *  Subtitle 
   */
  setSubtitle(subtitle: Subtitle | null): void {
    this.subtitle = subtitle
    this.logger.debug(`Subtitle reference ${subtitle ? 'updated' : 'cleared'}`)
  }

  /**
   *  hover 
   *  lookup  entry 
   */
  async handleWordHover(
    subtitleItem: HTMLElement,
    segment: SubtitleSegment,
    tokenElement: HTMLElement,
    word: string,
    segmentIndex: string,
  ): Promise<void> {
    //  hover
    const currentState = this.stateMachine.getState()
    if (
      currentState === 'expanded' ||
      (currentState === 'shortLocked' && this.stateMachine.getCurrentWord() === word)
    ) {
      return
    }

    this.markCurrentTarget(segmentIndex, word)
    const tokenData = this.collectTokenData(segment, tokenElement, word)
    const cachedDef = tokenData.linguisticToken?.definitionsCache?.[this.nativeLng]

    //  + 
    const loadingKey = `${word}:${this.nativeLng}`

    //  loading 
    if (this.loadingWords.has(loadingKey)) {
      this.logger.debug(
        `Word "${word}" is already loading, showing skeleton but skipping duplicate request`,
      )
      const newState = this.stateMachine.transition({
        type: 'HOVER_WORD',
        word,
        segment,
        segmentIndex,
      })
      if (newState === 'loading') {
        this.view.show(this.view.renderLoading(word))
        this.view.reposition(subtitleItem)
      }
      return
    }

    // 
    if (cachedDef) {
      //  short 
      this.stateMachine.transition({ type: 'HOVER_WORD', word, segment, segmentIndex })
      this.stateMachine.transition({ type: 'LOAD_SUCCESS', definitions: cachedDef })

      // 
      let phonetic: string | undefined = undefined
      const lemma = tokenData.linguisticToken?.lemma || word
      const lang_code = segment.targetLanguage || 'en'
      const tokenPos = tokenData.linguisticToken?.pos || undefined

      if (this.subtitle) {
        const cachedEntry = this.subtitle.getCachedEntry(lemma, lang_code, this.nativeLng)
        if (cachedEntry) {
          phonetic = cachedEntry.entries[0]?.sounds?.[0]?.ipa || undefined
          this.logger.debug(`Found phonetic from cache for "${word}": ${phonetic}`)
        } else {
          this.logger.debug(`No cached entry found for "${word}" (${lemma})`)
        }
      }

      this.logger.debug(`Rendering short card with phonetic: ${phonetic}, tokenPos: ${tokenPos}`)

      const content = this.view.renderShort({
        word,
        definitions: cachedDef,
        phrases: tokenData.phrases,
        translation: segment.targetContent,
        phonetic,
        lang_code: segment.targetLanguage || 'en',
        tokenPos,
      })
      this.view.show(content)
      this.view.reposition(subtitleItem)

      //  entry 
      this.preloadEntryData(
        tokenData.linguisticToken?.lemma || word,
        segment.targetLanguage || 'en',
      )
      return
    }

    //  loading 
    const newState = this.stateMachine.transition({
      type: 'HOVER_WORD',
      word,
      segment,
      segmentIndex,
    })
    if (newState !== 'loading') return

    // 
    this.loadingWords.add(loadingKey)
    this.logger.debug(`Added "${word}" to loading set`)

    //  loading
    this.view.show(this.view.renderLoading(word))
    this.view.reposition(subtitleItem)

    //  lookup  entry 
    const lemma = tokenData.linguisticToken?.lemma || word
    const lang_code = segment.targetLanguage || 'en'

    const [definition] = await Promise.all([
      this.service.lookupWordDefinition(
        word,
        segment.nativeContent,
        this.nativeLng,
        tokenData.linguisticToken,
      ),
      this.preloadEntryData(lemma, lang_code), //  entry 
    ])

    // 
    this.loadingWords.delete(loadingKey)
    this.logger.debug(`Removed "${word}" from loading set`)

    //  hover 
    if (this.currentTarget.segmentIndex !== segmentIndex || this.currentTarget.word !== word) {
      return
    }

    if (definition) {
      this.stateMachine.transition({ type: 'LOAD_SUCCESS', definitions: definition.definitions })
    } else {
      this.stateMachine.transition({ type: 'LOAD_ERROR' })
    }

    //  short 
    // entry 
    let phonetic: string | undefined = undefined
    const tokenPos = tokenData.linguisticToken?.pos || undefined

    if (this.subtitle) {
      const cachedEntry = this.subtitle.getCachedEntry(lemma, lang_code, this.nativeLng)
      if (cachedEntry) {
        phonetic = cachedEntry.entries[0]?.sounds?.[0]?.ipa || undefined
        this.logger.debug(`Found phonetic after preload for "${word}": ${phonetic}`)
      } else {
        this.logger.warn(`No cached entry after preload for "${word}" (${lemma})`)
      }
    }

    this.logger.debug(`Rendering short card with phonetic: ${phonetic}, tokenPos: ${tokenPos}`)

    const content = this.view.renderShort({
      word,
      definitions: definition?.definitions || '',
      phrases: tokenData.phrases,
      translation: segment.targetContent,
      isError: !definition,
      phonetic,
      lang_code: segment.targetLanguage || 'en',
      tokenPos,
    })
    this.view.show(content)
    this.view.reposition(subtitleItem)
  }

  /**
   *  hover 
   */
  handleNonWordHover(
    subtitleItem: HTMLElement,
    segment: SubtitleSegment,
    segmentIndex: string,
  ): void {
    if (this.stateMachine.getState() === 'expanded') return

    this.markCurrentTarget(segmentIndex, '')
    const newState = this.stateMachine.transition({ type: 'HOVER_NON_WORD', segment, segmentIndex })
    if (newState !== 'translation') return

    if (segment.targetContent) {
      this.view.show(this.view.renderTranslation(segment.targetContent))
      this.view.reposition(subtitleItem)
    } else {
      this.view.hide()
    }
  }

  /**
   *  token 
   */
  handleTokenClick(tokenElement: HTMLElement, word: string, segments: SubtitleSegment[]): void {
    const newState = this.stateMachine.transition({ type: 'CLICK_WORD', word })
    this.setActiveToken(tokenElement)
    // 
    switch (newState) {
      case 'expanded':
        this.expandTooltip(tokenElement, word, segments)
        break
      case 'shortLocked':
        this.collapseToShortMode(tokenElement, word, segments)
        break
    }
  }

  /**
   *  mouseout 
   */
  clearLockedWord(): void {
    this.stateMachine.transition({ type: 'MOUSE_OUT' })
  }

  /**
   *  tooltip
   */
  private markCurrentTarget(segmentIndex: string, word: string) {
    this.currentTarget = { segmentIndex, word }
  }

  /**
   *  token
   */
  private setActiveToken(tokenElement: HTMLElement): void {
    if (this.activeTokenElement) {
      this.activeTokenElement.classList.remove('active')
    }
    tokenElement.classList.add('active')
    this.activeTokenElement = tokenElement
  }

  /**
   *  token
   */
  private clearActiveToken(): void {
    if (this.activeTokenElement) {
      this.activeTokenElement.classList.remove('active')
      this.activeTokenElement = null
    }
  }

  /**
   *  token linguisticToken
   */
  private collectTokenData(
    segment: SubtitleSegment,
    tokenElement: HTMLElement,
    word: string,
  ): { start: number; end: number; linguisticToken?: LinguisticToken; phrases: Phrase[] } {
    const start = parseInt(tokenElement.dataset.start || '0', 10)
    const end = parseInt(tokenElement.dataset.end || '0', 10)
    const linguisticToken = segment.linguisticTokens?.find(
      (token) => token.text === word && token.start === start && token.end === end,
    )
    const phrases = this.findPhrasesForToken(segment, word, start, end)
    return { start, end, linguisticToken, phrases }
  }

  /**
   *  token 
   */
  private findPhrasesForToken(
    segment: SubtitleSegment,
    _tokenText: string,
    tokenStart: number,
    tokenEnd: number,
  ): Phrase[] {
    if (!segment.phrases || segment.phrases.length === 0) return []
    return segment.phrases.filter((phrase) =>
      phrase.positions.some(
        (pos: { start: number; end: number }) =>
          tokenStart < pos.end + 1 && pos.start < tokenEnd + 1,
      ),
    )
  }

  /**
   *  entry  UI
   *  API 
   */
  private async preloadEntryData(lemma: string, lang_code: string): Promise<void> {
    if (!this.subtitle) {
      this.logger.debug('Subtitle is null, skipping entry preload')
      return
    }

    const cacheKey = `${lemma}:${lang_code}:${this.nativeLng}`

    // 
    const cachedResponse = this.subtitle.getCachedEntry(lemma, lang_code, this.nativeLng)
    if (cachedResponse !== undefined) {
      this.logger.debug(`Entry already cached for "${lemma}" (${cacheKey}), skipping preload`)
      return
    }

    // 
    if (this.loadingEntries.has(cacheKey)) {
      this.logger.debug(
        `Entry already loading for "${lemma}" (${cacheKey}), skipping duplicate request`,
      )
      return
    }

    // 
    this.loadingEntries.add(cacheKey)
    this.logger.info(` Preloading entry for "${lemma}" (${cacheKey})`)

    try {
      const result = await this.service.lookupEntryDetails(lemma, lang_code, this.nativeLng)
      this.subtitle.setCachedEntry(lemma, lang_code, this.nativeLng, result.fullResponse)
      this.logger.info(` Preloaded and cached entry for "${lemma}" (${cacheKey})`)
    } catch (error) {
      this.logger.error(`Failed to preload entry for "${lemma}":`, error)
    } finally {
      // 
      this.loadingEntries.delete(cacheKey)
      this.logger.debug(`Removed entry "${lemma}" from loading set`)
    }
  }

  /**
   *  tooltip 
   * @param isUpdate - 
   */
  private async expandTooltip(
    tokenElement: HTMLElement,
    word: string,
    segments: SubtitleSegment[],
    isUpdate: boolean = false,
  ): Promise<void> {
    const subtitleItem = tokenElement.closest('.moki-subtitle-item') as HTMLElement
    if (!subtitleItem) return

    const segmentIndex = subtitleItem.dataset.index || ''
    const segment = segments.find((s) => s.index === parseInt(segmentIndex))
    if (!segment) return

    this.markCurrentTarget(segmentIndex, word)

    // 
    if (!isUpdate) {
      const video = document.querySelector('video')
      if (video && !video.paused) video.pause()
    }

    this.view.applyStateClasses('expanded')

    const tokenData = this.collectTokenData(segment, tokenElement, word)
    let cachedDefinition = tokenData.linguisticToken?.definitionsCache?.[this.nativeLng]
    const phrases = tokenData.phrases
    const lang_code = segment.targetLanguage || 'en'
    const lemma = tokenData.linguisticToken?.lemma || word

    //  expanded loading 
    if (!cachedDefinition) {
      const loadingContent = this.view.renderExpandedLoading(word)
      this.view.show(loadingContent)
      this.view.reposition(subtitleItem)

      const definition = await this.service.lookupWordDefinition(
        word,
        segment.nativeContent,
        this.nativeLng,
        tokenData.linguisticToken,
      )
      cachedDefinition = definition?.definitions || t('tooltip_error_loadingDefinition')
    }

    //  entry  hover 
    let entryDetails: EntryDetails | null = null
    let fullEntryResponse: import('../../tools/client').EntryLookupResponse | null = null

    if (this.subtitle) {
      const cacheKey = `${lemma}:${lang_code}:${this.nativeLng}`
      const cachedResponse = this.subtitle.getCachedEntry(lemma, lang_code, this.nativeLng)

      if (cachedResponse !== undefined) {
        this.logger.info(` Entry cache HIT for "${lemma}" (${cacheKey}) [expand]`)
        if (cachedResponse) {
          entryDetails = this.extractEntryDetails(cachedResponse)
          fullEntryResponse = cachedResponse
        }
      } else {
        //  hover 
        this.logger.warn(` Entry cache MISS for "${lemma}" (${cacheKey}) [expand - unexpected]`)
        const result = await this.service.lookupEntryDetails(lemma, lang_code, this.nativeLng)
        entryDetails = result.details
        fullEntryResponse = result.fullResponse
        this.subtitle.setCachedEntry(lemma, lang_code, this.nativeLng, fullEntryResponse)
      }
    } else {
      this.logger.warn('Subtitle instance is null, calling API directly')
      const result = await this.service.lookupEntryDetails(lemma, lang_code, this.nativeLng)
      entryDetails = result.details
      fullEntryResponse = result.fullResponse
    }

    //  hover 
    if (this.currentTarget.segmentIndex !== segmentIndex || this.currentTarget.word !== word) return

    // 
    //  Background 
    let isFavorited = false
    const videoViewId = this.subtitle?.getVideoViewId()
    if (videoViewId) {
      isFavorited = await CollectionBridge.isWordCollected(videoViewId, word, segment.begin)
    }

    this.expandedContentData = {
      word,
      phonetic: entryDetails?.phonetic ?? null,
      posSenses: entryDetails?.posSenses ?? [],
      examples: entryDetails?.examples ?? [],
      cachedDefinition: cachedDefinition || '',
      phrases,
      translation: segment.targetContent,
      lang_code,
      tokenPos: tokenData.linguisticToken?.pos, // Token 
      lemma, // 
      isFavorited, // 
      entryResponse: fullEntryResponse ?? undefined, //  API  null
    }

    const expandedContent = this.view.renderExpanded(this.expandedContentData)

    this.view.show(expandedContent)
    this.view.reposition(subtitleItem)
  }

  /**
   *  tooltip
   */
  private collapseToShortMode(
    tokenElement: HTMLElement,
    word: string,
    segments: SubtitleSegment[],
  ): void {
    const subtitleItem = tokenElement.closest('.moki-subtitle-item') as HTMLElement
    const segmentIndex = subtitleItem?.dataset.index || ''
    const segment = segments.find((s) => s.index === parseInt(segmentIndex))
    if (!segment) {
      this.view.hide()
      return
    }

    this.view.applyStateClasses('shortLocked')
    const tokenData = this.collectTokenData(segment, tokenElement, word)
    const cachedDefinition = tokenData.linguisticToken?.definitionsCache?.[this.nativeLng] || ''

    //  hover 
    const tokenPos = tokenData.linguisticToken?.pos || undefined
    let phonetic: string | undefined = undefined
    const lemma = tokenData.linguisticToken?.lemma || word
    const lang_code = segment.targetLanguage || 'en'

    if (this.subtitle) {
      const cachedEntry = this.subtitle.getCachedEntry(lemma, lang_code, this.nativeLng)
      if (cachedEntry) {
        phonetic = cachedEntry.entries[0]?.sounds?.[0]?.ipa || undefined
      }
    }

    const content = this.view.renderShort({
      word,
      definitions: cachedDefinition,
      phrases: tokenData.phrases,
      translation: segment.targetContent,
      phonetic,
      lang_code,
      tokenPos,
    })

    this.view.show(content)
    if (subtitleItem) this.view.reposition(subtitleItem)
  }

  /**
   * 
   */
  private handlePosSwitcherClick(pos: string): void {
    const currentState = this.stateMachine.getState()
    if (currentState !== 'expanded') return

    //  selectedPos
    if (this.expandedContentData) {
      this.expandedContentData.selectedPos = pos
      // 
      const content = this.view.renderExpanded(this.expandedContentData)
      this.view.updateContent(content)
      this.logger.debug(`POS switched to: ${pos}`)
    }
  }

  /**
   *  - 
   */
  private async handleFavoriteClick(): Promise<void> {
    if (!this.expandedContentData) {
      this.logger.warn('handleFavoriteClick: expandedContentData is null')
      return
    }

    // segment
    if (!this.subtitle) {
      this.logger.error('handleFavoriteClick: subtitle is null')
      return
    }

    const segmentIndex = this.currentTarget.segmentIndex
    const segment = this.subtitle.getSegments().find((s) => s.index === parseInt(segmentIndex))
    if (!segment) {
      this.logger.error(`handleFavoriteClick: segment not found for index ${segmentIndex}`)
      return
    }

    const word = this.expandedContentData.word
    const videoViewId = this.subtitle.getVideoViewId()

    //  Background 
    const isCollected = videoViewId
      ? await CollectionBridge.isWordCollected(videoViewId, word, segment.begin)
      : false

    if (isCollected) {
      // 
      await this.handleUnfavorite(word, segment.begin)
    } else {
      // 
      await this.handleFavorite(segment)
    }
  }

  /**
   * 
   */
  private async handleUnfavorite(word: string, segmentBegin: number): Promise<void> {
    if (!this.subtitle) return

    const videoViewId = this.subtitle.getVideoViewId()
    if (!videoViewId) {
      this.logger.error('handleUnfavorite: videoViewId not found')
      return
    }

    //  Background  collectionId
    const collectionId = await CollectionBridge.getCollectionId(videoViewId, word, segmentBegin)
    if (!collectionId) {
      this.logger.error('handleUnfavorite: collectionId not found')
      return
    }

    try {
      // 
      this.view.updateFavoriteButtonState('unfavoriting')
      this.logger.info(`: word=${word}, collectionId=${collectionId}`)

      //  Background API +  + 
      await CollectionBridge.removeCollection(collectionId, videoViewId)
      this.logger.info(`Collection deleted successfully: id=${collectionId}`)

      //  expandedContentData  isFavorited 
      if (this.expandedContentData) {
        this.expandedContentData.isFavorited = false
      }

      //  sidebar 
      try {
        this.logger.debug('Refreshing sidebar highlights...')
        this.refreshHighlightsCallback?.()
        this.logger.debug('Sidebar highlights refreshed successfully')
      } catch (refreshError) {
        this.logger.error('Failed to refresh sidebar highlights:', refreshError)
      }

      //  API 
      this.logger.debug('Updating button state to idle')
      this.view.updateFavoriteButtonState('idle')
      this.logger.info('')
    } catch (error) {
      this.logger.error(':', error)
      // 
      this.view.updateFavoriteButtonState('active')
    }
  }

  /**
   *  - 
   */
  private async handleFavorite(segment: import('../subtitle').SubtitleSegment): Promise<void> {
    if (!this.expandedContentData || !this.subtitle) return

    // 
    const videoElement = document.querySelector('video')
    if (!videoElement) {
      this.logger.error('handleFavorite: video element not found')
      return
    }

    try {
      // loading
      this.logger.info(
        `... segment: ${segment.index}, begin: ${segment.begin}ms, end: ${segment.end}ms`,
      )
      this.view.updateFavoriteButtonState('loading')
      this.showSidebarOverlay?.()

      // 
      const captureResult: CaptureResult = await this.mediaCaptureService.captureMediaForSubtitle(
        videoElement,
        segment.begin,
        segment.end,
      )
      this.logger.debug('')

      //  audioBlob  base64
      const audioBase64 = await this.blobToBase64(captureResult.audioBlob)

      //  video_view_id
      const videoViewId = this.subtitle.getVideoViewId()
      if (!videoViewId) {
        throw new Error(' video_view_id')
      }

      //  API 
      const lang_code = this.expandedContentData.lang_code || 'en'
      const audioDurationMs = segment.end - segment.begin // 

      const createRequest = {
        // Video view reference
        video_view_id: videoViewId,
        // Vocabulary information
        word: this.expandedContentData.word,
        sentence: segment.nativeContent,
        translation: segment.targetContent || '',
        definitions: this.expandedContentData.cachedDefinition || '',
        entry_key: this.getEntryKey(this.expandedContentData.word, lang_code) || '',
        phonetic: this.expandedContentData.phonetic || null,
        pos: this.expandedContentData.tokenPos || null,
        lemma: this.expandedContentData.lemma || null,
        // Media resources
        audio_base64: audioBase64,
        image_base64: captureResult.imageBase64,
        audio_duration: audioDurationMs,
        // Video context
        segment_begin: segment.begin,
        segment_end: segment.end,
        source_language: segment.targetLanguage || 'en',
        target_language: this.nativeLng,
        phrases: segment.phrases || null,
      }

      //  Background Service API +  + 
      this.logger.debug('Creating collection via CollectionBridge...')
      const collection = await CollectionBridge.createCollection(createRequest)
      this.logger.info(`Collection created successfully: id=${collection.id}`)

      //  expandedContentData  isFavorited 
      this.expandedContentData.isFavorited = true

      //  sidebar 
      this.refreshHighlightsCallback?.()

      // 
      this.view.updateFavoriteButtonState('active')
      this.hideSidebarOverlay?.()

      this.logger.debug(
        `:  ${(captureResult.audioBlob.size / 1024).toFixed(1)}KB,  ${(captureResult.imageBase64.length / 1024).toFixed(1)}KB`,
      )
    } catch (error) {
      this.logger.error(':', error)
      // 
      this.view.updateFavoriteButtonState('error')
      this.hideSidebarOverlay?.()
      setTimeout(() => {
        this.view.updateFavoriteButtonState('idle')
      }, 2000)
    }
  }

  /**
   *  Blob  base64 
   */
  private async blobToBase64(blob: Blob): Promise<string> {
    return new Promise((resolve, reject) => {
      const reader = new FileReader()
      reader.onloadend = () => {
        const result = reader.result as string
        resolve(result)
      }
      reader.onerror = reject
      reader.readAsDataURL(blob)
    })
  }

  /**
   *  entry_key
   * : word:lang_code:native_lang_code:pos:source
   *
   * TODO:  entry_key  expandedContentData 
   *        entry_key  tooltip  entry
   */
  private getEntryKey(word: string, lang_code: string): string | null {
    const entryResponse = this.expandedContentData?.entryResponse
    if (entryResponse?.entries?.length) {
      const selectedPos = this.expandedContentData?.selectedPos
      //  entry
      if (selectedPos) {
        const selectedEntry = entryResponse.entries.find((e) => e.pos === selectedPos)
        if (selectedEntry) {
          return selectedEntry.entry_key
        }
      }
      //  entry  entry_key
      return entryResponse.entries[0].entry_key
    }

    //  entry_key null
    this.logger.warn(`No entry found for word: ${word} (lang: ${lang_code}), using null entry_key`)
    return null
  }

  /**
   * 
   */
  private handlePronunciationClick(): void {
    if (!this.expandedContentData) return

    this.logger.debug(`Play pronunciation for "${this.expandedContentData.word}"`)

    // Extract audio URL from entryResponse
    const audioUrl = this.getAudioUrl()
    if (!audioUrl) {
      this.logger.warn('No audio URL available for pronunciation')
      return
    }

    // Play audio
    try {
      const audio = new Audio(audioUrl)
      audio.play().catch((error) => {
        this.logger.error(`Failed to play pronunciation: ${error}`)
      })
    } catch (error) {
      this.logger.error(`Error creating audio: ${error}`)
    }
  }

  /**
   * Extract audio URL from entry response
   * Priority: mp3_url > audio > ogg_url
   */
  private getAudioUrl(): string | null {
    if (!this.expandedContentData?.entryResponse) return null

    const entries = this.expandedContentData.entryResponse.entries
    if (!entries || entries.length === 0) return null

    const firstEntry = entries[0]
    if (!firstEntry?.sounds || firstEntry.sounds.length === 0) return null

    const sound = firstEntry.sounds[0]
    return sound.mp3_url || sound.audio || sound.ogg_url || null
  }

  /**
   *  EntryLookupResponse  EntryDetails
   */
  private extractEntryDetails(
    entryData: import('../../tools/client').EntryLookupResponse,
  ): EntryDetails {
    const phonetic = entryData.entries[0]?.sounds?.[0]?.ipa || null
    const posSenses: Array<{ pos: string; senses: Array<string[]> }> = []
    const allExamples: Array<{ text: string; translation: string | null }> = []

    for (const entry of entryData.entries) {
      const senses: Array<string[]> = []
      for (const sense of entry.senses) {
        const glosses = sense.glosses.map((g) => g.text)
        if (glosses.length > 0) {
          senses.push(glosses)
        }
        // Collect examples from glosses (max 3 total)
        if (allExamples.length < 3) {
          for (const gloss of sense.glosses) {
            if (allExamples.length >= 3) break
            if (gloss.example && gloss.example.text) {
              allExamples.push({
                text: gloss.example.text,
                translation: gloss.example.translation,
              })
            }
          }
        }
      }
      if (senses.length > 0) {
        posSenses.push({ pos: entry.pos, senses })
      }
    }

    return { phonetic, posSenses, examples: allExamples }
  }
}
