import { Logger } from '../../tools/logger'
import { TooltipEvent, TooltipState } from './types'

/**
 *  Tooltip 
 */
export class TooltipStateMachine {
  private state: TooltipState = 'hidden'
  private currentWord: string | null = null
  private logger: Logger
  private onHideCallback: (() => void) | null = null

  constructor(logger: Logger) {
    this.logger = logger
  }

  /**
   * 
   */
  setOnHideCallback(callback: () => void): void {
    this.onHideCallback = callback
  }

  /**
   * 
   * @param event 
   * @returns 
   */
  transition(event: TooltipEvent): TooltipState {
    const prevState = this.state

    this.logger.info(
      'This state: ' + this.state + ' Event: ' + event.type + ' Current Word: ' + this.currentWord,
    )

    switch (this.state) {
      case 'hidden':
        if (event.type === 'HOVER_WORD') {
          this.state = 'loading'
          this.currentWord = event.word
        } else if (event.type === 'HOVER_NON_WORD') {
          this.state = 'translation'
          this.currentWord = null
        } else if (event.type === 'CLICK_WORD') {
          //  hover 
          this.state = 'expanded'
          this.currentWord = event.word
        }
        break

      case 'loading':
        if (event.type === 'LOAD_SUCCESS') {
          this.state = 'shortHover'
        } else if (event.type === 'LOAD_ERROR') {
          this.state = 'shortHover' // 
        } else if (event.type === 'MOUSE_OUT') {
          this.state = 'hidden'
          this.currentWord = null
        } else if (event.type === 'HOVER_WORD' && event.word !== this.currentWord) {
          // 
          this.state = 'loading'
          this.currentWord = event.word
        } else if (event.type === 'HOVER_NON_WORD') {
          // hover   
          this.state = 'translation'
          this.currentWord = null
        } else if (event.type === 'CLICK_WORD') {
          //    "Loading..."
          this.state = 'expanded'
          this.currentWord = event.word
        }
        break

      case 'shortHover':
        if (event.type === 'CLICK_WORD') {
          // 
          this.state = 'expanded'
          this.currentWord = event.word
        } else if (event.type === 'MOUSE_OUT') {
          this.state = 'hidden'
          this.currentWord = null
        } else if (event.type === 'HOVER_WORD' && event.word !== this.currentWord) {
          this.state = 'loading'
          this.currentWord = event.word
        } else if (event.type === 'HOVER_NON_WORD') {
          // hover   
          this.state = 'translation'
          this.currentWord = null
        }
        break

      case 'expanded':
        if (event.type === 'CLICK_WORD') {
          if (event.word === this.currentWord) {
            //   
            this.state = 'shortLocked'
          } else {
            //    expanded 
            this.state = 'expanded'
            this.currentWord = event.word
          }
        } else if (event.type === 'CLOSE_TOOLTIP') {
          //  ESC 
          this.state = 'hidden'
          this.currentWord = null
        }
        //  HOVER_WORDHOVER_NON_WORDMOUSE_OUT
        break

      case 'shortLocked':
        if (event.type === 'MOUSE_OUT') {
          this.state = 'hidden'
          this.currentWord = null
        } else if (event.type === 'HOVER_WORD' && event.word !== this.currentWord) {
          this.state = 'loading'
          this.currentWord = event.word
        } else if (event.type === 'HOVER_NON_WORD') {
          // hover   
          this.state = 'translation'
          this.currentWord = null
        } else if (event.type === 'CLICK_WORD') {
          // 
          this.state = 'expanded'
          this.currentWord = event.word
        }
        break

      case 'translation':
        if (event.type === 'MOUSE_OUT') {
          this.state = 'hidden'
          this.currentWord = null
        } else if (event.type === 'HOVER_WORD') {
          this.state = 'loading'
          this.currentWord = event.word
        } else if (event.type === 'HOVER_NON_WORD') {
          // hover   
          this.state = 'translation'
          this.currentWord = null
        } else if (event.type === 'CLICK_WORD') {
          //   
          this.state = 'expanded'
          this.currentWord = event.word
        }
        break
    }

    if (prevState !== this.state) {
      this.logger.debug(
        `State transition: ${prevState} -> ${this.state} (word: ${this.currentWord})`,
      )

      //  hidden
      if (this.state === 'hidden' && this.onHideCallback) {
        this.logger.debug('Calling onHide callback')
        this.onHideCallback()
      }
    }

    return this.state
  }

  /**
   * 
   */
  getState(): TooltipState {
    return this.state
  }

  /**
   * 
   */
  getCurrentWord(): string | null {
    return this.currentWord
  }
}
