import { Definition, EntryLookupResponse } from '../../tools/client'
import { callAPI } from '../../tools/clientBridge'
import { Logger } from '../../tools/logger'
import { EntryDetails, LinguisticToken } from '../subtitle'

/**
 * TooltipService 
 * Controller
 */
export class TooltipService {
  private logger = new Logger('[TooltipService]')

  /**
   *  API
   */
  async lookupWordDefinition(
    word: string,
    context: string,
    nativeLng: string,
    linguisticToken?: LinguisticToken,
  ): Promise<{ word: string; definitions: string } | null> {
    try {
      this.logger.debug(`Looking up word "${word}" in context "${context}"`)

      const definitions = await callAPI<Definition[]>('lookupWord', {
        word,
        context,
        native_lng: nativeLng,
      })

      const result = this.formatDefinitionResponse(word, definitions)

      //  linguisticToken
      if (result && linguisticToken) {
        if (!linguisticToken.definitionsCache) {
          linguisticToken.definitionsCache = {}
        }
        linguisticToken.definitionsCache[nativeLng] = result.definitions
        this.logger.debug(`Cached definition for word "${word}" (language: ${nativeLng})`)
      }

      return result
    } catch (error) {
      this.logger.error(`Failed to lookup word "${word}":`, error)
      return null
    }
  }

  /**
   *  POS  senses  examples
   *  API  EntryDetails
   * 
   */
  async lookupEntryDetails(
    word: string,
    lang_code: string,
    native_lang_code: string,
  ): Promise<{
    details: EntryDetails | null
    fullResponse: import('../../tools/client').EntryLookupResponse | null
  }> {
    try {
      const entryData = await callAPI<EntryLookupResponse | null>(
        'lookupEntry',
        word,
        lang_code,
        native_lang_code,
      )
      if (!entryData) {
        return { details: null, fullResponse: null }
      }

      //  EntryDetails
      const details = this.extractEntryDetails(entryData)

      return { details, fullResponse: entryData }
    } catch (error) {
      this.logger.error(`Failed to lookup entry details for "${word}":`, error)
      return { details: null, fullResponse: null }
    }
  }

  /**
   *  EntryLookupResponse  EntryDetails
   */
  private extractEntryDetails(
    entryData: import('../../tools/client').EntryLookupResponse,
  ): EntryDetails {
    const phonetic = entryData.entries[0]?.sounds?.[0]?.ipa || null
    const posSenses: Array<{ pos: string; senses: Array<string[]> }> = []
    const allExamples: Array<{ text: string; translation: string | null }> = []

    for (const entry of entryData.entries) {
      const senses: Array<string[]> = []
      for (const sense of entry.senses) {
        const glosses = sense.glosses.map((g) => g.text)
        if (glosses.length > 0) {
          senses.push(glosses)
        }
        // Collect examples from glosses (max 3 total)
        if (allExamples.length < 3) {
          for (const gloss of sense.glosses) {
            if (allExamples.length >= 3) break
            if (gloss.example && gloss.example.text) {
              allExamples.push({
                text: gloss.example.text,
                translation: gloss.example.translation,
              })
            }
          }
        }
      }
      if (senses.length > 0) {
        posSenses.push({ pos: entry.pos, senses })
      }
    }

    return { phonetic, posSenses, examples: allExamples }
  }

  /**
   *  API  tooltip 
   */
  private formatDefinitionResponse(
    word: string,
    definitions: Definition[],
  ): { word: string; definitions: string } {
    const definitionsStr = definitions.map((def) => def.meaning).join('; ')
    return {
      word,
      definitions: definitionsStr,
    }
  }
}
