import { Phrase } from '../subtitle'
import { TooltipState, ShortContentData, ExpandedContentData } from './types'
import { TooltipIcons } from './icons'
import { t } from '../../i18n'
import './tooltip.css'

/**
 * TooltipView  Tooltip  DOM 
 * 
 */
export class TooltipView {
  private static instance: TooltipView | null = null
  private static readonly ELEMENT_ID = 'moki-tooltip-container'

  private element: HTMLDivElement | null = null
  private readonly stateClasses: TooltipState[] = ['loading', 'expanded']

  private constructor() {
    this.initDOM()
  }

  /**
   * 
   */
  static getInstance(): TooltipView {
    if (!TooltipView.instance) {
      TooltipView.instance = new TooltipView()
    }
    return TooltipView.instance
  }

  /**
   *  DOM 
   */
  private initDOM(): void {
    this.element = document.createElement('div')
    this.element.id = TooltipView.ELEMENT_ID
    this.element.className = 'moki-tooltip'
    this.element.style.display = 'none'
    document.body.appendChild(this.element)
  }

  /**
   *  tooltip 
   */
  destroy(): void {
    if (this.element) {
      this.element.remove()
      this.element = null
    }
    TooltipView.instance = null
  }

  /**
   *  tooltip 
   * @param content -  HTML 
   */
  show(content: string): void {
    if (!this.element) return
    this.element.innerHTML = content
    this.element.style.display = 'block'
  }

  /**
   *  tooltip 
   * @param content -  HTML 
   */
  updateContent(content: string): void {
    if (!this.element) return
    this.element.innerHTML = content
  }

  /**
   *  tooltip
   */
  hide(): void {
    if (!this.element) return
    this.element.style.display = 'none'
  }

  /**
   *  tooltip
   *  max-height  tooltip 
   * @param targetElement - tooltip 
   */
  reposition(targetElement: HTMLElement): void {
    if (!this.element) return

    const targetRect = targetElement.getBoundingClientRect()
    const padding = 10

    //  max-height padding
    const maxHeight = window.innerHeight - padding * 2
    this.element.style.maxHeight = `${maxHeight}px`

    //  max-height 
    const tooltipRect = this.element.getBoundingClientRect()

    //  12px 
    const left = targetRect.left - tooltipRect.width - 12

    // 
    let top = targetRect.top + targetRect.height / 2 - tooltipRect.height / 2

    // 
    if (top < padding) {
      top = padding
    }

    // 
    const maxTop = window.innerHeight - tooltipRect.height - padding
    if (top > maxTop) {
      top = maxTop
    }

    this.element.style.left = `${left}px`
    this.element.style.top = `${top}px`
  }

  /**
   *  CSS class
   * @param state -  TooltipState
   */
  applyStateClasses(state: TooltipState): void {
    if (!this.element) return
    this.stateClasses.forEach((className) => this.element!.classList.remove(className))
    if (this.stateClasses.includes(state)) {
      this.element.classList.add(state)
    }
  }

  /**
   *  tooltip  DOM 
   */
  getElement(): HTMLDivElement | null {
    return this.element
  }

  // --- Rendering Methods ---

  /**
   * 
   * @param labelKey - i18n 
   * @returns HTML 
   */
  private renderDividerWithLabel(labelKey: string): string {
    return `
      <div class="moki-tooltip-section-label">${t(labelKey)}</div>
      <div class="moki-tooltip-divider"></div>
    `
  }

  /**
   *  POS 
   * 
   * @param pos - 
   * @param entryResponse - 
   * @returns 
   */
  /**
   * Check if entry response has audio URL
   * @param entryResponse - Entry response from API
   * @returns true if any sound has audio URL
   */
  private hasAudioUrl(entryResponse?: import('../../tools/client').EntryLookupResponse): boolean {
    if (!entryResponse?.entries) return false

    const firstEntry = entryResponse.entries[0]
    if (!firstEntry?.sounds || firstEntry.sounds.length === 0) return false

    const sound = firstEntry.sounds[0]
    return !!(sound.mp3_url || sound.audio || sound.ogg_url)
  }

  private calculatePosContentScore(
    pos: string,
    entryResponse?: import('../../tools/client').EntryLookupResponse,
  ): number {
    if (!entryResponse) return 0

    const entry = entryResponse.entries.find((e) => e.pos === pos)
    if (!entry) return 0

    let score = 0

    //  senses
    for (const sense of entry.senses) {
      //  gloss  1 
      score += sense.glosses.length

      //  2 
      for (const gloss of sense.glosses) {
        if (gloss.example) {
          score += 2
        }
      }
    }

    return score
  }

  renderShort(data: ShortContentData): string {
    let content = ''

    // 
    content += `<div class="moki-tooltip-word-section">`

    // 
    content += `<div class="moki-tooltip-word">${data.word}</div>`

    // Short 
    if (data.phonetic) {
      const formattedPhonetic = this.formatPhonetic(data.phonetic, data.lang_code)
      content += `
        <div class="moki-tooltip-phonetic-container">
          <div class="moki-tooltip-phonetic">${formattedPhonetic}</div>
        </div>
      `
    }

    //  + 
    if (data.isError) {
      content += `<div class="moki-tooltip-definition" style="color: #ef4444;">${t('tooltip_error_loadingDefinition')}</div>`
    } else {
      //  tokenPos NLP
      const posLabel = data.tokenPos ? data.tokenPos.toLowerCase() : ''

      content += `
        <div class="moki-tooltip-definition-container">
          ${posLabel ? `<div class="moki-tooltip-pos-label">${posLabel}</div>` : ''}
          <div class="moki-tooltip-definition">${data.definitions}</div>
        </div>
      `
    }

    content += `</div>` //  word-section

    // 
    if (data.phrases.length > 0) {
      content += this.renderDividerWithLabel('tooltip_section_phrases')
      content += this.renderPhrases(data.phrases)
    }

    // 
    if (data.translation) {
      content += this.renderDividerWithLabel('tooltip_section_sentenceTranslation')
      content += `<div class="moki-tooltip-translation">${data.translation}</div>`
    }

    return content
  }

  renderExpanded(data: ExpandedContentData): string {
    //  posSenses
    const sortedPosSenses = [...data.posSenses].sort((a, b) => {
      const scoreA = this.calculatePosContentScore(a.pos, data.entryResponse)
      const scoreB = this.calculatePosContentScore(b.pos, data.entryResponse)
      return scoreB - scoreA // 
    })

    const selectedPos =
      data.selectedPos || (sortedPosSenses.length > 0 ? sortedPosSenses[0].pos : '')
    const currentPosSense =
      sortedPosSenses.find((ps) => ps.pos === selectedPos) || sortedPosSenses[0]

    let html = ''

    // 
    if (data.showLoadingSpinner) {
      html += '<div class="moki-tooltip-loading"></div>'
    } else {
      html += this.renderCloseButton()
      html += this.renderFavoriteButton(data.isFavorited || false)
    }

    // 
    html += `<div class="moki-tooltip-word">${data.word}</div>`

    // 
    if (data.phonetic) {
      const formattedPhonetic = this.formatPhonetic(data.phonetic, data.lang_code)
      const hasAudio = this.hasAudioUrl(data.entryResponse)
      html += `
        <div class="moki-tooltip-phonetic-container">
          <div class="moki-tooltip-phonetic">${formattedPhonetic}</div>
          ${hasAudio ? this.renderPronunciationButton() : ''}
        </div>
      `
    }

    // 
    if (data.cachedDefinition) {
      const posLabel = data.tokenPos ? data.tokenPos.toLowerCase() : '' //  token 
      html += `
        <div class="moki-tooltip-definition-container">
          ${posLabel ? `<div class="moki-tooltip-pos-label">${posLabel}</div>` : ''}
          <div class="moki-tooltip-definition">${data.cachedDefinition}</div>
        </div>
      `
    }

    //  posSenses
    html += this.renderPosSwitcher(sortedPosSenses, selectedPos)

    // 
    html += this.renderDividerWithLabel('tooltip_section_definition')

    // 
    if (currentPosSense) {
      html += this.renderSenseList(selectedPos, data.entryResponse)
    } else if (
      data.posSenses.length === 0 &&
      data.examples.length === 0 &&
      !data.showLoadingSpinner
    ) {
      html += `<div class="moki-tooltip-not-included">${t('tooltip_notIncluded')}</div>`
    }

    // 
    const isLoading =
      data.cachedDefinition === 'Loading...' || data.cachedDefinition === 'Loading definition...'
    if (!isLoading && data.phrases.length > 0) {
      html += this.renderDividerWithLabel('tooltip_section_phrases')
      html += this.renderPhrases(data.phrases)
    }

    // 
    if (data.translation) {
      html += this.renderDividerWithLabel('tooltip_section_sentenceTranslation')
      html += `<div class="moki-tooltip-translation">${data.translation}</div>`
    }

    return html
  }

  renderTranslation(translation: string): string {
    return `<div class="moki-tooltip-translation">${translation}</div>`
  }

  renderLoading(word: string, translation?: string): string {
    return `
      <!--  -->
      <div class="moki-tooltip-word">${word}</div>

      <!--  -->
      <div class="moki-tooltip-phonetic-skeleton">
        <div class="moki-skeleton-bar" style="width: 80px; height: 16px;"></div>
        <div class="moki-skeleton-circle"></div>
      </div>

      <!--  ( + ) -->
      <div class="moki-tooltip-definition-skeleton">
        <div class="moki-skeleton-bar" style="width: 40px; height: 16px;"></div>
        <div class="moki-skeleton-bar" style="width: 120px; height: 20px;"></div>
      </div>

      <!--  -->
      <div class="moki-tooltip-divider"></div>

      <!--  -->
      <div class="moki-tooltip-bottom-skeleton">
        <div class="moki-skeleton-bar" style="width: 100%; height: 16px;"></div>
        <div class="moki-skeleton-bar" style="width: 60%; height: 16px;"></div>
      </div>
    `
  }

  renderExpandedLoading(word: string): string {
    return `
      <!--  Loading -->
      <div class="moki-tooltip-loading-button" style="top: 20px; right: 40px;">
        <div class="moki-skeleton-circle" style="width: 20px; height: 20px;"></div>
      </div>

      <!--  Loading -->
      <div class="moki-tooltip-loading-button" style="top: 20px; right: 8px;">
        <div class="moki-skeleton-circle" style="width: 20px; height: 20px;"></div>
      </div>

      <!--  -->
      <div class="moki-tooltip-word">${word}</div>

      <!--  -->
      <div class="moki-tooltip-phonetic-skeleton">
        <div class="moki-skeleton-bar" style="width: 80px; height: 16px;"></div>
        <div class="moki-skeleton-circle"></div>
      </div>

      <!--  ( + ) -->
      <div class="moki-tooltip-definition-skeleton">
        <div class="moki-skeleton-bar" style="width: 40px; height: 16px;"></div>
        <div class="moki-skeleton-bar" style="width: 120px; height: 20px;"></div>
      </div>

      <!--  -->
      <div class="moki-tooltip-pos-switcher-skeleton">
        <div class="moki-skeleton-bar" style="flex: 1; height: 32px; border-radius: 6px 0 0 6px;"></div>
        <div class="moki-skeleton-bar" style="flex: 1; height: 32px; margin-left: 2px;"></div>
        <div class="moki-skeleton-bar" style="flex: 1; height: 32px; margin-left: 2px; border-radius: 0 6px 6px 0;"></div>
      </div>

      <!--  -->
      <div class="moki-tooltip-divider"></div>

      <!--  -->
      <div class="moki-tooltip-senses-skeleton">
        <div>
          <div class="moki-skeleton-bar" style="width: 100%; height: 18px;"></div>
          <div class="moki-skeleton-bar" style="width: 90%; height: 14px; margin-left: 28px; margin-top: 8px; opacity: 0.7;"></div>
        </div>
        <div>
          <div class="moki-skeleton-bar" style="width: 95%; height: 18px;"></div>
        </div>
        <div>
          <div class="moki-skeleton-bar" style="width: 85%; height: 18px;"></div>
          <div class="moki-skeleton-bar" style="width: 80%; height: 14px; margin-left: 28px; margin-top: 8px; opacity: 0.7;"></div>
        </div>
      </div>

      <!--  -->
      <div class="moki-tooltip-divider"></div>

      <!--  -->
      <div class="moki-tooltip-bottom-skeleton">
        <div class="moki-skeleton-bar" style="width: 100%; height: 16px;"></div>
        <div class="moki-skeleton-bar" style="width: 60%; height: 16px;"></div>
      </div>
    `
  }

  private renderPhrases(phrases: Phrase[]): string {
    let html = '<div class="moki-tooltip-phrases">'
    phrases.forEach((phrase, index) => {
      html += `
        <div class="moki-tooltip-phrase ${index > 0 ? 'moki-tooltip-phrase-extra' : ''}">
          <div class="moki-tooltip-phrase-header">
            <span class="moki-tooltip-phrase-text">${phrase.phrase}</span>
          </div>
          <div class="moki-tooltip-phrase-meaning">${phrase.meaning}</div>
          ${phrase.explanation ? `<div class.moki-tooltip-phrase-explanation">${phrase.explanation}</div>` : ''}
        </div>
      `
    })
    html += '</div>'
    return html
  }

  // ====================  ====================

  private renderCloseButton(): string {
    return `
      <button class="moki-tooltip-close" aria-label="Close">
        ${TooltipIcons.close(24)}
      </button>
    `
  }

  private renderFavoriteButton(isFavorited: boolean): string {
    return `
      <button
        class="moki-tooltip-favorite-btn ${isFavorited ? 'active' : ''}"
        data-action="favorite"
        aria-label="${isFavorited ? 'Remove from favorites' : 'Add to favorites'}"
      >
        ${TooltipIcons.star(20, isFavorited)}
      </button>
    `
  }

  private renderPronunciationButton(): string {
    return `
      <button
        class="moki-tooltip-pronunciation-btn"
        data-action="pronounce"
        aria-label="Pronounce"
      >
        ${TooltipIcons.volume(16)}
      </button>
    `
  }

  private renderPosSwitcher(
    posSenses: Array<{ pos: string; senses: Array<string[]> }>,
    selectedPos: string,
  ): string {
    if (posSenses.length <= 1) return ''

    return `
      <div class="moki-tooltip-pos-switcher">
        ${posSenses
          .map(
            (ps) => `
          <button
            class="moki-tooltip-pos-switcher-btn ${ps.pos === selectedPos ? 'active' : ''}"
            data-pos="${ps.pos}"
          >
            ${this.formatPosLabel(ps.pos)}
          </button>
        `,
          )
          .join('')}
      </div>
    `
  }

  private renderSenseList(
    selectedPos: string,
    entryResponse: import('../../tools/client').EntryLookupResponse | undefined,
  ): string {
    if (!entryResponse) return ''

    //  entry
    const selectedEntry = entryResponse.entries.find((e) => e.pos === selectedPos)
    if (!selectedEntry) return ''

    let html = '<div class="moki-tooltip-pos-senses">'
    let glossIndex = 0

    //  senses
    for (const sense of selectedEntry.senses) {
      //  sense  glosses
      for (const gloss of sense.glosses) {
        glossIndex++
        html += `
          <div class="moki-tooltip-pos-entry">
            <div class="moki-tooltip-sense-number">${glossIndex}</div>
            <div class="moki-tooltip-sense-content">
              <div class="moki-tooltip-sense-text">${gloss.text}</div>
              ${
                gloss.example
                  ? `
                <div class="moki-tooltip-example-wrapper">
                  <div class="moki-tooltip-example-text">${gloss.example.text}</div>
                  ${gloss.example.translation ? `<div class="moki-tooltip-example-translation">${gloss.example.translation}</div>` : ''}
                </div>
              `
                  : ''
              }
            </div>
          </div>
        `
      }
    }

    html += '</div>'
    return html
  }

  private formatPosLabel(pos: string): string {
    const labels: Record<string, string> = {
      noun: 'noun',
      verb: 'verb',
      adj: 'adj.',
      adv: 'adv.',
      prep: 'prep.',
      pron: 'pron.',
      conj: 'conj.',
      interj: 'interj.',
    }
    return labels[pos] || pos
  }

  private formatPhonetic(phonetic: string, lang_code?: string): string {
    // 
    if (lang_code === 'en' && !phonetic.startsWith('/')) {
      return `/${phonetic}/`
    }
    return phonetic
  }

  /**
   * 
   *
   * @param state - 'idle' | 'loading' | 'active' | 'unfavoriting' | 'error'
   */
  updateFavoriteButtonState(state: 'idle' | 'loading' | 'active' | 'unfavoriting' | 'error'): void {
    if (!this.element) return

    const favoriteBtn = this.element.querySelector('[data-action="favorite"]') as HTMLButtonElement
    if (!favoriteBtn) {
      return
    }

    // 
    favoriteBtn.classList.remove('loading', 'active', 'unfavoriting', 'error')

    // 
    if (state !== 'idle') {
      favoriteBtn.classList.add(state)
    }

    // 
    switch (state) {
      case 'loading':
        favoriteBtn.innerHTML = ''
        favoriteBtn.disabled = true
        break
      case 'active':
        // 
        favoriteBtn.innerHTML = TooltipIcons.star(20, true)
        favoriteBtn.disabled = false
        break
      case 'unfavoriting':
        // 
        favoriteBtn.innerHTML = TooltipIcons.star(20, true)
        favoriteBtn.disabled = true
        break
      case 'error':
        favoriteBtn.innerHTML = ''
        favoriteBtn.disabled = false // 
        break
      case 'idle':
      default:
        // 
        favoriteBtn.innerHTML = TooltipIcons.star(20, false)
        favoriteBtn.disabled = false
        break
    }
  }
}
