import type { Token, SubtitleSegment } from './subtitle'

/**
 * Minimum length for a single token to be considered a "sentence".
 * Short tokens like "[Music]", "[Applause]" should not trigger sentence mode.
 */
export const SENTENCE_MIN_LENGTH = 10

/**
 * Sentence-ending punctuation pattern for multiple languages:
 * - English/European: .!?
 * - Chinese/Japanese: 
 * - Korean:  (also uses Western punctuation)
 */
export const SENTENCE_END_PUNCTUATION = /[.!?]+\s*$/

/**
 * Pattern to detect if text contains any sentence-ending punctuation (not just at end).
 */
const SENTENCE_PUNCTUATION_ANYWHERE = /[.!?]/

/**
 * Detects whether the subtitle segments are in sentence mode or word mode.
 *
 * YouTube's subtitle API returns two formats:
 * 1. Word Mode: Each segment has multiple tokens with individual timestamps.
 *    Example: segs = [{utf8: "name"}, {utf8: " is", tOffsetMs: 160}, ...]
 *
 * 2. Sentence Mode: Each segment has a single token containing the full sentence.
 *    Example: segs = [{utf8: "You want to take your English listening to an advanced level."}]
 *
 * Detection logic: If ANY segment has a single token that looks like a full sentence
 * (longer than SENTENCE_MIN_LENGTH characters), treat the entire subtitle as sentence mode.
 * This conservative approach avoids incorrectly merging sentences in mixed-mode data.
 *
 * Short single-token segments like "[Music]", "[Applause]" are ignored as they appear
 * in word mode data as well.
 *
 * @param segments - Array of subtitle segments to check
 * @returns true if sentence mode, false if word mode
 */
export function isSentenceMode(segments: SubtitleSegment[]): boolean {
  return segments.some(
    (segment) => segment.tokens.length === 1 && segment.tokens[0].utf8.length > SENTENCE_MIN_LENGTH,
  )
}

/**
 * Checks if segments contain any sentence-ending punctuation.
 * We check the entire subtitle text (not individual segments) because:
 * - Punctuation may be distributed across segments
 * - A single punctuation mark is enough to enable reorganization
 *
 * @param segments - Array of subtitle segments to check
 * @returns true if any sentence-ending punctuation is found
 */
export function hasPunctuation(segments: SubtitleSegment[]): boolean {
  const fullText = segments.map((s) => s.nativeContent).join('')
  return SENTENCE_PUNCTUATION_ANYWHERE.test(fullText)
}

/**
 * Splits a section of tokens into smaller segments based on punctuation, length, and timing.
 * Implements a three-stage splitting strategy:
 * 1. Split by punctuation marks (sentence boundaries)
 * 2. Split by maximum token count (display length constraints)
 * 3. Merge short segments to avoid fragmentation
 *
 * @param section - Array of tokens to split
 * @param splitter - Regular expression pattern for sentence-ending punctuation
 * @param maxToken - Maximum number of characters per segment
 * @param maxInterval - Maximum time gap (ms) allowed for merging segments
 * @param mergedThreshold - Minimum segment length to avoid merging
 * @returns Array of token arrays, each representing a subtitle segment
 */
export function splitSection(
  section: Token[],
  splitter: RegExp,
  maxToken: number,
  maxInterval: number,
  mergedThreshold: number,
): Token[][] {
  // 1. Split by punctuation marks (sentence boundaries)
  const splitBySplitter: Token[][] = []
  let segment: Token[] = []
  for (const token of section) {
    segment.push(token)
    if (!splitter.test(token.utf8)) {
      continue
    }
    splitBySplitter.push(segment)
    segment = []
  }

  if (segment.length > 0) {
    splitBySplitter.push(segment)
  }

  // 2. Split by maximum token count (display length constraints)
  // Ensures subtitles don't exceed comfortable reading length
  const splitByMaxToken: Token[][] = []
  for (const subSection of splitBySplitter) {
    let tokenCount = 0
    let segment: Token[] = []
    for (const token of subSection) {
      segment.push(token)
      tokenCount += token.utf8.length
      if (tokenCount > maxToken) {
        splitByMaxToken.push(segment)
        segment = []
        tokenCount = 0
      }
    }
    if (segment.length > 0) {
      splitByMaxToken.push(segment)
    }
  }

  // 3. Merge short adjacent segments (forward merging only)
  // Prevents overly fragmented subtitles
  const mergedSegments: Token[][] = []
  for (let i = 0; i < splitByMaxToken.length; i++) {
    const segment = splitByMaxToken[i]
    if (i == 0 || segment.length > mergedThreshold) {
      mergedSegments.push(segment)
    } else {
      const latestSegment = mergedSegments[mergedSegments.length - 1]
      if (latestSegment[latestSegment.length - 1].tOffsetMs + maxInterval > segment[0].tOffsetMs) {
        latestSegment.push(...segment)
      } else {
        mergedSegments.push(segment)
      }
    }
  }

  return mergedSegments
}

/**
 * Reorganizes subtitle segments for better readability and natural sentence boundaries.
 * Merges tokens across segments and re-splits based on punctuation and timing.
 *
 * @param segments - Original subtitle segments to reorganize
 * @param maxToken - Maximum number of characters per segment (default: 70)
 * @param maxInterval - Maximum time gap (ms) to consider segments as continuous (default: 1000)
 * @param mergedThreshold - Minimum segment length to avoid merging (default: 2)
 * @returns Reorganized subtitle segments
 */
export function reorgWordMode(
  segments: SubtitleSegment[],
  maxToken: number = 70,
  maxInterval: number = 1000,
  mergedThreshold: number = 2,
): SubtitleSegment[] {
  const fullText: Token[][] = []

  // Merge all tokens into continuous sections
  let section: Token[] = []
  for (let i = 0; i < segments.length; i++) {
    if (i > 0 && segments[i].begin - segments[i - 1].end > maxInterval) {
      fullText.push(section)
      section = []
    }
    // Add space between YouTube ASR segments
    section.push({ utf8: ' ', tOffsetMs: segments[i].begin })
    section.push(...segments[i].tokens)
  }
  fullText.push(section)

  // Resplit by punctuation and other criteria
  let reorgSegmentIdx = 0
  const reorgSegments: SubtitleSegment[] = []
  for (const section of fullText) {
    const splitSegments = splitSection(
      section,
      SENTENCE_END_PUNCTUATION,
      maxToken,
      maxInterval,
      mergedThreshold,
    )
    for (const segment of splitSegments) {
      reorgSegments.push({
        tokens: segment,
        nativeContent: segment
          .map((token) => token.utf8)
          .join('')
          .trim(),
        begin: segment[0].tOffsetMs,
        end: segment[segment.length - 1].tOffsetMs,
        index: reorgSegmentIdx,
      })
      reorgSegmentIdx += 1
    }
  }

  return reorgSegments
}

/**
 * Default minimum length for sentence mode segments.
 * Short segments below this threshold will be merged with the next segment.
 */
export const SENTENCE_MODE_MIN_LENGTH = 15

/**
 * Default maximum length for sentence mode segments.
 */
export const SENTENCE_MODE_MAX_LENGTH = 100

/**
 * Merges multiple segments into a single segment.
 * Combines tokens, text content, and calculates timing.
 *
 * @param segments - Array of segments to merge
 * @param newIndex - Index for the new merged segment
 * @returns A single merged segment
 */
function mergeSegments(segments: SubtitleSegment[], newIndex: number): SubtitleSegment {
  const allTokens: Token[] = []
  const textParts: string[] = []

  for (const segment of segments) {
    allTokens.push(...segment.tokens)
    textParts.push(segment.nativeContent)
  }

  return {
    tokens: allTokens,
    nativeContent: textParts.join(''),
    begin: segments[0].begin,
    end: segments[segments.length - 1].end,
    index: newIndex,
  }
}

/**
 * Phase 1: Merge segments to punctuation boundaries.
 * Continues merging until finding a sentence-ending punctuation mark.
 *
 * @param segments - Original segments to merge
 * @returns Array of segment groups, each ending with punctuation (or the last group)
 */
function mergeToPunctuationBoundaries(segments: SubtitleSegment[]): SubtitleSegment[][] {
  const groups: SubtitleSegment[][] = []
  let currentGroup: SubtitleSegment[] = []

  for (const segment of segments) {
    currentGroup.push(segment)

    // Check if this segment ends with sentence punctuation
    if (SENTENCE_END_PUNCTUATION.test(segment.nativeContent.trim())) {
      groups.push(currentGroup)
      currentGroup = []
    }
  }

  // Don't forget remaining segments
  if (currentGroup.length > 0) {
    groups.push(currentGroup)
  }

  return groups
}

/**
 * Splits a segment group by maximum length constraint.
 * Splits at original segment boundaries when group exceeds maxLength.
 *
 * @param group - Array of segments to potentially split
 * @param maxLength - Maximum character count per output segment
 * @param startIndex - Starting index for new segments
 * @returns Array of segments respecting maxLength constraint
 */
function splitGroupByMaxLength(
  group: SubtitleSegment[],
  maxLength: number,
  startIndex: number,
): SubtitleSegment[] {
  const result: SubtitleSegment[] = []
  let currentGroup: SubtitleSegment[] = []
  let currentLength = 0
  let currentIndex = startIndex

  for (const segment of group) {
    const segmentLength = segment.nativeContent.length

    // If adding this segment would exceed maxLength, flush current group first
    if (currentLength > 0 && currentLength + segmentLength > maxLength) {
      result.push(mergeSegments(currentGroup, currentIndex))
      currentIndex++
      currentGroup = []
      currentLength = 0
    }

    currentGroup.push(segment)
    currentLength += segmentLength
  }

  // Flush remaining
  if (currentGroup.length > 0) {
    result.push(mergeSegments(currentGroup, currentIndex))
  }

  return result
}

/**
 * Phase 2: Normalize group lengths based on min/max constraints.
 * - Groups shorter than minLength are merged with the next group
 * - Groups longer than maxLength are split at segment boundaries
 *
 * @param groups - Segment groups from Phase 1
 * @param minLength - Minimum character count per segment
 * @param maxLength - Maximum character count per segment
 * @returns Normalized segments
 */
function normalizeGroupLengths(
  groups: SubtitleSegment[][],
  minLength: number,
  maxLength: number,
): SubtitleSegment[] {
  const result: SubtitleSegment[] = []
  let pendingGroup: SubtitleSegment[] = []
  let currentIndex = 0

  for (const group of groups) {
    const combinedSegments = [...pendingGroup, ...group]
    const combinedLength = combinedSegments.reduce((sum, s) => sum + s.nativeContent.length, 0)

    if (combinedLength < minLength) {
      // Too short - keep pending for next merge
      pendingGroup = combinedSegments
    } else if (combinedLength > maxLength) {
      // Too long - need to split
      // First, flush pending if any (as a separate segment if it has content)
      if (pendingGroup.length > 0) {
        result.push(mergeSegments(pendingGroup, currentIndex))
        currentIndex++
        pendingGroup = []
      }
      // Then split the current group
      const splitResults = splitGroupByMaxLength(group, maxLength, currentIndex)
      result.push(...splitResults)
      currentIndex += splitResults.length
    } else {
      // Just right - merge and emit
      result.push(mergeSegments(combinedSegments, currentIndex))
      currentIndex++
      pendingGroup = []
    }
  }

  // Flush any remaining pending segments
  if (pendingGroup.length > 0) {
    result.push(mergeSegments(pendingGroup, currentIndex))
  }

  return result
}

/**
 * Reorganizes sentence mode subtitles using a two-phase strategy:
 * 1. Merge segments to punctuation boundaries (sentences end with .!?)
 * 2. Normalize segment lengths (merge if too short, split if too long)
 *
 * @param segments - Original subtitle segments (sentence mode)
 * @param minLength - Minimum character count per segment (default: 20)
 * @param maxLength - Maximum character count per segment (default: 100)
 * @returns Reorganized subtitle segments
 */
export function reorgSentenceMode(
  segments: SubtitleSegment[],
  minLength: number = SENTENCE_MODE_MIN_LENGTH,
  maxLength: number = SENTENCE_MODE_MAX_LENGTH,
): SubtitleSegment[] {
  if (segments.length === 0) {
    return []
  }

  if (segments.length === 1) {
    return segments
  }

  // Phase 1: Merge to punctuation boundaries
  const groups = mergeToPunctuationBoundaries(segments)

  // Phase 2: Normalize group lengths
  return normalizeGroupLengths(groups, minLength, maxLength)
}
