import { Logger } from '../../tools/logger'
import { callAPI } from '../../tools/clientBridge'
import type { AnalyzeResponse, LanguageDetectionResponse, Phrase } from '../../tools/client'
import {
  isSentenceMode as checkSentenceMode,
  hasPunctuation as checkHasPunctuation,
  reorgWordMode as reorgWordModeHelper,
  reorgSentenceMode as reorgSentenceModeHelper,
} from './reorgHelper'
export type { Phrase } from '../../tools/client'

/**
 * Represents a segment within a YouTube timed text event.
 * Contains the text content and timing information.
 */
export interface YoutubeTimedTextSegment {
  utf8: string
  tOffsetMs: number
  acAsrConf: number
}

/**
 * Represents a YouTube timed text event with timing and segment data.
 * Each event corresponds to a subtitle entry with start time and duration.
 */
export interface YoutubeTimedTextEvent {
  tStartMs: number
  dDurationMs: number
  segs?: YoutubeTimedTextSegment[]
}

/**
 * Represents a text token with timing information.
 * Used for fine-grained subtitle manipulation and reorganization.
 */
export interface Token {
  utf8: string
  tOffsetMs: number
}

/**
 * Represents a linguistic token with comprehensive NLP annotations.
 * Simplified flat structure matching backend consolidated token format.
 */
export interface LinguisticToken {
  // Core token info (required)
  text: string
  start: number // Character position in nativeContent
  end: number // Character position in nativeContent

  // Part-of-speech tagging (optional, flat fields from backend)
  pos_tag?: string // Detailed tag (e.g., "NNP", "VBZ")
  pos?: string // Universal tag (e.g., "PROPN", "VERB")
  pos_explanation?: string

  // Syntactic dependency (optional, flat fields from backend)
  dep?: string // Dependency type (e.g., "nsubj", "ROOT")
  head?: number // Index of head token in linguisticTokens array
  head_text?: string

  // Lemma/base form (optional, from backend)
  lemma?: string

  // Named entity (optional)
  // If entity spans multiple tokens, each token stores the full entity info
  entity?: {
    label: string // Entity type (e.g., "ORG", "GPE")
    text: string // Full entity text (complete span)
  }

  // Romanization (optional, for zh/ja/ko)
  // Aggregated from character-level to token-level
  romanization?: Array<{
    char: string
    roman: string
  }>

  // Cached word definitions (optional)
  // Map from native language to definitions string
  // Example: { "zh-CN": "; ", "ja": "; " }
  // Allows reusing cached definitions when user switches language
  definitionsCache?: Record<string, string>
}

/**
 * Represents detailed entry information from dictionary API.
 * Used for caching expanded tooltip data (phonetic, POS senses, examples).
 */
export interface EntryDetails {
  phonetic: string | null
  posSenses: Array<{ pos: string; senses: Array<string[]> }>
  examples: Array<{ text: string; translation: string | null }>
}

/**
 * Represents a processed subtitle segment with original and translated content.
 * Includes token-centric NLP analysis results and extracted phrases.
 * Used internally by the Subtitle class for time-based text lookup.
 */
export interface SubtitleSegment {
  tokens: Token[]
  nativeContent: string
  targetContent?: string
  targetLanguage?: string // Detected language code (e.g., 'en', 'zh-CN', 'ja')
  begin: number
  end: number
  index: number
  // Token-centric NLP analysis results
  linguisticTokens?: LinguisticToken[]
  // Extracted phrases (idioms, phrasal verbs, collocations, etc.)
  phrases?: Phrase[]
}

/**
 * Converts a subtitle segment to a human-readable debug string.
 * Formats segment information including index, timing, content, and translation status.
 * @param segment - The subtitle segment to convert
 * @returns Formatted string with segment details
 */
export function subtitleSegment2Str(segment: SubtitleSegment): string {
  return `[${segment.index}](${segment.begin}-${segment.end}) ${segment.nativeContent} (${segment.targetContent == null ? 'hasNoTarget' : 'hasTarget'})`
}

/**
 * Converts an array of subtitle segments (chunk) to a debug string.
 * Joins multiple segment strings with a separator for logging purposes.
 * @param chunk - Array of subtitle segments to convert
 * @returns Formatted string with all segments in the chunk
 */
export function chunk2Str(chunk: SubtitleSegment[]): string {
  return chunk.map(subtitleSegment2Str).join('\n')
}

/**
 * Extracts and formats the indexes of all segments in a chunk as a comma-separated string.
 * Useful for logging and debugging to quickly identify which segments are in a chunk.
 * @param chunk - Array of subtitle segments
 * @returns Comma-separated string of segment indexes (e.g., "0,1,2")
 */
export function chunkIdxesStr(chunk: SubtitleSegment[]): string {
  return chunk.map((segment) => segment.index).join(',')
}

/**
 * Manages subtitle segments with time-based lookup functionality.
 * Handles conversion from YouTube's timed text format to internal representation.
 */
export class Subtitle {
  private logger = new Logger('[Subtitle]')
  private segments: SubtitleSegment[]
  private targetLanguage?: string // Detected language code (e.g., 'en', 'zh-CN', 'ja')
  private tlang: string | null = null // YouTube subtitle translation target language (from URL tlang parameter)
  private videoMetadataJson: string | null = null // Video metadata JSON string (channel info, description, tags, etc.)
  private videoViewId: number | null = null // Video view ID from addVideoView API (for creating collections)
  // Cache complete API response instead of simplified EntryDetails
  // This allows us to extract both EntryDetails and render gloss-specific examples
  private entryResponseCache: Map<string, import('../../tools/client').EntryLookupResponse | null> =
    new Map() // Entry response cache (null = 404 cached)

  constructor() {
    this.segments = []
  }

  /**
   * Gets the detected target language code.
   * @returns {string | undefined} The detected language code or undefined if not detected yet
   */
  getTargetLanguage(): string | undefined {
    return this.targetLanguage
  }

  /**
   * Sets the target language code.
   * @param {string} language - The language code to set
   */
  setTargetLanguage(language: string): void {
    this.targetLanguage = language
  }

  /**
   * Gets the YouTube subtitle translation target language (tlang).
   * @returns {string | null} The tlang value or null if not set
   */
  getTlang(): string | null {
    return this.tlang
  }

  /**
   * Sets the YouTube subtitle translation target language (tlang).
   * @param {string | null} tlang - The tlang value to set
   */
  setTlang(tlang: string | null): void {
    this.tlang = tlang
  }

  /**
   * Gets the video metadata JSON string.
   * @returns {string | null} The video metadata JSON string or null if not set
   */
  getVideoMetadataJson(): string | null {
    return this.videoMetadataJson
  }

  /**
   * Sets the video metadata JSON string.
   * @param {string | null} metadataJson - The video metadata JSON string to set
   */
  setVideoMetadataJson(metadataJson: string | null): void {
    this.videoMetadataJson = metadataJson
  }

  /**
   * Gets the video view ID (for creating collections).
   * @returns {number | null} The video view ID or null if not set
   */
  getVideoViewId(): number | null {
    return this.videoViewId
  }

  /**
   * Sets the video view ID (from addVideoView API).
   * @param {number | null} videoViewId - The video view ID to set
   */
  setVideoViewId(videoViewId: number | null): void {
    this.videoViewId = videoViewId
  }

  /**
   * Gets all subtitle segments.
   * @returns {SubtitleSegment[]} Array of all subtitle segments
   */
  getSegments(): SubtitleSegment[] {
    return this.segments
  }

  /**
   * Gets a specific subtitle segment by index.
   * @param {number} idx - The index of the segment to retrieve
   * @returns {SubtitleSegment | null} The subtitle segment at the given index, or null if index is out of bounds
   */
  getSegment(idx: number): SubtitleSegment | null {
    if (idx < 0 || idx >= this.segments.length) {
      return null
    }
    return this.segments[idx]
  }

  /**
   * Adds a subtitle segment to the collection.
   * @param {SubtitleSegment} segment - The subtitle segment to add
   */
  add(segment: SubtitleSegment): void {
    this.segments.push(segment)
  }

  /**
   * Finds the subtitle segment that should be displayed at the given time.
   * Uses begin times only: finds segment where begin <= timestamp < next.begin
   * This ensures no gaps between segments - the current segment displays until the next one starts.
   * @param {number} timestamp - The time in milliseconds to search for
   * @param {number} preloadMs - How many milliseconds to preload the next segment (default: 500)
   * @returns {SubtitleSegment | null} The matching segment or null if before first segment
   */
  find(timestamp: number, preloadMs: number = 500): SubtitleSegment | null {
    if (this.segments.length === 0) {
      return null
    }

    // Adjust timestamp to preload next segment earlier for smoother experience
    const adjustedTimestamp = timestamp + preloadMs

    // If adjusted timestamp is before the first segment, return null
    if (adjustedTimestamp < this.segments[0].begin) {
      return null
    }

    // Find the last segment where begin <= adjustedTimestamp
    // This segment displays until the next segment's begin time
    for (let i = this.segments.length - 1; i >= 0; i--) {
      if (this.segments[i].begin <= adjustedTimestamp) {
        return this.segments[i]
      }
    }

    return null
  }

  /**
   * Gets the total number of subtitle segments.
   * @returns {number} The number of segments in the collection
   */
  length(): number {
    return this.segments.length
  }

  /**
   * Builds a cache key for entry details lookup.
   * Format: lemma:lang_code:native_lang_code
   * @param lemma - Word lemma (base form)
   * @param lang_code - Language code of the word (e.g., "en", "ja")
   * @param native_lang_code - User's native language code (e.g., "zh-CN")
   * @returns Cache key string
   * @example "run:en:zh-CN"
   */
  private buildEntryCacheKey(lemma: string, lang_code: string, native_lang_code: string): string {
    return `${lemma}:${lang_code}:${native_lang_code}`
  }

  /**
   * Gets cached entry response for a word.
   * @param lemma - Word lemma (base form)
   * @param lang_code - Language code of the word
   * @param native_lang_code - User's native language code
   * @returns Cached entry response, null if 404 was cached, or undefined if not in cache
   */
  getCachedEntry(
    lemma: string,
    lang_code: string,
    native_lang_code: string,
  ): import('../../tools/client').EntryLookupResponse | null | undefined {
    const key = this.buildEntryCacheKey(lemma, lang_code, native_lang_code)
    return this.entryResponseCache.get(key)
  }

  /**
   * Sets entry response cache for a word.
   * @param lemma - Word lemma (base form)
   * @param lang_code - Language code of the word
   * @param native_lang_code - User's native language code
   * @param data - Complete entry response to cache, or null if 404 (entry not found)
   */
  setCachedEntry(
    lemma: string,
    lang_code: string,
    native_lang_code: string,
    data: import('../../tools/client').EntryLookupResponse | null,
  ): void {
    const key = this.buildEntryCacheKey(lemma, lang_code, native_lang_code)
    this.entryResponseCache.set(key, data)
  }

  /**
   * Clears the entry response cache.
   * Useful when native language changes or when refreshing data.
   */
  clearEntryCache(): void {
    this.entryResponseCache.clear()
  }

  /**
   * Gets entry cache statistics for debugging.
   * @returns Object containing cache size and all keys
   */
  getEntryCacheStats(): { size: number; keys: string[] } {
    return {
      size: this.entryResponseCache.size,
      keys: Array.from(this.entryResponseCache.keys()),
    }
  }

  /**
   * Gets the indexes of all segments that have been translated.
   * Useful for tracking translation progress and debugging.
   * @returns Array of segment indexes that have targetContent set
   */
  getTranslatedIdxes() {
    return this.segments
      .filter((segment) => segment.targetContent != null)
      .map((segment) => segment.index)
  }

  /**
   * Gets the next chunks of untranslated segments starting from the current timestamp.
   * Returns chunks aligned to chunkSize boundaries for efficient batch translation.
   * Filters out segments that already have translations.
   * @param timestamp - Current video playback time in milliseconds
   * @param chunkSize - Number of segments per chunk (default: 3)
   * @param chunkNum - Maximum number of chunks to return (default: 4)
   * @returns Array of subtitle segment chunks, each containing untranslated segments
   */
  getNextChunks(
    timestamp: number,
    chunkSize: number = 3,
    chunkNum: number = 4,
  ): SubtitleSegment[][] {
    const selectedSegment = this.find(timestamp)
    if (!selectedSegment) {
      return []
    }

    const chunks: SubtitleSegment[][] = []
    const loopStart = Math.max(0, selectedSegment.index - (selectedSegment.index % chunkSize))
    const loopEnd = Math.min(loopStart + chunkSize * chunkNum, this.segments.length)
    // Move forward from selected segment (inclusive)
    for (let i = loopStart; i < loopEnd; i += chunkSize) {
      const chunk = this.segments.slice(i, Math.min(i + chunkSize, this.segments.length))
      const filteredChunk = chunk.filter((segment) => segment.targetContent == null)
      if (filteredChunk.length > 0) {
        chunks.push(filteredChunk)
      }
      if (chunks.length >= chunkNum) {
        break
      }
    }
    return chunks
  }

  /**
   * Sets the translated content for a specific segment by index.
   * @param {number} idx - Index of the segment to update
   * @param {string} targetContent - The translated content to set
   */
  setTargetContent(idx: number, targetContent: string): void {
    if (idx < 0 || idx >= this.segments.length) {
      return
    }
    this.segments[idx].targetContent = targetContent
  }

  /**
   * Sets the translated content for a segment only if it hasn't been set yet.
   * Prevents overwriting existing translations.
   * @param {number} idx - Index of the segment to update
   * @param {string} targetContent - The translated content to set
   */
  setTargetContentIfNotExists(idx: number, targetContent: string): void {
    if (idx < 0 || idx >= this.segments.length) {
      return
    }
    if (!this.segments[idx].targetContent) {
      this.segments[idx].targetContent = targetContent
    }
  }

  /**
   * Reorganizes subtitle segments based on detected mode and punctuation presence.
   *
   * Four cases:
   * - Case 1: Word mode + has punctuation  reorgWordMode (three-stage splitting)
   * - Case 2: Word mode + no punctuation  no-op (keep original)
   * - Case 3: Sentence mode + has punctuation  reorgSentenceMode (two-phase merging)
   * - Case 4: Sentence mode + no punctuation  no-op (keep original)
   *
   * @param maxToken - Maximum characters per segment for word mode (default: 70)
   * @param maxInterval - Maximum time gap for word mode (default: 1000ms)
   * @param mergedThreshold - Minimum segment length for word mode merging (default: 2)
   * @param minLength - Minimum characters per segment for sentence mode (default: 15)
   * @param maxLength - Maximum characters per segment for sentence mode (default: 100)
   */
  reorg(
    maxToken: number = 70,
    maxInterval: number = 1000,
    mergedThreshold: number = 2,
    minLength: number = 15,
    maxLength: number = 100,
  ): void {
    const sentenceMode = checkSentenceMode(this.segments)
    const hasPunct = checkHasPunctuation(this.segments)

    if (sentenceMode) {
      if (hasPunct) {
        // Case 3: Sentence mode + has punctuation
        this.segments = reorgSentenceModeHelper(this.segments, minLength, maxLength)
      }
      // Case 4: Sentence mode + no punctuation  no-op
    } else {
      if (hasPunct) {
        // Case 1: Word mode + has punctuation
        this.segments = reorgWordModeHelper(this.segments, maxToken, maxInterval, mergedThreshold)
      }
      // Case 2: Word mode + no punctuation  no-op
    }
  }

  /**
   * Clears all translated content from all subtitle segments.
   * Sets targetContent to undefined for every segment, effectively removing all translations.
   * Useful when switching languages or resetting the translation state.
   */
  clearAllTargetContent(): void {
    for (let segment of this.segments) {
      segment.targetContent = undefined
    }
  }

  /**
   * Transforms backend NLP API response (consolidated tokens) into LinguisticToken structure.
   * Directly maps token fields and aggregates entities and romanization.
   * @param {AnalyzeResponse} analysis - Backend API response with consolidated token structure
   * @returns {LinguisticToken[]} Array of tokens with all linguistic annotations
   */
  private transformToLinguisticTokens(analysis: AnalyzeResponse): LinguisticToken[] {
    return analysis.tokens.map((token) => {
      // Find entity that contains this token (by character position)
      const entity = analysis.entities.find((e) => e.start <= token.start && e.end >= token.end)

      // Aggregate romanization for this token (character-level to token-level)
      const tokenRomanization = analysis.romanization
        ?.filter((r) => r.start >= token.start && r.end <= token.end)
        .map((r) => ({ char: r.char, roman: r.romanization }))

      return {
        // Direct copy from backend consolidated token
        text: token.text,
        start: token.start,
        end: token.end,
        pos_tag: token.pos_tag,
        pos: token.pos,
        pos_explanation: token.pos_explanation,
        dep: token.dep,
        head: token.head,
        head_text: token.head_text,
        lemma: token.lemma,

        // Still need to aggregate these
        entity: entity
          ? {
              label: entity.label,
              text: entity.text,
            }
          : undefined,
        romanization:
          tokenRomanization && tokenRomanization.length > 0 ? tokenRomanization : undefined,
      }
    })
  }

  /**
   * Detects the language of subtitle content using stratified sampling.
   * Samples 10-15 segments (or 20% of total) distributed across beginning, middle, and end.
   * This avoids intro/outro language bias and provides better accuracy.
   * @returns {Promise<string>} Detected language code (e.g., 'en', 'zh-CN', 'ja'), defaults to 'en' on failure
   */
  async detectLanguage(): Promise<string> {
    const logger = new Logger('[Subtitle.detectLanguage]')
    const totalSegments = this.segments.length

    if (totalSegments === 0) {
      logger.warn('No segments available for language detection, defaulting to "en"')
      return 'en'
    }

    // Smart sampling strategy: sample ~10-15 segments or 20% of total (whichever is smaller)
    const numSamples = Math.min(15, Math.ceil(totalSegments * 0.2))
    const sampleIndices: number[] = []

    if (totalSegments <= numSamples) {
      // If video is short, use all segments
      for (let i = 0; i < totalSegments; i++) {
        sampleIndices.push(i)
      }
    } else {
      // Stratified sampling: divide into thirds and sample from each
      const third = Math.floor(totalSegments / 3)
      const samplesPerSection = Math.ceil(numSamples / 3)

      // Beginning (0 to third)
      for (let i = 0; i < samplesPerSection; i++) {
        const idx = Math.floor((i / samplesPerSection) * third)
        sampleIndices.push(idx)
      }

      // Middle (third to 2*third)
      for (let i = 0; i < samplesPerSection; i++) {
        const idx = third + Math.floor((i / samplesPerSection) * third)
        sampleIndices.push(idx)
      }

      // End (2*third to end)
      const remainingSamples = numSamples - samplesPerSection * 2
      for (let i = 0; i < remainingSamples; i++) {
        const idx = 2 * third + Math.floor((i / remainingSamples) * (totalSegments - 2 * third))
        sampleIndices.push(idx)
      }
    }

    // Collect sample texts
    const sampleTexts = sampleIndices
      .map((idx) => this.segments[idx]?.nativeContent)
      .filter((text) => text && text.trim().length > 0)
      .join(' ')

    logger.debug(
      `Sampled ${sampleIndices.length} segments from ${totalSegments} total (${sampleTexts.length} chars)`,
    )

    if (sampleTexts.length === 0) {
      logger.warn('No valid text found in samples, defaulting to "en"')
      return 'en'
    }

    try {
      const result = await callAPI<LanguageDetectionResponse>('detectLanguage', {
        text: sampleTexts,
      })

      logger.info(
        `Language detected: ${result.language || 'none'} (confidence: ${result.confidence.toFixed(2)})`,
      )

      // Fallback to 'en' if detection fails or confidence is too low
      if (result.language && result.confidence > 0.5) {
        // Store detected language in subtitle instance and all segments
        const detectedLang = result.language
        this.targetLanguage = detectedLang
        this.segments.forEach((segment) => {
          segment.targetLanguage = detectedLang
        })
        return detectedLang
      } else {
        logger.warn(
          `Language detection confidence too low (${result.confidence.toFixed(2)}), defaulting to "en"`,
        )
        this.targetLanguage = 'en'
        return 'en'
      }
    } catch (error) {
      logger.error('Language detection failed:', error)
      this.targetLanguage = 'en'
      return 'en'
    }
  }

  /**
   * Enriches all subtitle segments with token-centric NLP analysis using batch processing.
   * Makes a single batch API call for all segments, significantly improving performance.
   * Runs asynchronously without blocking subtitle display. Failed analyses are logged but don't block.
   * @param {string} language - Language code for NLP analysis (default: "en")
   * @returns {Promise<void>} Promise that resolves when all segments have been processed
   */
  async enrichWithNLP(language: string = 'en'): Promise<void> {
    try {
      // Batch API call - all segments at once (performance improvement)
      const results = await callAPI<AnalyzeResponse[]>('analyzeText', {
        texts: this.segments.map((s) => s.nativeContent),
        language,
      })

      // Map results back to segments
      results.forEach((analysis, index) => {
        this.segments[index].linguisticTokens = this.transformToLinguisticTokens(analysis)
      })
    } catch (error) {
      console.error('NLP enrichment failed:', error)
    }
  }

  /**
   * Creates a Subtitle instance from an array of subtitle segments.
   * Useful for reconstructing subtitle objects from serialized data.
   * @param {SubtitleSegment[]} segments - Array of subtitle segments
   * @returns {Subtitle} A new Subtitle instance with the provided segments
   */
  static fromSegments(segments: SubtitleSegment[]): Subtitle {
    const subtitle = new Subtitle()
    subtitle.segments = segments
    return subtitle
  }

  /**
   * Creates a Subtitle instance from YouTube's timed text event data.
   * Processes raw YouTube subtitle data into a searchable format.
   * @param {YoutubeTimedTextEvent[]} events - Array of YouTube timed text events
   * @returns {Subtitle} A new Subtitle instance with processed segments
   */
  static fromYoutubeTimedText(events: YoutubeTimedTextEvent[]): Subtitle {
    const subtitle = new Subtitle()

    let segmentIdx = 0
    for (let i = 0; i < events.length; i++) {
      const event = events[i]
      const nextEvent = i < events.length - 1 ? events[i + 1] : null

      if (!event.segs) {
        continue
      }
      if (event.segs.length == 1 && event.segs[0].utf8 === '\n') {
        continue
      }

      const segmentTokens = event.segs.map((seg) => {
        return {
          utf8: seg.utf8,
          tOffsetMs: (seg.tOffsetMs || 0) + event.tStartMs,
        }
      })
      const segmentSentence = event.segs.map((seg) => seg.utf8).join('')

      let endAt = event.tStartMs + event.dDurationMs
      if (nextEvent) {
        endAt = nextEvent.tStartMs < endAt ? nextEvent.tStartMs : endAt
      }

      subtitle.add({
        tokens: segmentTokens,
        nativeContent: segmentSentence,
        begin: event.tStartMs,
        end: endAt,
        index: segmentIdx,
      })
      segmentIdx += 1
    }

    subtitle.reorg()

    return subtitle
  }
}

/**
 * Extracts the tlang parameter from a YouTube timedtext API URL.
 * The tlang parameter indicates the subtitle translation target language.
 * @param {string} url - YouTube timedtext API URL
 * @returns {string | null} The tlang value if present, null otherwise
 * @example
 * extractTlangFromUrl('https://www.youtube.com/api/timedtext?v=abc&lang=en&tlang=zh-CN&fmt=json3')
 * // Returns: 'zh-CN'
 * @example
 * extractTlangFromUrl('https://www.youtube.com/api/timedtext?v=abc&lang=en&fmt=json3')
 * // Returns: null
 */
export function extractTlangFromUrl(url: string): string | null {
  try {
    const urlObj = new URL(url)
    const tlang = urlObj.searchParams.get('tlang')
    return tlang
  } catch (error) {
    console.error('[extractTlangFromUrl] Invalid URL:', url, error)
    return null
  }
}
