import { Logger } from '../../tools/logger'
import { Phrase } from '../../tools/client'
import { sendMessageWithResponse } from '../../tools/messageUtils'
import { TaskQueueProcessor } from '../../tools/taskQueueProcessor'
import {
  MessageSource,
  MessageType,
  TranslateResponseData,
  PhraseExtractResponseData,
} from '../../types/messages'
import { Subtitle, SubtitleSegment } from '../subtitle'

/**
 * TranslationService manages translation and phrase extraction tasks in Content Script.
 *
 * This service implements the "stateless Background" architecture where:
 * - Content Script owns the data and manages task queues locally
 * - Background Service acts as a pure API proxy
 * - Each tab has its own independent task queues
 *
 * @example
 * const service = new TranslationService('en', 'zh-CN')
 * service.setSubtitle(subtitle)
 * service.startProcessing(currentTimeMs)
 *
 * // When video time changes
 * service.updateVideoTime(newTimeMs)
 */
export class TranslationService {
  private logger = new Logger('[TranslationService]')
  private subtitle: Subtitle | null = null

  private translationProcessor: TaskQueueProcessor<string[]>
  private phraseProcessor: TaskQueueProcessor<Phrase[][]>

  private sourceLng: string
  private nativeLng: string
  private currentTimeMs: number = 0

  // Callbacks for when segments are updated
  private onTranslationComplete?: (segments: SubtitleSegment[]) => void
  private onPhraseComplete?: (segments: SubtitleSegment[]) => void

  constructor(sourceLng: string, nativeLng: string) {
    this.sourceLng = sourceLng
    this.nativeLng = nativeLng

    // Initialize translation processor
    this.translationProcessor = new TaskQueueProcessor({
      name: 'Translation',
      concurrency: 4,
      idleWaitTime: 2000,
      processAPI: async (chunks: SubtitleSegment[][]) => {
        const promises = chunks.map((chunk) => this.sendTranslateRequest(chunk))
        return Promise.all(promises)
      },
      applyResult: (chunk: SubtitleSegment[], translatedContent: string[]) => {
        if (translatedContent.length !== chunk.length) {
          this.logger.error(
            `Translation result count mismatch: expected ${chunk.length}, got ${translatedContent.length}`,
          )
          return false
        }
        for (let i = 0; i < chunk.length; i++) {
          chunk[i].targetContent = translatedContent[i]
        }
        return true
      },
    })

    // Initialize phrase extraction processor
    this.phraseProcessor = new TaskQueueProcessor({
      name: 'PhraseExtraction',
      concurrency: 4,
      idleWaitTime: 2000,
      processAPI: async (chunks: SubtitleSegment[][]) => {
        const promises = chunks.map((chunk) => this.sendPhraseExtractRequest(chunk))
        return Promise.all(promises)
      },
      applyResult: (chunk: SubtitleSegment[], phraseResults: Phrase[][]) => {
        if (phraseResults.length !== chunk.length) {
          this.logger.error(
            `Phrase result count mismatch: expected ${chunk.length}, got ${phraseResults.length}`,
          )
          return false
        }
        for (let i = 0; i < chunk.length; i++) {
          chunk[i].phrases = phraseResults[i]
        }
        return true
      },
    })
  }

  /**
   * Sends a translation request to Background and returns a Promise.
   * Uses sendMessageWithResponse for synchronous request-response pattern.
   */
  private async sendTranslateRequest(chunk: SubtitleSegment[]): Promise<string[]> {
    const segments = chunk.map((s) => s.nativeContent)
    this.logger.debug(`Sending translate request: ${segments.length} segments`)

    const response = await sendMessageWithResponse<TranslateResponseData>(
      MessageSource.CONTENT_SCRIPT,
      MessageType.TRANSLATE_REQUEST,
      {
        segments,
        sourceLng: this.sourceLng,
        targetLng: this.nativeLng,
      },
      30000, // 30 second timeout
    )

    if (response.success && response.segments) {
      this.logger.debug(`Translate response: success, ${response.segments.length} segments`)
      return response.segments
    } else {
      this.logger.error(`Translate response: failed - ${response.error}`)
      throw new Error(response.error || 'Translation failed')
    }
  }

  /**
   * Sends a phrase extraction request to Background and returns a Promise.
   * Uses sendMessageWithResponse for synchronous request-response pattern.
   */
  private async sendPhraseExtractRequest(chunk: SubtitleSegment[]): Promise<Phrase[][]> {
    const texts = chunk.map((s) => s.nativeContent)
    this.logger.debug(`Sending phrase extract request: ${texts.length} texts`)

    const response = await sendMessageWithResponse<PhraseExtractResponseData>(
      MessageSource.CONTENT_SCRIPT,
      MessageType.PHRASE_EXTRACT_REQUEST,
      {
        texts,
        language: this.sourceLng,
        nativeLng: this.nativeLng,
      },
      30000, // 30 second timeout
    )

    if (response.success && response.phrases) {
      this.logger.debug(`Phrase response: success, ${response.phrases.length} results`)
      return response.phrases
    } else {
      this.logger.error(`Phrase response: failed - ${response.error}`)
      throw new Error(response.error || 'Phrase extraction failed')
    }
  }

  /**
   * Sets the subtitle instance to work with.
   */
  setSubtitle(subtitle: Subtitle | null): void {
    this.subtitle = subtitle
  }

  /**
   * Updates the source language.
   */
  setSourceLanguage(sourceLng: string): void {
    this.sourceLng = sourceLng
  }

  /**
   * Updates the native language (target for translation).
   */
  setNativeLanguage(nativeLng: string): void {
    this.nativeLng = nativeLng
  }

  /**
   * Sets callback for when translation completes for a chunk.
   */
  setOnTranslationComplete(callback: (segments: SubtitleSegment[]) => void): void {
    this.onTranslationComplete = callback
  }

  /**
   * Sets callback for when phrase extraction completes for a chunk.
   */
  setOnPhraseComplete(callback: (segments: SubtitleSegment[]) => void): void {
    this.onPhraseComplete = callback
  }

  /**
   * Starts translation and phrase extraction for the subtitle.
   * Should be called after subtitle is loaded and language is detected.
   */
  startProcessing(currentTimeMs: number, chunkSize: number = 3, chunkNum: number = 4): void {
    if (!this.subtitle) {
      this.logger.warn('Cannot start processing: no subtitle set')
      return
    }

    this.currentTimeMs = currentTimeMs

    // Initialize translation tasks
    const translationChunks = this.subtitle.getNextChunks(currentTimeMs, chunkSize, chunkNum)
    for (const chunk of translationChunks) {
      this.pushTranslationTask(chunk)
    }

    // Initialize phrase extraction tasks
    const phraseChunks = this.subtitle.getNextChunks(currentTimeMs, chunkSize, chunkNum)
    for (const chunk of phraseChunks) {
      this.pushPhraseTask(chunk)
    }

    // Start processing loops
    this.translationProcessor.startProcessing()
    this.phraseProcessor.startProcessing()

    this.logger.info(
      `Started processing: ${translationChunks.length} translation chunks, ${phraseChunks.length} phrase chunks`,
    )
  }

  /**
   * Pushes a translation task to the queue.
   */
  private pushTranslationTask(chunk: SubtitleSegment[]): void {
    this.translationProcessor.pushTask(
      chunk,
      (processedChunk) => {
        this.logger.debug(
          `Translation completed for chunk: ${processedChunk.map((s) => s.index).join(',')}`,
        )
        this.onTranslationComplete?.(processedChunk)
      },
      (error) => {
        this.logger.error(`Translation task failed:`, error)
      },
    )
  }

  /**
   * Pushes a phrase extraction task to the queue.
   */
  private pushPhraseTask(chunk: SubtitleSegment[]): void {
    this.phraseProcessor.pushTask(
      chunk,
      (processedChunk) => {
        this.logger.debug(
          `Phrase extraction completed for chunk: ${processedChunk.map((s) => s.index).join(',')}`,
        )
        this.onPhraseComplete?.(processedChunk)
      },
      (error) => {
        this.logger.error(`Phrase extraction task failed:`, error)
      },
    )
  }

  /**
   * Updates the current video time and manages task queue.
   * Should be called periodically (e.g., every 500ms) with the current video time.
   *
   * Handles:
   * - Video jumps (backwards or forward >2s): Clear queue and reload
   * - Normal playback: Append new tasks for upcoming segments
   */
  updateVideoTime(newTimeMs: number, chunkSize: number = 3, chunkNum: number = 4): void {
    if (!this.subtitle) return

    // Check for video jump (backwards or forward >2s)
    const timeDiff = newTimeMs - this.currentTimeMs
    const isJump = timeDiff < 0 || timeDiff > 2000

    if (isJump) {
      // Clear queues and reload
      this.translationProcessor.clearQueue()
      this.phraseProcessor.clearQueue()

      const translationChunks = this.subtitle.getNextChunks(newTimeMs, chunkSize, chunkNum)
      for (const chunk of translationChunks) {
        this.pushTranslationTask(chunk)
      }

      const phraseChunks = this.subtitle.getNextChunks(newTimeMs, chunkSize, chunkNum)
      for (const chunk of phraseChunks) {
        this.pushPhraseTask(chunk)
      }

      this.logger.debug(`Video jump detected, reloaded queues`)
    } else {
      // Normal playback: append new tasks
      const translationChunks = this.subtitle.getNextChunks(newTimeMs, chunkSize, chunkNum)
      const translationQueued = this.translationProcessor.getQueuedSegmentIndices()
      for (const chunk of translationChunks) {
        if (!translationQueued.has(chunk[0].index)) {
          this.pushTranslationTask(chunk)
        }
      }

      const phraseChunks = this.subtitle.getNextChunks(newTimeMs, chunkSize, chunkNum)
      const phraseQueued = this.phraseProcessor.getQueuedSegmentIndices()
      for (const chunk of phraseChunks) {
        if (!phraseQueued.has(chunk[0].index)) {
          this.pushPhraseTask(chunk)
        }
      }
    }

    this.currentTimeMs = newTimeMs
  }

  /**
   * Stops all processing and clears queues.
   */
  stop(): void {
    this.translationProcessor.setFinished(true)
    this.phraseProcessor.setFinished(true)
    this.translationProcessor.clearQueue()
    this.phraseProcessor.clearQueue()
    this.logger.debug('Stopped all processing')
  }

  /**
   * Resets the service to initial state.
   */
  reset(): void {
    this.stop()
    this.translationProcessor.reset()
    this.phraseProcessor.reset()
    this.subtitle = null
    this.currentTimeMs = 0
    this.logger.debug('Service reset')
  }
}
