import { X, Star, Volume2 } from 'lucide-static'

/**
 *  Lucide  SVG 
 * @param svg - SVG 
 * @param size -  24
 * @returns SVG HTML 
 */
function formatIcon(svg: string, size: number = 24): string {
  // 
  return svg
    .replace(/width="24"/, `width="${size}"`)
    .replace(/height="24"/, `height="${size}"`)
    .replace(/<svg/, `<svg style="display:block"`) //  inline 
}

/**
 *  Tooltip 
 */
export const TooltipIcons = {
  close: (size = 24) => formatIcon(X, size),
  star: (size = 20, filled = false) => {
    let svg = formatIcon(Star, size)
    if (filled) {
      // 
      svg = svg
        .replace(/fill="none"/, 'fill="currentColor"')
        .replace(/stroke="currentColor"/, 'stroke="none"')
    }
    return svg
  },
  volume: (size = 16) => formatIcon(Volume2, size),
}
