import { initI18n, setInterfaceLanguage, t } from '../i18n'
import { VideoViewResponse } from '../tools/client'
import { callAPI } from '../tools/clientBridge'
import { CollectionBridge } from '../tools/collectionBridge'
import { Logger } from '../tools/logger'
import { sendMessageFireAndForget, sendMessageWithResponse } from '../tools/messageUtils'
import {
  Message,
  MessageSource,
  MessageType,
  CollectionInfo,
  SettingsChangedData,
} from '../types/messages'
import { LoginPrompt } from './loginPrompt'
import { TranslationService } from './services'
import { SidebarController } from './sidebar'
import {
  clearVideoMetadataCache,
  extractYoutubeVideoMetadata,
  getCurrentVideoTimeMs,
  getPlayer,
  hideYoutubeSubtitle,
  initVideoMetadataListener,
  isYoutubePage,
  YoutubeVideoMetadata,
  waitForVideoMetadata,
  // Moki Control Button
  initCaptionsListener,
  clearCaptionsCache,
  hasSubtitles,
  triggerAndWaitForSubtitle,
  hideYoutubeNativeSubtitle,
  showYoutubeNativeSubtitle,
  isSubtitleButtonEnabled,
  clickSubtitleButton,
  MokiControl,
} from './youtube'
import { Subtitle, SubtitleSegment, YoutubeTimedTextEvent, extractTlangFromUrl } from './subtitle'
import { SubtitleBar } from './subtitleBar'

/**
 * Content script manager that handles communication between injected scripts and background scripts.
 * This class manages the injection of interceptor scripts, processes subtitle data,
 * and provides custom subtitle overlay for YouTube videos.
 */
class ContentScriptManager {
  private logger = new Logger('[Content Script]')
  private enable: boolean = false
  private subtitle: Subtitle | null = null
  private currentSegment: SubtitleSegment | null = null

  private nativeLng: string = 'zh-CN'
  private isAuthenticated: boolean = false
  private observers: MutationObserver[] = []
  private subtitleUpdater: NodeJS.Timeout | null = null
  private videoElement: HTMLVideoElement | null = null

  private navigationChecker: NodeJS.Timeout | null = null
  private sidebar: SidebarController | null = null
  private subtitleBar: SubtitleBar | null = null
  private translationService: TranslationService | null = null
  private loginPrompt: LoginPrompt | null = null
  private videoMetadata: YoutubeVideoMetadata | null = null
  private currentVideoViewId: number | null = null
  private videoViewPromise: Promise<void> | null = null
  private mokiControl: MokiControl | null = null

  // Flags to prevent duplicate listener registration
  private messageBridgeSetup: boolean = false
  private storageListenerSetup: boolean = false
  private boundStorageListener:
    | ((changes: { [key: string]: chrome.storage.StorageChange }, areaName: string) => void)
    | null = null

  constructor() {
    // Must be set up BEFORE injectInterceptor() to avoid missing messages
    initVideoMetadataListener()
    initCaptionsListener() // For Moki Control Button subtitle availability check

    this.injectInterceptor()
    this.setupYoutubeNavigation()

    // Initialize i18n and load user settings
    this.initializeAsync().catch((err) => {
      this.logger.error('Initialization failed:', err)
    })
  }

  private async initializeAsync() {
    // Initialize i18n system first
    await initI18n()
    this.logger.debug('i18n initialized')

    //  chrome.storage 
    const result = await chrome.storage.sync.get(['nativeLanguage'])
    if (result.nativeLanguage) {
      this.nativeLng = result.nativeLanguage
      this.logger.debug(`Loaded native language from storage: ${this.nativeLng}`)
    }

    // Check initial authentication status before any subtitle processing
    await this.checkInitialAuthStatus()

    //  i18n 
    await this.init()
  }

  /**
   * Check authentication status during startup.
   * This ensures isAuthenticated is set correctly before handleInterceptedRequest is called.
   */
  private async checkInitialAuthStatus(): Promise<void> {
    try {
      const response = await sendMessageWithResponse<{ data?: { authenticated?: boolean } }>(
        MessageSource.CONTENT_SCRIPT,
        MessageType.CHECK_AUTH,
      )
      this.isAuthenticated = response?.data?.authenticated || false
      this.logger.debug(`Initial auth status: ${this.isAuthenticated}`)
    } catch (error) {
      this.logger.error('Failed to check auth status:', error)
      this.isAuthenticated = false
    }
  }

  /**
   * Initializes or reinitializes the content script manager.
   * Resets all state variables, sets up message bridge, and configures YouTube subtitle functionality.
   *
   * Simplified authentication flow:
   * - If not authenticated: Show login prompt only, skip all other initialization
   * - If authenticated: Initialize all features (sidebar, subtitle processing, etc.)
   */
  private async init() {
    this.logger.debug('Init all!')

    // Always setup message bridge and storage listener (needed for auth state changes)
    this.setupMessageBridge()
    this.setupStorageListener()

    // Check if on YouTube page
    if (!isYoutubePage(window.location.href) || getPlayer() === null) {
      return
    }

    // Authentication check: Show login prompt or initialize full features
    if (!this.isAuthenticated) {
      this.logger.debug('User not authenticated, showing login prompt only')
      this.showLoginPrompt()
      return
    }

    // User is authenticated: Initialize all features
    this.logger.debug('User authenticated, initializing all features')
    this.enable = false
    this.subtitle = null
    this.currentSegment = null
    this.observers = []
    this.subtitleUpdater = null
    this.videoElement = null
    this.sidebar = null
    this.subtitleBar = null
    this.translationService = null
    this.loginPrompt = null
    this.videoMetadata = null
    this.currentVideoViewId = null
    this.mokiControl = null

    // Clear cached video metadata from previous video
    clearVideoMetadataCache()
    clearCaptionsCache()

    // Wait for video metadata from Injected Script, then extract
    const hasMetadata = await waitForVideoMetadata(3000)
    if (hasMetadata) {
      this.videoMetadata = extractYoutubeVideoMetadata()
      if (this.videoMetadata) {
        this.logger.debug('Video metadata extracted', { videoId: this.videoMetadata.videoId })
        this.videoViewPromise = this.addVideoViewAsync()
      }
    } else {
      this.logger.warn('Failed to receive video metadata from Injected Script')
    }

    // Initialize Moki Control Button (user clicks to enable subtitles)
    await this.initMokiControl()

    // Set up listeners for player events
    this.addListeners()
    this.setupNavigationCleanup()

    sendMessageFireAndForget(MessageSource.CONTENT_SCRIPT, MessageType.INIT_BACKGROUND)
  }

  /**
   * Track video view asynchronously (fire-and-forget).
   * Stores the video_view_id for later use when creating collections.
   */
  private async addVideoViewAsync(): Promise<void> {
    if (!this.videoMetadata) {
      this.logger.warn('No video metadata available, skipping video view tracking')
      return
    }

    try {
      const response = await callAPI<VideoViewResponse>('addVideoView', {
        platform: 'youtube',
        video_url: this.videoMetadata.videoUrl,
        video_id: this.videoMetadata.videoId,
        video_title: this.videoMetadata.title,
        video_metadata: JSON.stringify(this.videoMetadata),
      })

      this.currentVideoViewId = response.id
      this.logger.info(
        `Video view tracked: id=${response.id}, video_id=${response.video_id}, view_count=${response.view_count}`,
      )
    } catch (error) {
      this.logger.error('Failed to track video view:', error)
      // Don't throw - video view tracking failure shouldn't break the extension
    }
  }

  /**
   * Get the current video view ID (for creating collections)
   */
  public getVideoViewId(): number | null {
    return this.currentVideoViewId
  }

  /**
   * Pre-load collections from Background Service via CollectionBridge.
   * Warms up the Background cache for faster subsequent lookups.
   * Note: Data is managed centrally in Background, no local cache in Content Script.
   */
  private async preloadCollectionsFromBackground(
    videoViewId: number,
    sourceLang: string,
  ): Promise<void> {
    try {
      const collections = await CollectionBridge.loadCollections(videoViewId, sourceLang)
      this.logger.info(`Preloaded ${collections.length} collections from Background`)
    } catch (error) {
      this.logger.error('Failed to preload collections from Background:', error)
    }
  }

  /**
   * Handle collection added broadcast from Background.
   * Refreshes sidebar highlights to reflect the new collection.
   */
  private handleCollectionAdded(data: { videoViewId: number; collection: CollectionInfo }): void {
    if (data.videoViewId !== this.currentVideoViewId) {
      return // Not for this video
    }

    this.logger.debug(`Collection added: ${data.collection.word}`)
    this.sidebar?.refreshCollectedHighlights()
  }

  /**
   * Handle collection removed broadcast from Background.
   * Refreshes sidebar highlights to reflect the removal.
   */
  private handleCollectionRemoved(data: {
    videoViewId: number
    collectionId: string
    word: string
    lemma: string | null
  }): void {
    if (data.videoViewId !== this.currentVideoViewId) {
      return // Not for this video
    }

    this.logger.debug(`Collection removed: ${data.word}`)
    this.sidebar?.refreshCollectedHighlights()
  }

  /**
   * Handle settings changed broadcast from Background Service.
   * Updates UI components when user changes settings in Popup.
   */
  private async handleSettingsChanged(data: SettingsChangedData): Promise<void> {
    const { settings, changedKeys } = data
    this.logger.debug('Settings changed:', { changedKeys, settings })

    // Handle native language change
    if (changedKeys.includes('nativeLanguage') && this.nativeLng !== settings.nativeLanguage) {
      this.logger.debug(`Change native lng from ${this.nativeLng} to ${settings.nativeLanguage}`)
      this.subtitle?.clearAllTargetContent()
      this.translationService?.setNativeLanguage(settings.nativeLanguage)
      this.sidebar?.updateNativeLanguage(settings.nativeLanguage)
      this.nativeLng = settings.nativeLanguage
    }

    // Handle interface language change
    if (changedKeys.includes('interfaceLanguage')) {
      this.logger.debug(`Interface language changed to ${settings.interfaceLanguage}`)
      // Reload i18n messages for the new locale
      await setInterfaceLanguage(settings.interfaceLanguage)
      // Refresh MokiControl menu text
      this.mokiControl?.refreshI18n()
    }
  }

  /**
   * Stores bound event handler references for proper cleanup.
   * Prevents memory leaks by maintaining references to event listeners for later removal.
   */
  private boundHandlers = {
    chromeMessage: (
      message: Message,
      _sender: chrome.runtime.MessageSender,
      sendResponse: (response?: any) => void,
    ) => {
      if (message.source === MessageSource.BACKGROUND) {
        switch (message.type) {
          case MessageType.NATIVE_LANGUAGE_CHANGE:
            if (this.nativeLng !== message.data.nativeLng) {
              this.logger.debug(
                `Change native lng from ${this.nativeLng} to ${message.data.nativeLng}`,
              )
              this.subtitle?.clearAllTargetContent()
              //  TranslationService 
              this.translationService?.setNativeLanguage(message.data.nativeLng)
              //  Sidebar  Tooltip 
              this.sidebar?.updateNativeLanguage(message.data.nativeLng)
            }
            this.nativeLng = message.data.nativeLng
            break

          // Collection broadcasts from Background (CollectionManager)
          case MessageType.COLLECTION_ADDED:
            this.handleCollectionAdded(message.data)
            break

          case MessageType.COLLECTION_REMOVED:
            this.handleCollectionRemoved(message.data)
            break

          // Settings change broadcast from Background (SettingsHandler)
          case MessageType.SETTINGS_CHANGED:
            this.handleSettingsChanged(message.data)
            break

          default:
            this.logger.warn(`Unknown message type: ${message.type}`)
        }
      } else if (message.source === MessageSource.POPUP) {
        // Handle messages from Popup (e.g., Debug page)
        switch (message.type) {
          case MessageType.GET_SUBTITLE_DATA:
            // Return subtitle data for Debug page
            const subtitleData = this.subtitle
              ? {
                  segments: this.subtitle.getSegments(),
                  length: this.subtitle.length(),
                  metadata: {
                    videoId: this.videoMetadata?.videoId || null,
                    videoTitle: this.videoMetadata?.title || null,
                    tlang: this.subtitle.getTlang(),
                    targetLanguage: this.subtitle.getTargetLanguage(),
                    nativeLng: this.nativeLng,
                    hasSubtitle: true,
                  },
                }
              : null

            sendResponse({
              source: MessageSource.CONTENT_SCRIPT,
              type: MessageType.GET_SUBTITLE_DATA_RESPONSE,
              data: { subtitleData },
              timestamp: Date.now(),
            })
            return true // Keep channel open for async response
        }
      } else {
        this.logger.warn(`Unknown message source: ${message.source}`)
      }
      return false
    },
    videoPlay: () => this.adjustYoutubeSubtitleBar(),
    videoFullscreen: () => this.adjustYoutubeSubtitleBar(),
    windowResize: () => this.adjustYoutubeSubtitleBar(),
    beforeUnload: () => {
      this.logger.debug('beforeunload')
      this.cleanup()
    },
    popstate: () => {
      this.logger.debug('popstate')
      this.cleanup()
    },
    pagehide: () => {
      this.logger.debug('pagehide')
      this.cleanup()
    },
  }

  /**
   * Injects the interceptor script into the page's real environment.
   * This allows the script to access and modify XMLHttpRequest in the page context
   * to capture subtitle data from YouTube's API responses.
   */
  private injectInterceptor(): void {
    if (!this.shouldInject(window.location.href)) {
      this.logger.debug('Skip inject interceptor')
      return
    }

    const script = document.createElement('script')

    // Defined in `vite.config.ts`:
    // input: `src/injected/injected.ts`
    // output: `assets/injected.js`
    script.src = chrome.runtime.getURL('assets/injected.js')

    // Remove the script tag from DOM immediately after loading to avoid duplicate execution
    // and prevent other extensions from accessing this injection
    script.onload = () => {
      script.remove()
      this.logger.debug('Interceptor script injected')
    }

    // Inject the interceptor script as early as possible, before page load completion
    // document.head always exists in modern browsers, but in older browsers,
    // document.head might not exist, so use document.documentElement as fallback
    ;(document.head || document.documentElement).appendChild(script)
  }

  /**
   * Establishes a message bridge between `injected.js` and `background.js`
   * Listens for messages from the injected script and forwards them appropriately
   * Only sets up once to prevent duplicate listeners.
   */
  private setupMessageBridge(): void {
    if (this.messageBridgeSetup) {
      this.logger.debug('Message bridge already setup, skipping')
      return
    }

    // Listen for message from the `background.js`
    chrome.runtime.onMessage.addListener(this.boundHandlers.chromeMessage)

    this.messageBridgeSetup = true
    this.logger.debug('Message bridge setup completed')
  }

  /**
   * Sets up storage listener to detect login state changes.
   * When user logs in or logs out, automatically update UI and subtitle display.
   * Only sets up once to prevent duplicate listeners.
   */
  private setupStorageListener(): void {
    if (this.storageListenerSetup) {
      this.logger.debug('Storage listener already setup, skipping')
      return
    }

    // Create bound listener and store reference for cleanup
    this.boundStorageListener = (changes, areaName) => {
      if (areaName === 'local' && changes.access_token) {
        const newToken = changes.access_token.newValue
        const oldToken = changes.access_token.oldValue

        if (!oldToken && newToken) {
          // User just logged in
          this.logger.info('User logged in, updating authentication state...')
          this.handleAuthSuccess()
        } else if (oldToken && !newToken) {
          // User logged out
          this.logger.info('User logged out, updating authentication state...')
          this.handleAuthLogout()
        }
      }
    }

    chrome.storage.onChanged.addListener(this.boundStorageListener)

    this.storageListenerSetup = true
    this.logger.debug('Storage listener setup completed')
  }

  /**
   * Handles user login event.
   * Cleans up login prompt and reinitializes all features.
   */
  private handleAuthSuccess(): void {
    this.isAuthenticated = true
    this.logger.debug('User logged in, reinitializing all features...')

    // Clean up login prompt if it exists
    if (this.loginPrompt) {
      this.loginPrompt.destroy()
      this.loginPrompt = null
    }

    // Reinitialize all features
    this.init()
  }

  /**
   * Handles user logout event.
   * Cleans up all features and shows login prompt.
   */
  private handleAuthLogout(): void {
    this.isAuthenticated = false
    this.logger.debug('User logged out, showing login prompt...')

    // Clean up all features
    this.cleanup()

    // Show login prompt
    this.showLoginPrompt()
  }

  /**
   * Determines whether the interceptor should be injected based on the current URL
   * @param url - The current page URL
   * @returns True if the interceptor should be injected, false otherwise
   */
  private shouldInject(url: string): boolean {
    return isYoutubePage(url)
  }

  /**
   * Updates the custom subtitle overlay with current video time.
   * Continuously checks video time and displays corresponding subtitle segments.
   */
  private setSubtitleSegmentUpdater(): void {
    const video = document.querySelector('video')
    if (!video) {
      return
    }

    this.subtitleUpdater = setInterval(() => {
      if (!document.contains(video)) {
        this.cleanup()
        return
      }

      if (video.ended) {
        this.cleanup()
        return
      }

      this.updateSubtitleSegment()
    }, 300)
  }

  /**
   * Refreshes the subtitle display with the given segment.
   * Updates both the subtitle bar and sidebar.
   * Supports single-line layout (not authenticated) and double-line layout (authenticated).
   * @param {SubtitleSegment} segment - The segment to display (optional, defaults to current segment)
   */
  private refreshSubtitleDisplay(segment?: SubtitleSegment): void {
    if (!this.subtitleBar) {
      return
    }

    // Use provided segment or current segment
    const segmentToDisplay = segment || this.currentSegment
    if (!segmentToDisplay) {
      return
    }

    this.subtitleBar.updateContent(
      segmentToDisplay.nativeContent || '',
      segmentToDisplay.targetContent || null,
      this.isAuthenticated,
    )

    this.currentSegment = segmentToDisplay
    this.toggleSubtitleBar()

    // Auto-scroll sidebar to current subtitle
    if (this.sidebar && segment) {
      this.sidebar.scrollToSegment(segmentToDisplay.index)
    }
  }

  /**
   * Updates the displayed subtitle content based on current video time.
   * Shows the appropriate subtitle segment for the current playback position.
   */
  private updateSubtitleSegment(): void {
    if (!this.subtitle || !this.subtitleBar) {
      // 
      // 
      return
    }

    const currentVideoTimeMs = getCurrentVideoTimeMs()
    if (!currentVideoTimeMs) {
      return
    }

    //  TranslationService 
    this.translationService?.updateVideoTime(currentVideoTimeMs)

    const segment = this.subtitle?.find(currentVideoTimeMs)

    if (!segment) {
      // 
      this.toggleSubtitleBar()
      return
    }

    if (segment !== this.currentSegment) {
      this.refreshSubtitleDisplay(segment)
    }
  }

  /**
   * Adjusts the position and styling of the subtitle bar based on player state.
   * Handles positioning relative to YouTube player and control bar visibility.
   */
  private adjustYoutubeSubtitleBar() {
    this.subtitleBar?.adjustPosition(getPlayer())
  }

  /**
   * Sets up event listeners for video playback and UI changes.
   * Monitors video events, window resize, and YouTube control bar changes.
   */
  private addListeners() {
    this.videoElement = document.querySelector('video')
    if (this.videoElement) {
      this.videoElement.addEventListener('play', this.boundHandlers.videoPlay)
      this.videoElement.addEventListener('fullscreenchange', this.boundHandlers.videoFullscreen)
    }

    window.addEventListener('resize', this.boundHandlers.windowResize)

    const player = getPlayer()
    if (player) {
      // YouTube/ SubtitleBar
      const observer = new MutationObserver(() => this.adjustYoutubeSubtitleBar())
      observer.observe(player, {
        attributes: true,
        attributeFilter: ['class'],
      })
      this.observers.push(observer)

      //  SubtitleBar 
      const subtitleObserver = new MutationObserver(() => this.toggleSubtitleBar())
      subtitleObserver.observe(player, {
        childList: true,
        subtree: true,
        attributes: true,
        attributeFilter: ['style', 'class'],
      })
      this.observers.push(subtitleObserver)
    }
  }

  /**
   * Toggles subtitle bar visibility based on current state.
   * When Moki is enabled, show subtitles based on content availability only.
   * When Moki is disabled, also check if YouTube native subtitles are enabled.
   * Note: YouTube dynamically adds/removes .caption-window elements during playback gaps,
   * so we can't rely on isSubtitleEnabled() when Moki is active.
   */
  private toggleSubtitleBar(): void {
    if (!this.subtitleBar) {
      return
    }

    // When Moki is enabled, we manage subtitles ourselves - don't check YouTube's caption-window
    // because YouTube removes it during gaps between subtitles
    const shouldShow = this.subtitleBar.hasContent() && this.enable
    this.subtitleBar.setVisible(shouldShow)
  }

  /**
   * Sets up YouTube SPA (Single Page Application) navigation monitoring.
   * Periodically checks for URL changes and reinitializes the extension when navigating to new videos.
   */
  private setupYoutubeNavigation(): void {
    //  YouTube SPA 
    let currentUrl = window.location.href
    const checkNavigation = () => {
      if (window.location.href !== currentUrl) {
        this.logger.debug('current url changed from ', currentUrl, ' to ', window.location.href)
        currentUrl = window.location.href
        if (this.enable) {
          this.logger.debug('Cleanup because of navigation')
          this.cleanup()
        }
        if (isYoutubePage(currentUrl) && getPlayer() !== null) {
          this.logger.debug('Init because of navigation')
          this.init()
        }
      }
    }

    //  URL YouTube  SPA
    this.navigationChecker = setInterval(checkNavigation, 1000)
  }

  /**
   * Sets up cleanup mechanisms for page navigation and unload events.
   * Monitors URL changes and cleans up resources when leaving YouTube pages.
   */
  private setupNavigationCleanup(): void {
    // 
    window.addEventListener('beforeunload', this.boundHandlers.beforeUnload)
    window.addEventListener('popstate', this.boundHandlers.popstate)
    window.addEventListener('pagehide', this.boundHandlers.pagehide)
  }

  /**
   * 
   *  sidebar 
   */
  private async showLoginPrompt(): Promise<void> {
    try {
      const insertionPoint = await LoginPrompt.waitForInsertionPoint(5000)
      if (!insertionPoint) {
        this.logger.warn('Insertion point not found, login prompt init aborted')
        return
      }

      this.loginPrompt = LoginPrompt.getInstance()
      this.loginPrompt.activate(insertionPoint.container, insertionPoint.referenceNode)
      this.logger.debug('Login prompt displayed')
    } catch (error) {
      this.logger.error('Failed to show login prompt:', error)
    }
  }

  /**
   * 
   * SidebarView  DOM 
   */
  private async initSidebar(): Promise<void> {
    try {
      // 
      this.sidebar = SidebarController.getInstance(this.nativeLng)
      await this.sidebar.init(this.subtitle)
      this.logger.debug('Sidebar initialized')
    } catch (error) {
      this.logger.error('Failed to initialize sidebar:', error)
    }
  }

  /**
   *  Moki 
   */
  private async initMokiControl(): Promise<void> {
    this.mokiControl = new MokiControl({
      onEnable: () => this.enableMokiSubtitles(),
      onDisable: () => this.disableMokiSubtitles(),
      onLanguageChange: (langCode) => this.handleSubtitleLanguageChange(langCode),
    })

    await this.mokiControl.init()

    // Check if subtitles are available (updates button state)
    // Note: hasSubtitles() uses cached captions data from VIDEO_METADATA message
    setTimeout(() => {
      const available = hasSubtitles()
      this.mokiControl?.setAvailable(available)
      this.logger.debug(`Moki control initialized, subtitles available: ${available}`)
    }, 500) // Small delay to ensure captions data is received
  }

  /**
   *  Moki 
   * Called when user clicks Moki button to enable
   */
  private async enableMokiSubtitles(): Promise<void> {
    try {
      this.logger.info('Enabling Moki subtitles...')

      // 1. Trigger CC button and intercept subtitle data
      // The triggerAndWaitForSubtitle function will:
      // - Select the best caption track (ASR preferred)
      // - Send params to Injected Script to modify the URL
      // - Click CC button to trigger the request
      // - Wait for intercepted data
      // Use selected language from MokiControl as tlang
      const selectedLang = this.mokiControl?.getSelectedLanguage()
      const result = await triggerAndWaitForSubtitle(selectedLang)

      // 2. Initialize Subtitle from intercepted data
      this.subtitle = Subtitle.fromYoutubeTimedText(result.events as YoutubeTimedTextEvent[])
      const tlang = extractTlangFromUrl(result.url)
      this.subtitle.setTlang(tlang)
      this.logger.info(`Subtitle initialized with tlang: ${tlang}`)

      // 3. Create or reuse UI components
      this.enable = true
      hideYoutubeNativeSubtitle()

      // SubtitleBar 
      this.subtitleBar = SubtitleBar.getInstance()
      this.subtitleBar.activate()

      // Sidebar 
      await this.initSidebar()

      // 4. Detect language and start translation service
      let detectedLng = 'en'
      try {
        detectedLng = await this.subtitle.detectLanguage()
        this.logger.info(`Detected subtitle language: ${detectedLng}`)
      } catch (err) {
        this.logger.error('Language detection failed, using default "en":', err)
      }

      // 5. Initialize TranslationService
      this.translationService = new TranslationService(detectedLng, this.nativeLng)
      this.translationService.setSubtitle(this.subtitle)

      // Set translation complete callback
      this.translationService.setOnTranslationComplete((segments) => {
        const currentSegmentIndex = this.currentSegment?.index
        for (const segment of segments) {
          if (currentSegmentIndex !== undefined && segment.index === currentSegmentIndex) {
            this.refreshSubtitleDisplay(this.currentSegment!)
            break
          }
        }
      })

      // Start translation and phrase extraction
      const currentTimeMs = getCurrentVideoTimeMs() || 0
      this.translationService.startProcessing(currentTimeMs)

      // 6. Start subtitle updater
      this.setSubtitleSegmentUpdater()

      // 7. NLP enrichment (async)
      this.subtitle
        .enrichWithNLP(detectedLng)
        .then(() => {
          this.logger.debug('NLP enrichment completed')
          if (this.sidebar) {
            this.sidebar.updateSubtitles(this.subtitle!)
          }
        })
        .catch((err) => {
          this.logger.error('NLP enrichment failed:', err)
        })

      // 8. Preload collected words via CollectionBridge (centralized in Background)
      if (this.videoViewPromise) {
        this.videoViewPromise
          .then(async () => {
            if (this.currentVideoViewId && this.subtitle) {
              this.subtitle.setVideoViewId(this.currentVideoViewId)
              await this.preloadCollectionsFromBackground(this.currentVideoViewId, detectedLng)
              this.sidebar?.refreshCollectedHighlights()
            }
          })
          .catch((err) => {
            this.logger.error('Failed to preload collected words:', err)
          })
      }

      // Store video metadata
      if (this.videoMetadata) {
        this.subtitle.setVideoMetadataJson(JSON.stringify(this.videoMetadata))
      }

      this.logger.info('Moki subtitles enabled successfully')
    } catch (error) {
      this.logger.error('Failed to enable Moki subtitles:', error)
      // Reset state on error
      this.disableMokiSubtitles()
      throw error // Re-throw to let MokiControl handle the error
    }
  }

  /**
   *  Moki 
   * Called when user clicks Moki button to disable
   */
  private disableMokiSubtitles(): void {
    this.logger.info('Disabling Moki subtitles...')

    // Stop translation service
    if (this.translationService) {
      this.translationService.stop()
      this.translationService = null
    }

    // Stop subtitle updater
    if (this.subtitleUpdater) {
      clearInterval(this.subtitleUpdater)
      this.subtitleUpdater = null
    }

    // Hide UI components (don't destroy, keep for quick re-enable)
    this.subtitleBar?.hide()
    this.sidebar?.hide()

    // Restore YouTube native subtitle if it was enabled
    showYoutubeNativeSubtitle()
    if (isSubtitleButtonEnabled()) {
      clickSubtitleButton() // Turn off CC to match Moki state
    }

    this.enable = false
    this.subtitle = null
    this.currentSegment = null

    this.logger.info('Moki subtitles disabled')
  }

  /**
   * Handle subtitle language change while Moki is enabled.
   * Triggered by user selecting a language in Moki menu.
   */
  private async handleSubtitleLanguageChange(langCode: string): Promise<void> {
    this.logger.info(`Subtitle language changed to: ${langCode}`)

    try {
      // Re-fetch subtitles with new language
      const result = await triggerAndWaitForSubtitle(langCode)

      // Initialize new Subtitle from fetched data
      const newSubtitle = Subtitle.fromYoutubeTimedText(result.events as YoutubeTimedTextEvent[])
      const tlang = extractTlangFromUrl(result.url)
      newSubtitle.setTlang(tlang)

      // Use shared reinitialize logic
      await this.reinitializeWithNewSubtitle(newSubtitle, tlang)

      this.logger.info('Subtitle language change completed')
    } catch (error) {
      this.logger.error('Failed to change subtitle language:', error)
      // Don't disable Moki on error, keep showing previous subtitles
    }
  }

  /**
   * Shared logic for reinitializing subtitle after language change.
   * Called by handleSubtitleLanguageChange when user selects a language in MokiControl menu.
   */
  private async reinitializeWithNewSubtitle(
    newSubtitle: Subtitle,
    tlang: string | null,
  ): Promise<void> {
    // 1. Stop current translation service
    if (this.translationService) {
      this.translationService.stop()
      this.translationService = null
    }

    // 2. Update subtitle instance
    this.subtitle = newSubtitle
    this.subtitle.setTlang(tlang)
    this.logger.info(`Reinitialized subtitle with tlang: ${tlang}`)

    // 3. Update Sidebar
    if (this.sidebar) {
      this.sidebar.updateSubtitles(this.subtitle)
    }

    // 4. Detect language
    let detectedLng = 'en'
    try {
      detectedLng = await this.subtitle.detectLanguage()
      this.logger.info(`Detected subtitle language: ${detectedLng}`)
    } catch (err) {
      this.logger.error('Language detection failed, using default "en":', err)
    }

    // 5. Restart TranslationService
    this.translationService = new TranslationService(detectedLng, this.nativeLng)
    this.translationService.setSubtitle(this.subtitle)
    this.translationService.setOnTranslationComplete((segments) => {
      const currentSegmentIndex = this.currentSegment?.index
      for (const segment of segments) {
        if (currentSegmentIndex !== undefined && segment.index === currentSegmentIndex) {
          this.refreshSubtitleDisplay(this.currentSegment!)
          break
        }
      }
    })

    const currentTimeMs = getCurrentVideoTimeMs() || 0
    this.translationService.startProcessing(currentTimeMs)

    // 6. NLP enrichment (async)
    this.subtitle
      .enrichWithNLP(detectedLng)
      .then(() => {
        if (this.sidebar) {
          this.sidebar.updateSubtitles(this.subtitle!)
        }
      })
      .catch((err) => {
        this.logger.error('NLP enrichment failed:', err)
      })

    // 7. Store video metadata
    if (this.videoMetadata) {
      this.subtitle.setVideoMetadataJson(JSON.stringify(this.videoMetadata))
    }
  }

  /**
   * Cleans up all active timers, observers, and event listeners.
   * Prevents memory leaks by properly disposing of resources.
   */
  private cleanup(): void {
    this.enable = false
    //  subtitle bar
    this.toggleSubtitleBar()

    // 
    if (this.translationService) {
      this.translationService.stop()
      this.translationService = null
    }

    // 
    if (this.subtitleUpdater) {
      clearInterval(this.subtitleUpdater)
      this.subtitleUpdater = null
    }

    //  MutationObserver
    this.observers.forEach((observer) => observer.disconnect())
    this.observers.length = 0

    // 
    window.removeEventListener('resize', this.boundHandlers.windowResize)
    window.removeEventListener('beforeunload', this.boundHandlers.beforeUnload)
    window.removeEventListener('popstate', this.boundHandlers.popstate)
    window.removeEventListener('pagehide', this.boundHandlers.pagehide)

    if (this.videoElement) {
      this.videoElement.removeEventListener('play', this.boundHandlers.videoPlay)
      this.videoElement.removeEventListener('fullscreenchange', this.boundHandlers.videoFullscreen)
      this.videoElement = null
    }

    //  Chrome runtime 
    chrome.runtime.onMessage.removeListener(this.boundHandlers.chromeMessage)

    // Reset message bridge flag so it can be re-registered on next init
    this.messageBridgeSetup = false

    // 
    if (this.sidebar) {
      this.sidebar.destroy()
      this.sidebar = null
    }

    // 
    if (this.subtitleBar) {
      this.subtitleBar.destroy()
      this.subtitleBar = null
    }

    // 
    if (this.loginPrompt) {
      this.loginPrompt.destroy()
      this.loginPrompt = null
    }

    //  Moki 
    if (this.mokiControl) {
      this.mokiControl.cleanup()
      this.mokiControl = null
    }

    this.logger.debug('Cleaned up!')
  }
}

/**
 * Initialize the content script manager
 */
new ContentScriptManager()
