import { MessageSource, MessageType } from '../../types/messages'
import { sendMessageFireAndForget } from '../../tools/messageUtils'
import { tBrowser } from '../../i18n'
import './loginPrompt.css'

/**
 * LoginPrompt - 
 *  sidebar 
 */
export class LoginPrompt {
  private static instance: LoginPrompt | null = null
  private container: HTMLDivElement | null = null

  private constructor() {
    this.initDOM()
  }

  /**
   * 
   */
  static getInstance(): LoginPrompt {
    if (!LoginPrompt.instance) {
      LoginPrompt.instance = new LoginPrompt()
    }
    return LoginPrompt.instance
  }

  /**
   *  DOM 
   */
  private initDOM(): void {
    this.container = document.createElement('div')
    this.container.className = 'moki-login-prompt'
    this.container.innerHTML = `
      <div class="moki-login-prompt-content">
        <div class="moki-login-prompt-icon"></div>
        <h3 class="moki-login-prompt-title">${tBrowser('sidebar_auth_banner_text')}</h3>
        <button class="moki-login-prompt-button" id="moki-login-btn">
          ${tBrowser('sidebar_auth_login_button')}
        </button>
      </div>
    `

    // 
    const loginBtn = this.container.querySelector('#moki-login-btn')
    if (loginBtn) {
      loginBtn.addEventListener('click', this.handleLoginClick.bind(this))
    }
  }

  /**
   * 
   * @param insertionPoint - 
   * @param referenceNode - 
   */
  activate(insertionPoint: HTMLElement, referenceNode: Element | null): void {
    if (this.container && this.container.parentElement !== insertionPoint) {
      insertionPoint.insertBefore(this.container, referenceNode)
    }
  }

  /**
   * 
   */
  destroy(): void {
    if (this.container) {
      this.container.remove()
      this.container = null
    }
    LoginPrompt.instance = null
  }

  /**
   * 
   *  Background Service  Popup
   */
  private handleLoginClick(): void {
    sendMessageFireAndForget(MessageSource.CONTENT_SCRIPT, MessageType.OPEN_POPUP)
  }

  /**
   * YouTube  SPA
   * @param timeout - 
   * @returns  null
   */
  static async waitForInsertionPoint(
    timeout: number,
  ): Promise<{ container: HTMLElement; referenceNode: Element | null } | null> {
    return new Promise((resolve) => {
      const getPoint = () => {
        const secondary = document.querySelector<HTMLElement>('#secondary')
        if (secondary) {
          const watchNext = secondary.querySelector('#secondary-inner')
          return { container: secondary, referenceNode: watchNext }
        }
        return null
      }

      const point = getPoint()
      if (point) {
        return resolve(point)
      }

      const observer = new MutationObserver(() => {
        const point = getPoint()
        if (point) {
          observer.disconnect()
          resolve(point)
        }
      })

      observer.observe(document.body, { childList: true, subtree: true })

      setTimeout(() => {
        observer.disconnect()
        resolve(getPoint())
      }, timeout)
    })
  }
}
