/**
 * YouTube Video Metadata Extractor
 *
 * Extracts video metadata from YouTube page data and DOM elements.
 * The primary source is YouTube's ytInitialPlayerResponse object,
 * with DOM fallbacks for reliability.
 */

// ============================================================================
// Type Definitions
// ============================================================================

export interface YoutubeVideoMetadata {
  // Basic Info
  videoId: string
  title: string
  description: string
  duration: number | null // in seconds
  durationFormatted: string | null

  // Channel Info
  channelId: string | null
  channelName: string | null
  channelUrl: string | null

  // Video Stats
  viewCount: number | null
  likeCount: number | null
  publishDate: string | null // ISO date string
  uploadDate: string | null // ISO date string

  // Categorization
  category: string | null
  tags: string[]
  isLiveContent: boolean
  isPrivate: boolean

  // Thumbnails
  thumbnailUrl: string | null

  // URL
  videoUrl: string
}

// YouTube's internal data structure (partial)
interface YTInitialPlayerResponse {
  videoDetails?: {
    videoId?: string
    title?: string
    lengthSeconds?: string
    channelId?: string
    shortDescription?: string
    thumbnail?: {
      thumbnails?: Array<{ url: string; width: number; height: number }>
    }
    viewCount?: string
    author?: string
    isLiveContent?: boolean
    isPrivate?: boolean
  }
  microformat?: {
    playerMicroformatRenderer?: {
      category?: string
      publishDate?: string
      uploadDate?: string
      ownerChannelName?: string
      externalChannelId?: string
      lengthSeconds?: string
      viewCount?: string
    }
  }
}

interface YTInitialData {
  contents?: {
    twoColumnWatchNextResults?: {
      results?: {
        results?: {
          contents?: Array<{
            videoPrimaryInfoRenderer?: {
              title?: { runs?: Array<{ text: string }> }
              viewCount?: {
                videoViewCountRenderer?: {
                  viewCount?: { simpleText?: string }
                }
              }
              dateText?: { simpleText?: string }
            }
            videoSecondaryInfoRenderer?: {
              owner?: {
                videoOwnerRenderer?: {
                  title?: { runs?: Array<{ text: string }> }
                  navigationEndpoint?: {
                    browseEndpoint?: { browseId?: string }
                  }
                }
              }
              attributedDescription?: { content?: string }
              metadataRowContainer?: {
                metadataRowContainerRenderer?: {
                  rows?: Array<{
                    metadataRowRenderer?: {
                      title?: { simpleText?: string }
                      contents?: Array<{ simpleText?: string }>
                    }
                  }>
                }
              }
            }
          }>
        }
      }
    }
  }
}

// ============================================================================
// Cached YouTube Data (received from Injected Script)
// ============================================================================

let cachedPlayerResponse: YTInitialPlayerResponse | null = null
let cachedInitialData: YTInitialData | null = null
let dataReceivedPromise: Promise<void> | null = null
let dataReceivedResolve: (() => void) | null = null

/**
 * Initialize the message listener for receiving data from Injected Script
 * Should be called once when Content Script initializes
 */
export function initVideoMetadataListener(): void {
  window.addEventListener('message', (event) => {
    if (event.source !== window) return
    if (event.data?.source === 'INJECTED_SCRIPT' && event.data?.type === 'VIDEO_METADATA') {
      cachedPlayerResponse = event.data.data?.playerResponse || null
      cachedInitialData = event.data.data?.initialData || null

      // Resolve the pending promise if any
      if (dataReceivedResolve) {
        dataReceivedResolve()
        dataReceivedResolve = null
        dataReceivedPromise = null
      }

      console.log('[VideoMetadata] Received data from Injected Script', {
        hasPlayerResponse: !!cachedPlayerResponse,
        hasInitialData: !!cachedInitialData,
      })
    }
  })
}

/**
 * Request video metadata from Injected Script
 */
export function requestVideoMetadata(): void {
  window.postMessage(
    {
      source: 'CONTENT_SCRIPT',
      type: 'GET_VIDEO_METADATA',
      timestamp: Date.now(),
    },
    '*',
  )
}

/**
 * Wait for video metadata to be received (with timeout)
 * @param timeoutMs - Maximum time to wait in milliseconds
 */
export async function waitForVideoMetadata(timeoutMs: number = 3000): Promise<boolean> {
  if (cachedPlayerResponse || cachedInitialData) {
    return true
  }

  // Create a promise that will be resolved when data is received
  if (!dataReceivedPromise) {
    dataReceivedPromise = new Promise<void>((resolve) => {
      dataReceivedResolve = resolve
    })
  }

  // Request the data
  requestVideoMetadata()

  // Wait with timeout
  const timeoutPromise = new Promise<void>((resolve) => setTimeout(resolve, timeoutMs))
  await Promise.race([dataReceivedPromise, timeoutPromise])

  return !!(cachedPlayerResponse || cachedInitialData)
}

/**
 * Clear cached data (call when navigating to a new video)
 */
export function clearVideoMetadataCache(): void {
  cachedPlayerResponse = null
  cachedInitialData = null
  dataReceivedPromise = null
  dataReceivedResolve = null
}

// ============================================================================
// Helper Functions
// ============================================================================

/**
 * Extract video ID from YouTube URL
 */
function extractVideoIdFromUrl(url: string): string | null {
  const urlObj = new URL(url)
  const searchParams = urlObj.searchParams

  // Standard watch URL: youtube.com/watch?v=VIDEO_ID
  if (searchParams.has('v')) {
    return searchParams.get('v')
  }

  // Short URL: youtu.be/VIDEO_ID
  if (urlObj.hostname === 'youtu.be') {
    return urlObj.pathname.slice(1)
  }

  // Embed URL: youtube.com/embed/VIDEO_ID
  const embedMatch = urlObj.pathname.match(/\/embed\/([^/?]+)/)
  if (embedMatch) {
    return embedMatch[1]
  }

  return null
}

/**
 * Format duration in seconds to HH:MM:SS or MM:SS
 */
function formatDuration(seconds: number): string {
  const hours = Math.floor(seconds / 3600)
  const minutes = Math.floor((seconds % 3600) / 60)
  const secs = seconds % 60

  if (hours > 0) {
    return `${hours}:${minutes.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
  }
  return `${minutes}:${secs.toString().padStart(2, '0')}`
}

/**
 * Parse view count string to number
 */
function parseViewCount(viewCountStr: string | undefined): number | null {
  if (!viewCountStr) return null

  // Remove non-numeric characters except for K, M, B
  const cleaned = viewCountStr.replace(/[^0-9KMBkmb.]/g, '')

  // Handle abbreviated formats (e.g., "1.2M views")
  const match = cleaned.match(/^([\d.]+)([KMBkmb])?$/)
  if (match) {
    let num = parseFloat(match[1])
    const suffix = match[2]?.toUpperCase()
    if (suffix === 'K') num *= 1000
    else if (suffix === 'M') num *= 1000000
    else if (suffix === 'B') num *= 1000000000
    return Math.round(num)
  }

  // Try parsing as plain number
  const parsed = parseInt(viewCountStr.replace(/\D/g, ''), 10)
  return isNaN(parsed) ? null : parsed
}

/**
 * Get YouTube's internal data objects from cache
 * Data is received from Injected Script via postMessage
 */
function getYouTubeData(): {
  playerResponse: YTInitialPlayerResponse | null
  initialData: YTInitialData | null
} {
  return {
    playerResponse: cachedPlayerResponse,
    initialData: cachedInitialData,
  }
}

/**
 * Extract tags from meta tags or YouTube data
 */
function extractTags(): string[] {
  const tags: string[] = []

  // Try meta keywords
  const metaKeywords = document.querySelector('meta[name="keywords"]')
  if (metaKeywords) {
    const content = metaKeywords.getAttribute('content')
    if (content) {
      tags.push(...content.split(',').map((t) => t.trim()))
    }
  }

  return tags
}

/**
 * Get highest quality thumbnail URL
 */
function getBestThumbnail(
  thumbnails?: Array<{ url: string; width: number; height: number }>,
): string | null {
  if (!thumbnails || thumbnails.length === 0) return null

  // Sort by width (descending) and return the highest quality
  const sorted = [...thumbnails].sort((a, b) => b.width - a.width)
  return sorted[0].url
}

// ============================================================================
// Main Export Functions
// ============================================================================

/**
 * Extract video metadata from the current YouTube page
 * @returns YoutubeVideoMetadata object with all available metadata
 */
export function extractYoutubeVideoMetadata(): YoutubeVideoMetadata | null {
  const url = window.location.href

  // Get video ID first
  const videoId = extractVideoIdFromUrl(url)
  if (!videoId) {
    return null
  }

  // Get YouTube's internal data
  let { playerResponse, initialData } = getYouTubeData()

  // IMPORTANT: Validate that cached data matches current video
  // In SPA navigation, ytInitialPlayerResponse may contain stale data from previous video
  const cachedVideoId = playerResponse?.videoDetails?.videoId
  if (cachedVideoId && cachedVideoId !== videoId) {
    console.log(
      `[VideoMetadata] Cache mismatch: cached=${cachedVideoId}, current=${videoId}. Ignoring stale cache.`,
    )
    playerResponse = null
    initialData = null
  }

  const videoDetails = playerResponse?.videoDetails
  const microformat = playerResponse?.microformat?.playerMicroformatRenderer

  // Extract primary info renderer from initialData
  const watchResults = initialData?.contents?.twoColumnWatchNextResults?.results?.results?.contents
  const primaryInfo = watchResults?.find(
    (c) => c.videoPrimaryInfoRenderer,
  )?.videoPrimaryInfoRenderer
  const secondaryInfo = watchResults?.find(
    (c) => c.videoSecondaryInfoRenderer,
  )?.videoSecondaryInfoRenderer

  // Build metadata object
  const metadata: YoutubeVideoMetadata = {
    // Basic Info
    videoId,
    title:
      videoDetails?.title ||
      primaryInfo?.title?.runs?.map((r) => r.text).join('') ||
      document.title.replace(' - YouTube', ''),
    description:
      videoDetails?.shortDescription ||
      secondaryInfo?.attributedDescription?.content ||
      document.querySelector('meta[name="description"]')?.getAttribute('content') ||
      '',
    duration: videoDetails?.lengthSeconds
      ? parseInt(videoDetails.lengthSeconds, 10)
      : microformat?.lengthSeconds
        ? parseInt(microformat.lengthSeconds, 10)
        : null,
    durationFormatted: null, // Will be set below

    // Channel Info
    channelId:
      videoDetails?.channelId ||
      microformat?.externalChannelId ||
      secondaryInfo?.owner?.videoOwnerRenderer?.navigationEndpoint?.browseEndpoint?.browseId ||
      null,
    channelName:
      videoDetails?.author ||
      microformat?.ownerChannelName ||
      secondaryInfo?.owner?.videoOwnerRenderer?.title?.runs?.[0]?.text ||
      // DOM fallback: YouTube shows channel name in #channel-name or .ytd-channel-name
      document.querySelector('#channel-name a')?.textContent?.trim() ||
      document.querySelector('.ytd-channel-name a')?.textContent?.trim() ||
      null,
    channelUrl: null, // Will be set below

    // Video Stats
    viewCount: parseViewCount(
      videoDetails?.viewCount ||
        microformat?.viewCount ||
        primaryInfo?.viewCount?.videoViewCountRenderer?.viewCount?.simpleText,
    ),
    likeCount: null, // YouTube doesn't expose this easily anymore
    publishDate: microformat?.publishDate || null,
    uploadDate: microformat?.uploadDate || null,

    // Categorization
    category: microformat?.category || null,
    tags: extractTags(),
    isLiveContent: videoDetails?.isLiveContent || false,
    isPrivate: videoDetails?.isPrivate || false,

    // Thumbnails
    thumbnailUrl: getBestThumbnail(videoDetails?.thumbnail?.thumbnails),

    // URL
    videoUrl: `https://www.youtube.com/watch?v=${videoId}`,
  }

  // Set derived fields
  if (metadata.duration) {
    metadata.durationFormatted = formatDuration(metadata.duration)
  }
  if (metadata.channelId) {
    metadata.channelUrl = `https://www.youtube.com/channel/${metadata.channelId}`
  }

  return metadata
}

/**
 * Convert YoutubeVideoMetadata to JSON string
 * @param metadata - The metadata object to serialize
 * @returns JSON string representation of the metadata
 */
export function youtubeVideoMetadataToJson(metadata: YoutubeVideoMetadata): string {
  return JSON.stringify(metadata, null, 2)
}

/**
 * Extract and serialize video metadata in one call
 * @returns JSON string of video metadata, or null if extraction fails
 */
export function getYoutubeVideoMetadataJson(): string | null {
  const metadata = extractYoutubeVideoMetadata()
  if (!metadata) {
    return null
  }
  return youtubeVideoMetadataToJson(metadata)
}
