import { SubtitleSegment, chunkIdxesStr } from '../contentScript/subtitle'
import { Logger } from './logger'

/**
 * Represents a generic task in the processing queue.
 * Contains subtitle segments to process and callback functions.
 */
export interface Task<T = any> {
  chunk: SubtitleSegment[]
  onComplete: (chunk: SubtitleSegment[], result: T) => void
  onError?: (error: Error) => void
}

/**
 * Configuration for TaskQueueProcessor.
 * Defines how tasks should be processed, including API calls and result handling.
 */
export interface TaskQueueConfig<T> {
  /** Name of the task type for logging purposes */
  name: string

  /** Number of tasks to process in parallel */
  concurrency: number

  /** Time to wait (in ms) when queue is empty before checking again */
  idleWaitTime: number

  /**
   * Function to call the API for processing a batch of chunks.
   * @param chunks - Array of subtitle segment chunks to process
   * @returns Promise resolving to array of results (one per chunk)
   */
  processAPI: (chunks: SubtitleSegment[][]) => Promise<T[]>

  /**
   * Function to validate and apply the API result to the chunk.
   * Should modify the chunk in-place and return whether validation passed.
   * @param chunk - Subtitle segments to update
   * @param result - API result for this chunk
   * @returns true if validation passed, false otherwise
   */
  applyResult: (chunk: SubtitleSegment[], result: T) => boolean
}

/**
 * A generic task queue processor for background processing of subtitle segments.
 * Handles task queuing, concurrent processing, API calls, and result callbacks.
 *
 * This class can be used in both Background Service and Content Script contexts,
 * enabling the "stateless Background" architecture where Content Script owns the data.
 *
 * @template T - The type of API result for each task
 *
 * @example
 * // Create a translation processor
 * const processor = new TaskQueueProcessor({
 *   name: 'Translation',
 *   concurrency: 4,
 *   idleWaitTime: 2000,
 *   processAPI: async (chunks) => {
 *     return Promise.all(chunks.map(chunk => translateAPI(chunk)))
 *   },
 *   applyResult: (chunk, result) => {
 *     if (result.length !== chunk.length) return false
 *     for (let i = 0; i < chunk.length; i++) {
 *       chunk[i].targetContent = result[i]
 *     }
 *     return true
 *   }
 * })
 *
 * // Add tasks
 * processor.pushTask(chunk, (chunk, result) => {
 *   console.log('Task completed')
 * })
 *
 * // Start processing
 * processor.startProcessing()
 */
export class TaskQueueProcessor<T = any> {
  private queue: Task<T>[] = []
  private finished: boolean = false
  private logger: Logger
  private config: TaskQueueConfig<T>

  constructor(config: TaskQueueConfig<T>) {
    this.config = config
    this.logger = new Logger(`[TaskQueueProcessor:${config.name}]`)
  }

  /**
   * Gets the processor name (useful for debugging).
   */
  getName(): string {
    return this.config.name
  }

  /**
   * Adds a new task to the queue.
   * @param chunk - Subtitle segments to process
   * @param onComplete - Callback when processing completes successfully
   * @param onError - Optional callback when processing fails
   */
  pushTask(
    chunk: SubtitleSegment[],
    onComplete: Task<T>['onComplete'],
    onError?: Task<T>['onError'],
  ) {
    this.queue.push({ chunk, onComplete, onError })
  }

  /**
   * Gets the current queue length.
   */
  getQueueLength(): number {
    return this.queue.length
  }

  /**
   * Gets all chunks currently in the queue.
   */
  getQueuedChunks(): SubtitleSegment[][] {
    return this.queue.map((task) => task.chunk)
  }

  /**
   * Clears all tasks from the queue.
   */
  clearQueue(): void {
    this.queue.splice(0, this.queue.length)
    this.logger.debug(`Queue cleared`)
  }

  /**
   * Sets the finished flag to stop the processing loop.
   */
  setFinished(finished: boolean): void {
    this.finished = finished
    if (finished) {
      this.logger.debug(`Marked as finished, will stop processing loop`)
    }
  }

  /**
   * Gets the current finished status.
   */
  isFinished(): boolean {
    return this.finished
  }

  /**
   * Resets the processor state.
   */
  reset(): void {
    this.clearQueue()
    this.finished = false
    this.logger.debug(`Processor reset`)
  }

  /**
   * Main processing loop that continuously processes tasks from the queue.
   * Runs until the finished flag is set.
   *
   * Workflow:
   * 1. Take up to 'concurrency' tasks from the queue
   * 2. Call the API in batch
   * 3. Apply results to chunks
   * 4. Invoke onComplete callbacks
   * 5. Wait if queue is empty, then repeat
   */
  async startProcessing(): Promise<void> {
    this.logger.debug(`Starting processing loop (concurrency: ${this.config.concurrency})`)

    while (true) {
      // Check if we should stop
      if (this.finished) {
        this.logger.debug(`Stop the process loop!`)
        break
      }

      // Get next batch of tasks
      const tasks = this.queue.splice(0, this.config.concurrency)
      if (tasks.length === 0) {
        this.logger.debug(`No tasks to process, wait for ${this.config.idleWaitTime}ms`)
        await new Promise((resolve) => setTimeout(resolve, this.config.idleWaitTime))
        continue
      }

      // Process the batch
      try {
        const chunks = tasks.map((task) => task.chunk)
        this.logger.debug(`Processing batch of ${tasks.length} tasks`)

        // Call API
        const results = await this.config.processAPI(chunks)

        // Validate result length
        if (results.length !== tasks.length) {
          throw new Error(`API returned ${results.length} results but expected ${tasks.length}`)
        }

        // Apply results and invoke callbacks
        for (let i = 0; i < tasks.length; i++) {
          const task = tasks[i]
          const result = results[i]

          // Apply result to chunk
          const valid = this.config.applyResult(task.chunk, result)
          if (!valid) {
            this.logger.warn(`Result validation failed for chunk: ${chunkIdxesStr(task.chunk)}`)
            continue
          }

          // Invoke completion callback
          task.onComplete(task.chunk, result)
        }

        this.logger.debug(`Finished batch: ${tasks.length} tasks`)
      } catch (error) {
        this.logger.error(`Error processing batch:`, error)
        // Invoke error callbacks
        tasks.forEach((task) => task.onError?.(error as Error))
      }
    }
  }

  /**
   * Gets a set of segment indices that are currently in the queue.
   * Useful for avoiding duplicate tasks.
   */
  getQueuedSegmentIndices(): Set<number> {
    const indices = new Set<number>()
    for (const task of this.queue) {
      if (task.chunk.length > 0) {
        indices.add(task.chunk[0].index)
      }
    }
    return indices
  }

  /**
   * Logs the current queue state for debugging.
   */
  logQueueState(): void {
    this.logger.debug(
      `Queue state (${this.queue.length} tasks):\n  ${this.queue.map((task) => chunkIdxesStr(task.chunk)).join('\n  ')}`,
    )
  }
}
