import { Logger } from '../../tools/logger'

/**
 * 
 */
export interface CaptureResult {
  audioBlob: Blob //  Blob
  audioBase64: string //  base64
  imageBase64: string //  base64
  word: string // 
  sentence: string // 
}

/**
 * 
 * 
 */
export class MediaCaptureService {
  private logger = new Logger('[MediaCapture]')
  private recordingIndicator: HTMLDivElement | null = null

  /**
   * 
   *
   * @param videoElement - 
   * @param startMs - 
   * @param endMs - 
   * @returns  + 
   */
  async captureMediaForSubtitle(
    videoElement: HTMLVideoElement,
    startMs: number,
    endMs: number,
  ): Promise<CaptureResult> {
    // 2.1 
    const originalTime = videoElement.currentTime
    const wasPlaying = !videoElement.paused

    try {
      // 2.2 
      const startTime = startMs / 1000
      const duration = endMs - startMs // padding

      this.logger.debug(
        ` ${startTime.toFixed(2)}s ${duration}ms (${(duration / 1000).toFixed(2)}s)`,
      )

      // 2.3 
      videoElement.currentTime = startTime
      await this.waitForSeeked(videoElement)

      // 2.4 
      await videoElement.play()
      await this.waitForVideoFrame(videoElement)

      this.logger.debug('')

      // 2.5 
      const imageBase64 = this.captureFrame(videoElement)
      this.logger.debug(`: ${(imageBase64.length / 1024).toFixed(1)}KB`)

      // 2.6 
      this.showRecordingIndicator(videoElement)

      // 2.7 
      const audioBlob = await this.captureAudio(videoElement, duration)

      // 2.8 
      this.hideRecordingIndicator()

      // 2.9 
      videoElement.pause()

      // 2.8  base64
      const audioBase64 = await this.blobToBase64(audioBlob)

      this.logger.info(
        ` ${(audioBlob.size / 1024).toFixed(1)}KB ${(imageBase64.length / 1024).toFixed(1)}KB`,
      )

      // 2.9 
      return {
        audioBlob,
        audioBase64,
        imageBase64,
        word: '', // 
        sentence: '', // 
      }
    } finally {
      // 
      this.hideRecordingIndicator()

      // 
      videoElement.currentTime = originalTime
      if (wasPlaying) {
        await videoElement.play()
      }
      this.logger.debug('')
    }
  }

  /**
   * 
   *
   * a.html:600-644
   *
   * @param videoElement - 
   * @param durationMs - 
   * @returns  Blob
   */
  private async captureAudio(videoElement: HTMLVideoElement, durationMs: number): Promise<Blob> {
    // 1. 
    let stream: MediaStream

    if ('captureStream' in videoElement) {
      stream = (videoElement as any).captureStream()
    } else if ('mozCaptureStream' in videoElement) {
      stream = (videoElement as any).mozCaptureStream()
    } else {
      throw new Error(' captureStream API')
    }

    const audioTracks = stream.getAudioTracks()
    const videoTracks = stream.getVideoTracks()

    this.logger.debug(` ${audioTracks.length} , ${videoTracks.length} `)

    if (audioTracks.length === 0) {
      throw new Error('')
    }

    // 
    audioTracks.forEach((track, i) => {
      this.logger.debug(
        ` ${i}: enabled=${track.enabled}, readyState=${track.readyState}, muted=${track.muted}`,
      )
    })

    // 2. 
    const audioOnlyStream = new MediaStream(audioTracks)

    // 3.  MediaRecorder opus
    const mimeTypes = ['audio/webm;codecs=opus', 'audio/webm', 'audio/ogg;codecs=opus']
    let selectedMimeType = ''

    for (const type of mimeTypes) {
      if (MediaRecorder.isTypeSupported(type)) {
        selectedMimeType = type
        this.logger.debug(` MIME : ${type}`)
        break
      }
    }

    const recorder = new MediaRecorder(
      audioOnlyStream,
      selectedMimeType ? { mimeType: selectedMimeType } : {},
    )
    const chunks: Blob[] = []

    return new Promise((resolve, reject) => {
      recorder.ondataavailable = (e) => {
        this.logger.debug(`ondataavailable: size=${e.data.size}`)
        if (e.data.size > 0) {
          chunks.push(e.data)
        }
      }

      recorder.onstop = () => {
        const blob = new Blob(chunks, { type: recorder.mimeType })
        // 
        audioOnlyStream.getTracks().forEach((track) => track.stop())
        videoTracks.forEach((track) => track.stop())
        this.logger.debug(
          `: ${(blob.size / 1024).toFixed(2)}KB, chunks: ${chunks.length}`,
        )
        resolve(blob)
      }

      recorder.onerror = (e) => {
        this.logger.error(':', e)
        reject(e)
      }

      recorder.start()
      this.logger.debug(` ${durationMs}ms`)
      setTimeout(() => {
        this.logger.debug(``)
        recorder.stop()
      }, durationMs)
    })
  }

  /**
   *  GIF
   *
   * @param videoElement - 
   * @returns  base64 
   */
  private captureFrame(videoElement: HTMLVideoElement): string {
    const canvas = document.createElement('canvas')
    const originalWidth = videoElement.videoWidth || videoElement.clientWidth
    const originalHeight = videoElement.videoHeight || videoElement.clientHeight

    if (originalWidth === 0 || originalHeight === 0) {
      throw new Error('')
    }

    // Scale down to max width 640px for smaller file size
    const maxWidth = 640
    const scale = Math.min(1, maxWidth / originalWidth)
    canvas.width = Math.round(originalWidth * scale)
    canvas.height = Math.round(originalHeight * scale)

    const ctx = canvas.getContext('2d')
    if (!ctx) {
      throw new Error(' Canvas ')
    }

    ctx.drawImage(videoElement, 0, 0, canvas.width, canvas.height)

    return canvas.toDataURL('image/jpeg', 0.8)
  }

  /**
   *  seek 
   *
   * @param videoElement - 
   */
  private waitForSeeked(videoElement: HTMLVideoElement): Promise<void> {
    return new Promise((resolve) => {
      const onSeeked = () => {
        videoElement.removeEventListener('seeked', onSeeked)
        this.logger.debug('Seek ')
        resolve()
      }
      videoElement.addEventListener('seeked', onSeeked)
    })
  }

  /**
   * 
   *
   *  requestVideoFrameCallbackChrome 83+
   *  timeupdate 
   *
   * @param videoElement - 
   */
  private waitForVideoFrame(videoElement: HTMLVideoElement): Promise<void> {
    return new Promise((resolve) => {
      const video: HTMLVideoElement = videoElement

      if ('requestVideoFrameCallback' in video) {
        //  API
        ;(video as any).requestVideoFrameCallback(() => {
          this.logger.debug(' (requestVideoFrameCallback)')
          resolve()
        })
        return
      }

      //  timeupdate 
      const onTimeUpdate = (() => {
        this.logger.debug(' (timeupdate)')
        resolve()
      }) as EventListener
      // @ts-expect-error TypeScript narrowing issue - video is HTMLVideoElement at runtime
      video.addEventListener('timeupdate', onTimeUpdate, { once: true })
    })
  }

  /**
   * Blob  base64
   *
   * @param blob - Blob 
   * @returns base64 
   */
  private blobToBase64(blob: Blob): Promise<string> {
    return new Promise((resolve, reject) => {
      const reader = new FileReader()
      reader.onloadend = () => resolve(reader.result as string)
      reader.onerror = (e) => {
        this.logger.error('Blob  base64 :', e)
        reject(e)
      }
      reader.readAsDataURL(blob)
    })
  }

  /**
   * 
   *  "REC" 
   *
   * @param videoElement - 
   */
  private showRecordingIndicator(videoElement: HTMLVideoElement): void {
    // 
    this.hideRecordingIndicator()

    // 
    const playerContainer =
      videoElement.closest('#movie_player') ||
      videoElement.closest('.html5-video-player') ||
      videoElement.parentElement

    if (!playerContainer) {
      this.logger.warn('')
      return
    }

    // 
    this.recordingIndicator = document.createElement('div')
    this.recordingIndicator.className = 'moki-recording-indicator'
    this.recordingIndicator.innerHTML = `
      <svg width="120" height="50" viewBox="0 0 300 126" fill="none" xmlns="http://www.w3.org/2000/svg">
        <rect width="300" height="126" rx="32" fill="#171717" fill-opacity="0.9"/>
        <circle cx="63" cy="63" r="32" fill="#4ade80" class="moki-recording-dot"/>
        <text x="115" y="85" fill="white" font-size="72" font-weight="800" font-family="system-ui, -apple-system, sans-serif">REC</text>
      </svg>
    `

    playerContainer.appendChild(this.recordingIndicator)
    this.logger.debug('')
  }

  /**
   * 
   */
  private hideRecordingIndicator(): void {
    if (this.recordingIndicator) {
      this.recordingIndicator.remove()
      this.recordingIndicator = null
      this.logger.debug('')
    }
  }
}
