/**
 * YouTube 
 *  YouTube  DOM 
 */

// ============================================================================
// YouTube Player Mode Detection
// ============================================================================

/**
 * YouTube 
 * - default:  #secondary
 * - theater:  full-bleed-container
 * - fullscreen: 
 */
export type YouTubePlayerMode = 'default' | 'theater' | 'fullscreen'

/**
 *  YouTube 
 *  ytd-watch-flexy 
 * @returns 
 */
export function getYouTubePlayerMode(): YouTubePlayerMode {
  const watchFlexy = document.querySelector('ytd-watch-flexy')
  if (!watchFlexy) return 'default'

  // 
  if (watchFlexy.hasAttribute('fullscreen')) return 'fullscreen'
  // 
  if (watchFlexy.hasAttribute('theater')) return 'theater'

  return 'default'
}

/**
 *  ytd-watch-flexy 
 *  YouTube 
 * @returns ytd-watch-flexy  null
 */
export function getWatchFlexy(): Element | null {
  return document.querySelector('ytd-watch-flexy')
}

/**
 *  full-bleed-container 
 * 
 * @returns full-bleed-container  null
 */
export function getFullBleedContainer(): HTMLElement | null {
  return document.querySelector('#full-bleed-container')
}

/**
 * 
 * @returns 
 */
export function isTheaterMode(): boolean {
  return getYouTubePlayerMode() === 'theater'
}

/**
 * 
 * @returns 
 */
export function isFullscreenMode(): boolean {
  return getYouTubePlayerMode() === 'fullscreen'
}

// ============================================================================
// YouTube Page Detection
// ============================================================================

/**
 *  URL  YouTube 
 * @param url -  URL
 * @returns  YouTube 
 */
export function isYoutubePage(url: string): boolean {
  const patterns = [/youtube\.com/]
  return patterns.some((pattern) => pattern.test(url))
}

/**
 *  YouTube 
 * @returns YouTube  null
 */
export function getPlayer(): Element | null {
  const video = document.querySelector('video')
  if (!video) {
    return null
  }

  //  HTML5 
  return video.closest('#movie_player') || video.closest('.html5-video-player')
}

/**
 * 
 * @returns video  null
 */
export function getVideoElement(): HTMLVideoElement | null {
  return document.querySelector('video')
}

/**
 * 
 * @returns  null
 */
export function getCurrentVideoTimeMs(): number | null {
  const video = getVideoElement()
  if (!video) {
    return null
  }
  return video.currentTime * 1000
}

/**
 *  YouTube 
 * @returns 
 */
export function isSubtitleEnabled(): boolean {
  const player = getPlayer()
  if (!player) {
    return false
  }

  const windowContainer = player.getElementsByClassName('ytp-caption-window-container')
  if (!windowContainer || windowContainer.length === 0) {
    return false
  }

  // 
  const captionWindows = player.getElementsByClassName('caption-window')
  return captionWindows.length > 0
}

/**
 *  YouTube 
 *  CSS 
 */
export function hideYoutubeSubtitle(): void {
  const styleId = 'moki-hide-youtube-subtitle'
  if (document.getElementById(styleId)) {
    return
  }

  const style = document.createElement('style')
  style.id = styleId
  style.textContent = `
    #ytp-caption-window-container,
    .html5-video-player #ytp-caption-window-container {
      display: none !important;
    }
  `
  ;(document.head || document.documentElement).appendChild(style)
}

/**
 * 
 * @param scriptUrl -  URL
 * @param onLoad - 
 */
export function injectScript(scriptUrl: string, onLoad?: () => void): void {
  const script = document.createElement('script')
  script.src = scriptUrl

  script.onload = () => {
    script.remove()
    onLoad?.()
  }
  ;(document.head || document.documentElement).appendChild(script)
}
